/* eslint-disable max-lines-per-function */
import { getUrlParam } from "~/common/utils/https.js";
import { ViewBase } from "@ehas/core";
/**
 * 跳转到产品详情
 * @param {Object} good 产品信息
 */
// eslint-disable-next-line max-statements
export function clickGoodDetail(good = {}) {
    let searchArr = good.pgNo.split("#");
    let appId = "";
    let title = "";
    let isHiddenNavigationBar = "";
    for (let i = 0; i < searchArr.length; i++) {
        let _appId = getUrlParam(searchArr[i], "appId");
        let _title = getUrlParam(searchArr[i], "title");
        let _isHiddenNavigationBar = getUrlParam(searchArr[i], "isHiddenNavigationBar");
        if (_appId) {
            appId = _appId;
        }
        if (_title) {
            title = _title;
        }
        if (_isHiddenNavigationBar) {
            isHiddenNavigationBar = _isHiddenNavigationBar;
        }
    }
    if (appId) {
        // 小程序容器打开
        App.jsApi.openApplet({
            url: good.pgNo,
            appId: appId,
            isHiddenNavigationBar: isHiddenNavigationBar,
            title: title || "商品详情"
        }).catch((rsp) => {
            //通过code和messge可查看调用失败的原因
            if (rsp.code == "EPFJ002W") {
                ViewBase.prototype.$alert({
                    message: "该版本手机银行不支持此功能，请升级最新版本手机银行",
                    btnText: "确定"
                });
            } else {
                ViewBase.prototype.$toast(rsp.cvrtMsg);
            }
        });
    }else{
        if (good.jumpTyp == '5') {
            if(title){
                title = decodeURIComponent(title);
            }
            try {
                App.jsApi.openWebPageByJSBContainer({
                    url: good.pgNo,
                    title: title
                });
            } catch (e) {
                window.$G.jsApiErrorMsg(e);
            }
        }
    }
}
/**
 * 跳转到薪易平台
 * @param {Object} good 产品信息
 */
// eslint-disable-next-line max-statements
export function clickSalaryEasy(good = {}) {
    let searchArr = good.pgNo.split("#");
    let appId = "";
    let title = "";
    let isHiddenNavigationBar = "";
    for (let i = 0; i < searchArr.length; i++) {
        let _appId = getUrlParam(searchArr[i], "appId");
        let _title = getUrlParam(searchArr[i], "title");
        let _isHiddenNavigationBar = getUrlParam(searchArr[i], "isHiddenNavigationBar") || true;
        if (_appId) {
            appId = _appId;
        }
        if (_title) {
            title = _title;
        }
        if (_isHiddenNavigationBar) {
            isHiddenNavigationBar = _isHiddenNavigationBar;
        }
    }
    if (appId) {
        // 小程序容器打开
        App.jsApi.openApplet({
            url: good.pgNo,
            appId: appId,
            isHiddenNavigationBar: isHiddenNavigationBar,
            title: title
        }).catch((rsp) => {
            //通过code和messge可查看调用失败的原因
            if (rsp.code == "EPFJ002W") {
                ViewBase.prototype.$alert({
                    message: "该版本手机银行不支持此功能，请升级最新版本手机银行",
                    btnText: "确定"
                });
            } else {
                ViewBase.prototype.$toast(rsp.cvrtMsg);
            }
        });
    }
}