import { getJson } from "@ehas/request-ajax";import { WapWeChatUtil } from "~/common/js/wap";/** * moduleName cdn放入的模块名 * fileName 放入cdn的文件名 * 处理json文件放入cdn信息 */async function dealWithJsonFun(moduleName, fileName) {    try {        const bankId = App.bank.flags?.common?.bankId;        let requestPath = `${ehasProcessCDNServer}/public/${bankId}/h5/mbank/${moduleName}/json/${fileName}.json`;        //楼层兜底        let jsonData = await getJson(requestPath);        return jsonData.data || "";    } catch (_error) {        return "";    }}/** * 获取指定字符串中的url参数 */function getUrlParam(url, name) {    let searchArr = url.split("#")[0].split("?");    if (searchArr.length > 1) {        let params = searchArr[1].split("&");        let _arr = [];        for (let i = 0; i < params.length; i++) {            _arr = params[i].split("=");            if (_arr !== null && _arr[0] === name) {                return _arr[1];            }        }        return "";    } else {        return "";    }}/** *获取标准URl参数方法 * @param {string} url 标准url * @param {string} name 需要截取的参数名 *//* eslint-disable */function getUrlParams(url, name) {    let reg = new RegExp("(&|\\?)" + name + "=([^&]*)(&|#|$)");    //let urlParamStr = url.split("?")[1];    let r = url.match(reg);    if (r != null) return decodeURI(r[2]);    return null;}/* eslint-enable *//** * 图片上传方法 * @param {*} path 文件路径,取file.config.js文件映射 * @param {*} picPath jsApi返回文件路径 * @param {*} bucketType MinIO文件类型,取file.config.js文件映射 *//* eslint-disable */async function uploadPic(path, picPath, bucketType = "1") {    const fileData = {        // 对应上传文件路径,例如:transfer:/transfer/phone        upload: {            setting: "/setting/feedback",            childCard: "/index/index",            completeMemberInfo: "/affectionAccount/completeMemberInfo",            fudQlfdinvstApply: "/fud/QlfdinvstApply",            // 调研问卷            questionnaire: "/productExperience/questionnaire",            //信用卡资料上传            creditcardInformation:"/modules/CreditCard/Upload"        },        download: {},        bucketType: {            "1": "/customer", //存储与用户相关的文件            "2": "/public" //存储不需要校验的文件，与用户无关，不需要权限校验可直接通过路径访问        }    };    const fileArr = picPath.split("/");    const fileName = fileArr[fileArr.length - 1];    // 拼接上传文件路径    const filePath = fileData["bucketType"][bucketType] + fileData["upload"][path];    // 调用接口获取文件上传token    const tokenObj = await App.requester.request("file/upload/token.do", {        data: {            filePath: filePath,            fileName: fileName        }    });    const token = tokenObj.token;    // 调用jsapi上传图片    const picObj = await App.jsApi.uploadFile4Biz({        fileName: fileName,        filePath: picPath,        token: token    });    let picInfo = {        picName: "",        picUrl: ""    };    // 解析返回数据,成功则返回路径和名称,失败直接报错    if (picObj.result.rspHead.rspCd === "S000A000") {        picInfo.picName = picObj.result.rspData.fileName;        picInfo.picUrl = picObj.result.rspData.url;        return picInfo;    } else {        window.$G.popErrorMsg(picObj.result.rspHead.rspCd, picObj.result.rspHead.msg, picObj.result.rspHead.errorId);    }}/* eslint-enable *//** * 图片下载方法 * @param {*} picPath 图片下载路径 * @returns {*} 图片路径(file协议) */async function downloadPic(picPath, cancelWhenRouting = true) {    const fileArr = picPath.split("/");    const fileName = fileArr[fileArr.length - 1];    const tokenObj = await App.requester        .request("file/download/token.do", {            data: {                fileFullPath: picPath            },            useDefaultError: false,            cancelWhenRouting: cancelWhenRouting        })        .catch(err => {
//EHAS_RESULT_BRANCH_START true 
            window.$G.popErrorMsg(err);
//EHAS_RESULT_BRANCH_END 
        });    const token = tokenObj.token;    const picObj = await App.jsApi.downloadFile4Biz({ fileName: fileName, token: token }).catch(err => {
//EHAS_RESULT_BRANCH_START true 
        window.$G.popErrorMsg(err);
//EHAS_RESULT_BRANCH_END 
    });    return picObj.result.filePath;}/** * 获取域名 * @param {*} type 类型 * @param {*} bankid 行号 */function getDomainName(type, bankid = App.bank.flags.common.bankId) {    let value = "";
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 

//EHAS_RESULT_BRANCH_START true 
    const name = `${ehasProcessContextFlag}.config`;    const plugin = require(`../config/domainName/${name}`);    value = plugin[bankid][type];
//EHAS_RESULT_BRANCH_END 
    return value;}/** * 设置submitKey * @param {*} url 表单里的url */async function onReqSubmitKey(url) {    if (url.substring(0, 1) === "/") {        url = url.substring(1, url.length);    }    const urlList = url.split("/");    let submitKeyObj = null;    try {        submitKeyObj = await App.requester.request(urlList[0] + "/submitkey.biz", {            useDefaultError: false,            slient: true,            resolution: true        });        const submitKey = submitKeyObj.submitKey;        return submitKey;    } catch (error) {        window.$G.logErrorWithCode("common/utils/https.js捕获异常", "submitKey第一次请求失败错误信息为" + JSON.stringify(error));        //捕获异常报错，防止await一直等待        try {            submitKeyObj = await App.requester.request(urlList[0] + "/submitkey.biz", {                useDefaultError: false,                slient: true,                resolution: true            });            const submitKey = submitKeyObj.submitKey;            return submitKey;        } catch (err) {            window.$G.logErrorWithCode("common/utils/https.js捕获异常", "submitKey第二次请求失败错误信息是" + JSON.stringify(err));            return "";        }    }}/* eslint-disable */function getHmacSecretKey() {
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 

//EHAS_RESULT_BRANCH_START true 
    return "d6c86549-5c8e-4be9-863d-056289d07fbd";
//EHAS_RESULT_BRANCH_END 
}function getRequestAppid() {    if (WapWeChatUtil.isWXBusiness()) {        return "0000000027c84c07ffffffff9a3e4a8e";    }
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 

//EHAS_RESULT_BRANCH_START true 
    return "00000000-3950-d063-ffff-ffffcedfe028";
//EHAS_RESULT_BRANCH_END 
}/* eslint-enable *//** * 读取按照编码返回数据 * @param {*} response blob返回数据 */function readResAsString(response) {    // 走流读取，编码可能是gbk，gb2312等，比如sofa 3默认是gbk编码    return new Promise((resolve, reject) => {        const reader = new window.FileReader();        // 是否已经使用正则匹配过        var hasMatch = false;        reader.onload = () => {            if (!hasMatch) {                // 正则                const charsetReg = /charset=(?<charset>(gb2312|sofa|utf-8|gbk|x-cp20936))/gi;                // 正则匹配结果                const matchResult = charsetReg.exec(reader.result.match(charsetReg));                // 真实编码类型                let charset = matchResult?.groups?.charset;                // 只使用正则匹配一次                hasMatch = true;                //  返回的文本中有编码格式，并且不是utf-8格式的，重新按照真实的编码格式去读文本                if (charset && charset.toUpperCase() !== "UTF-8") {                    if (charset == "x-cp20936") {                        charset = "gb2312";                    }                    reader.readAsText(response, charset);                } else {                    resolve(reader.result);                }            } else {                resolve(reader.result);            }        };        reader.onerror = reject;        // 默认按照utf-8的编码格式读取文本        reader.readAsText(response, "utf-8");    });}/** * 获取协议版本号、类型 * @param {*} list 协议列表 */function agrTypVerData(list) {    return list.map(item => {        let agrContent = item.content.substring(item.content.indexOf("?"), item.content.indexOf('">'));        return {            agrVerNo: getUrlParam(agrContent, "verNo"),            agrTyp: getUrlParam(agrContent, "agrTyp")        };    });}/** * 协议勾选提交记录 * @param {*} list 协议版本、类型列表 */function agrCheckRecord(list) {    if (list.length > 0) {        App.requester.request("cmm/agr/agrfilerecd.biz", {            useDefaultError: false,            data: {                agrFileRecdListDtoEntityList: list            }        });    }}export {    dealWithJsonFun,    getUrlParam,    getUrlParams,    uploadPic,    downloadPic,    getDomainName,    onReqSubmitKey,    getHmacSecretKey,    getRequestAppid,    readResAsString,    agrTypVerData,    agrCheckRecord};