import { RequestNative } from "@ehas/request-native";import { RequesterConfig } from "@ehas/core";// import { arrayUtils } from "@ehas/utils";import { requestFlag } from "~/common/config/request.config";import { uniqueAlphaNumericId } from "~/common/utils/format.js";import { clearLoginFlag, goToLogin } from "~/common/utils/login.js";import { globalUtil } from "~/common/js/globalUtil";import { transferUrl } from "~/common/js/filters";import menu from "../common/datas/menu";const SUCCESSCODE = "S000A000";import stepReqBiz from "../common/config/step.req.conf";import { encryptJS } from "@ehas/encrypt";import { idsUtils } from "@ehas/utils";import { LocalStorage } from "@ehas/local-h5";import { isNewStartup } from "~/common/utils/check.js";import { useAppLaunchPopDialog } from "~/common/utils/device.js";let tempSid = "";let sessionid = createHexmd5(new Date().getTime()); //生成随机的sessionid// http请求头客户端传的字段const httpHeadersKey = ["Referer", "appid", "espier", "msapToken"];// 报文头客户端传的字段const reqHeadersKey = [    "chnlNo",    "acsSty",    "mrctNo",    "subMrctNo",    "requestTime",    "sid",    "dTyp",    "dId",    "bsdId",    "olddId",    "xddId",    "cNo",    "osVer",    "appVer",    "isCrack",    "posX",    "posY",    "submitKey",    "phoneNo",    "isWifi",    "brand",    "transAddr",    "bkId",    "rspFmt",    "clientTraceNo",    "custGrpNo",    "rqId",    "version_num",    "zipLastModFlag",    "stime",    "openAdb",    "clientIp",    "clientCity",    "domestic"];let uuid = idsUtils.random(32);const getUuid = async () => {    let res = await App.local.get("requst_uuid");    if (!res) {        await App.local.set("requst_uuid", uuid);        return uuid;    } else {        return res;    }};class SelfRequesterConfig extends RequesterConfig {    getRequestHandler(config) {        if (requestFlag == "1") {            Object.assign(config.headers, {                /* eslint-disable max-len */                "did":                    "33efb6c4edf44813c92ad9c43006d8721aa7b3cafadc9d91|RRBgVp9fErwPdQPtojHkksTRaXTXqtWz|cfbdf54901f80c8542899f69e2de3822||17728E6F-A3FE-4ACB-9925-5502303FD31D",                "X-EOS-SourceAppCode": "aaa",                "X-EOS-ApiSubscriptionKey": "95a1358b62ba484181ffff3a58840f79",                "X-EOS-SourceSysCode": "aaa",                "X-EOS-SourceSysKey": "aaa",                "appid":                    ehasProcessContextFlag === "prod"                        ? "0000000029e2f9770000000025cbb247"                        : "ffffffffcdc6af160000000030500e8e",                "globaltracenum": "00000210000005e6f1440dc804310ba8008c5a2379aaa",                "chnlno": "A03",                "acssty": "01",                "content-type": "application/json",                "customerid": "102",                "requesttime": new Date().getTime(),                "usetype": "123456",                // PC端不验密,勿删                "h5_test_pd": true            });        }
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 

//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 
        return new RequestNative();    }    /**     * 判断是否为object类型     */    isObject(obj) {        return obj !== null && typeof obj === "object";    }    analyResult(content, success, error) {        this.checkAd(content);        let { moduleName } = App.router.fragment;        moduleName = moduleName.toLowerCase();        // 风控触发人脸验证+手机震动        if (content?.rspHead?.faceVerificationByRiskCheck && moduleName != "login") {            App.jsApi.jsapiExistInApp({ jsApiList: ["deviceVibrate", "getSuitableState"] }).then(res => {                if (res?.result?.getSuitableState) {                    App.jsApi.getSuitableState().then(rsp => {                        if (rsp?.result?.state) {                            // 手机震动                            if (res?.result?.deviceVibrate) {                                App.jsApi.deviceVibrate();                            }                        }                    });                }            });        }        if (content.rspHead.sid) {            tempSid = content.rspHead.sid;            localStorage.setItem("tempSid", tempSid);        }        //返回报文时，将  替换为䶮        if (this.isObject(content.rspData)) {            //如果content.rspData是对象，则进行替换            content.rspData = JSON.parse(JSON.stringify(content.rspData).replace(//g, "䶮"));        }        if (content.rspHead.rspCd === SUCCESSCODE) {            // rspHead.finalStep 为true时，清理客户端缓存的跨步场景标识            if (content.rspHead.finalStep) {                App.globalFlag.busiSceneNo = "";            }            if (content.rspHead.msg) {                content.rspData.message = content.rspHead.msg; //转账状态为失败时，需要在结果页展示失败原因            } else {                content.rspData.message = "";            }            success(content.rspData);        } else {            error(                {                    code: content.rspHead.rspCd,                    message: content.rspHead.msg                },                {                    rspData: content.rspData,                    rspHead: content.rspHead                }            );        }    }    checkAd(content) {        if (content.rspHead && content.rspHead.mktDetails && content.rspHead.mktDetails.length !== 0) {            try {                const mktDetails = eval(content.rspHead.mktDetails);                let { moduleName } = App.router.fragment;                moduleName = moduleName.toLowerCase();                if (moduleName === "login" || moduleName === "setting") {                    // 缓存登录、设置模块的插屏广告                    window.imageAdCache[moduleName].push({                        picUrl: transferUrl(mktDetails[0].pctUrl),                        pctLinkUrl: mktDetails[0].svcUrl,                        pctLinkTyp: mktDetails[0].reqType                    });                } else {                    // 直接显示插屏广告                    const picUrl = transferUrl(mktDetails[0].pctUrl);                    if (picUrl) {                        ViewBase.prototype.$imageAd({                            picUrl: picUrl,                            pctLinkUrl: mktDetails[0].svcUrl,                            pctLinkTyp: mktDetails[0].reqType                        });                    }                }            } catch (error) {                window.$G.logErrorWithCode("src/config/requestSetting.jsrequestSetting.js   checkAd ", JSON.stringify(error));            }        }    }    //前置过滤器    // eslint-disable-next-line no-empty-function    beforeFilter() {}    // 请求返回的异常过滤处理    // eslint-disable-next-line max-lines-per-function    errorFilter(error) {        //退出登录接口报错        if (error.option.url == "usr/logout/userlogout.biz") {            return false;        }        const { code, message, rspHead } = error;        const errorId = rspHead && rspHead.errorId ? `参考码：【${rspHead.errorId}】` : "";        const alertOptions = {            title: "温馨提示",            btnText: "确定",            message: `${message}`,            subInfo: `${errorId}`        };        let data = null;        switch (code) {            case "EPFM020B": //会话超时            case "EPFM023B": // 会话异常            case "EPFM022B": // 账号在其他设备登录                error.isErrorExecute = true;                if (App.globalFlag.loginView !== "Y") {                    App.globalFlag.loginView = "Y";                    const closed = async () => {                        if (code == "EPFM022B") {                            //如果账号在其他设备登录则直接更新客户端状态                            await App.jsApi.updateLoginInfo();                        } else {                            //其他情况主动退出登录的，则直接请求退出接口在进行更新客户端登录状态                            await clearLoginFlag();                        }                        App.globalFlag.loginView = "";                        await goToLogin("#FloorHome/Home/Index", null, { sessionout: true });                    };                    useAppLaunchPopDialog({ ...alertOptions, modalId: "sessionOut", closed: closed }, "alert");                }                return false;            case "EPFB072T": //系统超时                const closed = () => {                    //判断是否为主webview                    const rootFlag = App.jsApi.isRootView();                    if (!rootFlag) {                        //不是主webview，则先关掉当前webview                        App.jsApi.closeWebContainer();                    }                    //如果不是主webview，则先关掉当前webview再提示引导去登录；否则直接引导去登录。                    goToLogin(undefined, null, { sessionout: true });                };                useAppLaunchPopDialog({ ...alertOptions, modalId: "sessionOut", closed: closed }, "alert");                return false;            case "EPFM019B": // 菜单停用                error.isErrorExecute = true;                useAppLaunchPopDialog(                    {                        ...alertOptions,                        modalId: "menuDeactivation",                        closed: () => {                            App.router.back();                        }                    },                    "alert"                );                return false;            case "EBMB0058": // 停服                // sccba03811852--判断错误码跳转网络诊断zip页面的停服页面，传递错误码参数                this.openWebPageOfNoService();                return false;            case "EPFN009T":            case "EPFN010T":                // 频道页不弹窗，其他页面弹窗                const { moduleName } = App.router.fragment;                if (moduleName != "FloorHome") {                    alertOptions.message = "系统开小差了，请稍后重试！";                    useAppLaunchPopDialog({ ...alertOptions, modalId: "systemDeviation" }, "alert");                }                return false;            case "EPFM040S":            case "EPFM044S": //040和044错误码为直接拒绝申请白名单                const _closed = () => {                    //点击退出，则退出app                    App.jsApi.exitApp();                };                useAppLaunchPopDialog({ ...alertOptions, modalId: "sessionOut", closed: _closed }, "alert");                return false;            case "EPFM041S": //增加越狱白名单错误码                const { moduleNm } = App.router.fragment;                if (moduleNm == "Login") {                    //如果该模块是登录模块，则关掉登录模块的当前webview                    App.jsApi.closeWebContainer();                } else {                    const _closed = isSure => {                        if (isSure) {                            //点击确定，则跳转到申请白名单页面                            window.$G.goToNext("#Allowlist/Allowlist/AppliInFiling", {                                errorId: rspHead.errorId,                                deviceHash: rspHead.deviceHash                            });                        } else {                            //点击退出，则退出app                            App.jsApi.exitApp();                        }                    };                    useAppLaunchPopDialog(                        {                            message: `${message}`,                            sureText: `确定`,                            cancelText: "退出",                            modalId: "sessionOut",                            closed: _closed                        },                        "confirm"                    );                }                return false;            case "error":                error.isErrorExecute = true;                data = error.data || {};                if (data.code == "EPFN555E") {                    // 跳转停服页面                    this.openWebPageOfNoService();                    return false;                }
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 
                if (error.option.url == "file/download/token.do") return true;                // 无网络连接，多个接口发请求情况下，仅弹窗一次                if (data.code === "EPFN000T") {                    useAppLaunchPopDialog(                        {                            message: "网络异常，请检查您的网络设置",                            sureText: "查看详情",                            cancelText: "知道了",                            modalId: "networkStatus",                            closed: isSure => {                                if (isSure) {                                    App.jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });                                }                            }                        },                        "confirm"                    );                    return false;                }                window.$G.jsApiErrorMsg(data);                return false;            default:                return true;        }    }    // 请求返回异常的默认处理(没有被过滤处理掉的异常)    defaultErrorFunc(err) {        globalUtil.popErrorMsg(err, err?.option?.view);    }    async getSubmitKey(url) {        if (url.substring(0, 1) === "/") {            url = url.substring(1, url.length);        }        const urlList = url.split("/");        let submitKeyObj = null;        try {            submitKeyObj = await App.requester.request(urlList[0] + "/submitkey.biz", {                slient: true,                resolution: true            });            const submitKey = submitKeyObj.submitKey;            return submitKey;        } catch (error) {            window.$G.logErrorWithCode("src/config/requestSetting.js捕获异常", "submitKey第一次请求失败错误信息为" + JSON.stringify(error));            //捕获异常报错，防止await一直等待            try {                submitKeyObj = await App.requester.request(urlList[0] + "/submitkey.biz", {                    slient: true,                    resolution: true                });                const submitKey = submitKeyObj.submitKey;                return submitKey;            } catch (err) {                window.$G.logErrorWithCode("src/config/requestSetting.js捕获异常", "submitKey第二次请求失败错误信息是" + JSON.stringify(err));                return "";            }        }    }    /**     * 生成跨步场景码     * 生成规则:     * 1、请求接口在跨步开始接口列表中     * 2、维护一个场景组,请求接口在场景组中,判断是否有场景码,若没有则生成一个,若有则不变     * 3、跨步开始接口不在场景组中,判断两次请求接口是否一致,一致则码值不变     * 4、不满足2/3条情况则重新生成一个码值     * 5、不满足第一条则不处理场景码     * @param {*} reqUrl 请求url     */    getBusiSceneNo(reqUrl) {        // 跨步开始接口列表        const startBizs = stepReqBiz.start;        // 获取场景分组        const stepGroup = stepReqBiz.group;        // 判断请求url是否在跨步开始接口列表中        if (startBizs.includes(reqUrl)) {            // 判断请求接口是否在场景组中            let groupFlag = false;            for (let items of stepGroup) {                if (items.includes(reqUrl) && items.includes(App.globalFlag.lastStartBizsReqUrl)) {                    groupFlag = true;                }            }            if (groupFlag) {                // 在场景组中判断是否有场景码,没有则生成一个场景码                if (!App.globalFlag.busiSceneNo) {                    this.generateBusiSceneNo(reqUrl);                }            } else if (App.globalFlag.lastReqUrl !== reqUrl) {                //  判断两次请求接口是否一致                // 两次请求接口不一致则重新生成场景码                this.generateBusiSceneNo(reqUrl);            } else {                this.generateBusiSceneNo(reqUrl);            }        }        App.globalFlag.lastReqUrl = reqUrl;    }    generateBusiSceneNo(reqUrl) {        App.globalFlag.busiSceneNo = uniqueAlphaNumericId();        App.globalFlag.lastStartBizsReqUrl = reqUrl;    }    // 生成请求报文    /* eslint-disable */    async transformReqData(reqData, option) {        const { moduleName, menuName, pageName, params } = App.router.fragment;        const h5url = `${moduleName}/${menuName}/${pageName}`;        let menuInfo = await menu.findByUrl(h5url);        // 如果查询不到 就查询带参数的功能码        if (!menuInfo) menuInfo = await menu.findByUrl(`${h5url}/${params}`);        const menuId = menuInfo ? menuInfo.bsnCode : "";        // 获取用户缓存 用于取机构号        let userInfo = (await App.local.get("m_user_info")) || {};        // 初始化报文头        const reqHead = {};        if (App.jsApi.isInApp()) {            const jsApiClientInfo = await App.jsApi.getNativeInfo();            const nativeInfo = jsApiClientInfo?.result?.nativeInfo;            // 从jsapi获取http请求头并更新option.headers            const httpHeaders = {};            httpHeadersKey.forEach(key => {                httpHeaders[key] = nativeInfo[key] || "";            });            Object.assign(option.headers, httpHeaders);            //蜜罐固定http请求头部字段 并更新option.headers            const staticHttpHeader = {                sessionid,                isroot: "false",                ishook: "false",                isproxy: "false",                istransaction: creatRandomBoolean()            };            staticHttpHeader["hash"] = createHexmd5(staticHttpHeader); //md5加密            Object.assign(option.headers, httpHeaders, staticHttpHeader);            // 从jsapi获取报文头并更新 reqHead            reqHeadersKey.forEach(key => {                reqHead[key] = nativeInfo[key] || "";            });            // 客户端启动改方法只调用一次            if (App.globalFlag.setRequestHead) {                // 防止重复请求，阻塞                App.globalFlag.setRequestHead = false;                // 判断是否存在该插件                try {                    await App.jsApi.jsapiExistInApp({ jsApiList: ["setReqHead"] }).then(res => {                        if (res?.result?.setReqHead) {                            //通知客户端是否获取MAC、客户端自行拼装到clientInfo里面                            App.jsApi.setReqHead({ options: { wifimac: "true" } });                        }                    });                } catch (err) {                    window.$G.logErrorWithCode("src/config/requestSetting.jsjsapiExistInApp报错", "reqData:" + JSON.stringify(err));                }            }            // 频道页屏蔽指定网络报错时需要connection为close            // 网络报错的类型参考errorfilter中的错误码            if (App.bank.flags.common.isKeepAliveClosed) {                option.headers.Connection = "close";            } else {                option.headers.Connection = "keepalive";                option.headers["Keep-Alive"] = "30";            }            //蜜罐固定reqHead头 并更新到报文头            let staticReqHead = {                sessionid,                clientIp: nativeInfo.clientIp || "",                istransaction: creatRandomBoolean(),                hasswd: String(Math.round(Math.random() * 100)) //2位的随机数            };            staticReqHead["hash"] = createHexmd5(staticReqHead); //md5加密            Object.assign(reqHead, staticReqHead);            reqHead.appSceneNo = await getUuid();        }        // zip包名称        option.headers.zipid = App.router.zipName ? App.router.zipName.split(".")[0] : "";        // zip包版本号        option.headers.zipverno = App.router.zipVersion;        option.headers.h5url = h5url;        option.headers.menuid = menuId;        // option.headers.targetIP = "36.30.100.149";        //TODO:临时报文,移动网关不加jsonData,待移动网关修改之后删除        let reqUrl = option.url;        if (reqUrl.substring(0, 1) === "/") {            reqUrl = reqUrl.substring(1, reqUrl.length);        }        this.getBusiSceneNo(reqUrl);        const reqHeaders = option.reqHeaders || {};        Object.assign(reqHead, {            busiSceneNo: App.globalFlag.busiSceneNo,            branchNo: userInfo.branchNo || ""        });        // 防重复submitKey        if (reqData.submitKey) {            reqHeaders.submitKey = reqData.submitKey;            delete reqData.submitKey;        } else if (option.submitKey) {            reqHeaders.submitKey = await this.getSubmitKey(option.url);        }        // 合并请求header        if (requestFlag == "1") {            Object.assign(reqHead, reqHeaders, {                // "opId": "ebus_ProductList",                // "themeId": window.App.theme.getThemeId(),                "rqId": "01",                "acsSty": "01",                "osVer": "ios",                "appVer": "6.0",                "chnlNo": "A03",                "content-type": "application/json",                "bkId": App.bank.flags.common.bankId,                "appid":                    ehasProcessContextFlag === "prod"                        ? "0000000029e2f9770000000025cbb247"                        : "ffffffffcdc6af160000000030500e8e",                "sid": localStorage.getItem("tempSid") || "",                "dId": "18431819f51dd5041cacffa0e6e3a4ae68edce58da434e8db75663a28f171685"            });        } else {            // 请求头填充分享参数            if (App.jsApi.isInApp()) {                const res = await App.jsApi.readFromMemoryStorage({ key: "sharedparam" });                let shareparam = "";                try {                    shareparam = res.result.sharedparam && JSON.parse(decodeURIComponent(res.result.sharedparam));                    if (shareparam) {                        delete shareparam.expdParamCntnt;                        Object.assign(reqHead, { shareparam });                    }                } catch (err) {                    window.$G.logErrorWithCode("src/config/requestSetting.js请求头填充分享参数异常", "shareparam:" + shareparam);                }            }            Object.assign(reqHead, reqHeaders);        }        //输入源标注取值 meta        const meta = reqData.$__meta__$ || [];        if (meta) {            reqHead.meta = meta.join(",");            delete reqData.$__meta__$;        }        try {            reqData = JSON.parse(JSON.stringify(reqData).replace(/䶮/g, ""));        } catch (err) {            window.$G.logErrorWithCode("src/config/requestSetting.js请求头生僻字转换异常", "reqData:" + JSON.stringify(reqData));        }        // if (data) {        //     result.reqHead.opId = data.opId;        //     delete data.opId;        // }        //TODO: 封装登录信息 session状态管理，做SID赋值        //TODO：文件上传
//EHAS_RESULT_BRANCH_START true 
        this.checkNumberParams(reqUrl, reqData);
//EHAS_RESULT_BRANCH_END 
        return { reqHead, reqData };    }    // 测试环境增加报文字段非字符串问题增加校验，检测出有数字类型时，以弹框形式进行提示    checkNumberParams(url, params) {        if (!params) return;        Object.keys(params).forEach(key => {            const value = params[key];            const valueType = this.getType(value);            if (valueType === "number") {                const message = `【报文参数校验】业务上送了错误的number类型字段，应该是string类型!\n【异常接口：】${url},\n【异常参数：】${key}:${value}。\n（注：该校验及提示仅测试环境生效，该问题请测试人员提CQ优化）`;                App.jsApi.appLaunchPopDialog({                    desc: message,                    buttonList: ["确定"]                });            }            if (valueType === "object" || valueType === "array") this.checkNumberParams(url, value);        });    }    // 判断数据类型    getType(val) {        return Object.prototype.toString            .call(val)            .replace(/\[object\s(\S+)\]/, "$1")            .toLowerCase();    }    /**     * 打开停服页面     */    openWebPageOfNoService() {        App.jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html?service=0" });    }}export default {    beforeStart: function(App) {
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 
        App.IContainer.inject(new SelfRequesterConfig());    },    afterStart: function(App) {        //TODO: 以下内容将在后续移入ehas core中做集成处理        App.requester.on("before", (target, option) => {            if (!option.slient) {                let floorPageHashList = [                    "#FloorHome/Home/Index",                    "#FloorHome/Financial/Index",                    "#FloorHome/Life/Index",                    "#FloorHome/CreditCard/Index",                    "#MessageCenter/MessageCenter/Index",                    "#FloorHome/Loan/Index"                ];                if (floorPageHashList.indexOf(location.hash) !== -1) {
//EHAS_RESULT_BRANCH_START true 
                    // window.$G.logErrorWithCode("src/config/requestSetting.js频道页请求带loading", `频道页请求带loading接口：${option.url}`);
//EHAS_RESULT_BRANCH_END 
                    return;                }                // 针对转账模块下所有请求loading的展示，增加请求信息作为loading初始化参数                // 方便在loading异常时能够上送APM                option.needCheck = false;                let loadingOptions = {};                if (location.hash.includes("#Transfer/Transfer")) {                    option.needCheck = true;                    loadingOptions = {                        requestInfo: {                            url: option.url,                            hash: location.hash                        }                    };                }                option.closeLoading = ViewBase.prototype.$loading(loadingOptions);            }        });        App.requester.on("after", (e, option) => {            if (!option.slient) {                option.closeLoading && option.closeLoading();            }        });    }};//蜜罐hash字段：md5加密function createHexmd5(object) {    let valuesArray = Object.values(object);    let jsonString = valuesArray.join("");    let jsonData = encryptJS.MD5(jsonString);    return jsonData;}//蜜罐istransaction字段:   生成随机的 true  falsefunction creatRandomBoolean() {    let index = Math.round(Math.random());    return index == 1 ? "true" : "false";}