/* eslint-disable */
//引入视图
import { ViewBase } from "@ehas/core";
import { getLocalUserInfo } from "~/common/utils/user";
/**
 * 获取客户等级校验失败后的提示信息
 */
function getAuthInfo(item) {
    const msg = App.message?.get("authMsg") || {};
    const authMsg = msg[item.bsnCode] || {};
    if (item.customerAuth == "1" || item.customerAuth == "2") {
        return {
            title: `该功能需要添加${App.bank.flags.common.bankName}借记卡或存折后使用`,
            cancelBtnTxt: "取消",
            sureBtnTxt: "立即添加",
            sureBtnUrl: "#Account/MyAccount/MyAcctList",
            ...authMsg
        };
    } else if (item.customerAuth == "3") {
        return {
            title: `该功能需要添加${App.bank.flags.common.bankName}柜面开立或面签的账户后使用`,
            cancelBtnTxt: "取消",
            sureBtnTxt: "立即添加",
            sureBtnUrl: "#Account/MyAccount/MyAcctList",
            ...authMsg
        };
    } else {
        return {
            title: `该功能需要前往${App.bank.flags.common.bankName}网点升级手机银行后使用`,
            cancelBtnTxt: "我知道了",
            sureBtnTxt: "去网点",
            sureBtnUrl: "#BranchService/BranchInquery/Index",
            ...authMsg
        };
    }
}
/**
 * 用户权限校验
 * @param {*} txCd 交易码
 * @param {*} stackJump 压栈跳转
 *  TRN000100 银行账号转账
 *  TRN000200 手机号转账
 *  TRN000300 预约转账
 *  TRN000400 慈善捐款
 *  TRN000201 手机号转账签约收款账户
 *  TRN000800 资金归集收款账户
 *  PMT000200 预约取款
 *  PMT000300 扫码取款
 *  @return {*} Boolean: true-权限满足,false-权限不足
 */
function checkAuth(txCd, stackJump) {
    return App.requester
        .request("act/acct/authctrlchk.biz", {
            data: {
                txCd: txCd
            },
            useDefaultError: false
        })
        .then(() => {
            return true;
        })
        .catch(rsp => {
            let menuInfo = App.dict.get("ERROR_MSG", rsp.code);
            //清除缓存
            App.storage.remove("TransferSignManagement", App.router.fragmentUrl);
            //存储本页面路径
            App.storage.set("TransferSignManagement", App.router.fragmentUrl);
            if (menuInfo && rsp.code == "EA1J061B") {
                return App.getViewInstance("content").$confirm({
                    message: rsp.message,
                    sureText: menuInfo.menu,
                    closed: isSure => {
                        if (isSure) {
                            if (menuInfo.url) {
                                if (stackJump) {
                                    window.$G.goToNextRetain({ router: menuInfo.url });
                                } else {
                                    window.$G.goToNext(menuInfo.url);
                                }
                            }
                        }
                    }
                });
            } else if (menuInfo && rsp.code != "EA1J135B") {
                return App.getViewInstance("content").$confirm({
                    message: rsp.message,
                    sureText: menuInfo.menu,
                    closed: isSure => {
                        if (isSure) {
                            if (menuInfo.url) {
                                if (stackJump) {
                                    window.$G.goToNextRetain({ router: menuInfo.url });
                                } else {
                                    window.$G.goToNext(menuInfo.url);
                                }
                            } else {
                                return true;
                            }
                        } else {
                            App.router.back();
                        }
                    }
                });
            } else if (menuInfo && rsp.code == "EA1J135B") {
                return App.getViewInstance("content").$confirm({
                    message: rsp.message,
                    sureText: menuInfo.menu,
                    closed: isSure => {
                        if (isSure) {
                            if (menuInfo.url) {
                                if (stackJump) {
                                    window.$G.goToNextRetain({ router: menuInfo.url });
                                } else {
                                    window.$G.goToNext(menuInfo.url);
                                }
                            } else {
                                return true;
                            }
                        }
                    }
                });
            } else {
                window.$G.popErrorMsg(rsp);
            }
        });
}

/**
 * 非四级用户访问签约界面弹窗阻止
 * @return {Boolean} true-权限满足,false-权限不足
 */
async function verifyLevel4UserRight(
    { message, sureText, cancelText } = {
        message: `该功能需要前往${App.bank.flags.common.bankName}网点升级手机银行后使用`,
        sureText: "去网点",
        cancelText: "我知道了"
    }
) {
    // 获取当前法人行是否需要此判断
    let level4UserRight = App.bank.flags?.common?.verifyLevel4UserRight;
    if (level4UserRight) {
        // 获取存储的用户信息
        const localUserInfo = await getLocalUserInfo();
        const userLevel = await App.jsApi.readFromMemoryStorage({ key: "m_userLevel" });
        // 获取用户等级
        let level = userLevel?.result?.m_userLevel || localUserInfo?.userLevel;
        if (parseInt(level) != 4) {
            // 弹窗提示
            ViewBase.prototype.$confirm({
                message: message,
                sureText: sureText,
                cancelText: cancelText,
                closed(isSure) {
                    if (isSure) {
                        window.$G.goToNext("#BranchService/BranchInquery/Index");
                    }
                }
            });
            return false;
        }
    }
    return true;
}
export { getAuthInfo, checkAuth, verifyLevel4UserRight };
