/* eslint-disable max-len */
/* eslint-disable max-lines-per-function */
import { TalkingData } from "@ehas/collection-talkingdata";
import dataCollectionConfig from "../common/datas/dc";
import menu from "../common/datas/menu";

// 获取埋点信息: "页面ID || 页面名称 || 页面传参"
const getPageInfo = async function(fragmenutUrl, fragment, routerOption) {
    // 获取当前菜单信息
    const menuInfo = await menu.findByUrl(fragmenutUrl);
    const { bsnCode } = menuInfo || {};

    // 获取所有父级菜单信息, 并生成菜单层级路径
    const menusChain = menuInfo ? await menu.getMenusChain(menuInfo) : [];
    const menusNameStr = menusChain.map(menu => menu.bsnName).join("/");

    // 获取菜单传参
    const routerKey = [fragment.moduleName, fragment.menuName, fragment.pageName].join("/");
    const paramSetting = dataCollectionConfig[routerKey];

    const urlParam = fragment.params || [];
    let pageParam = routerOption.pageParam || {};
    if (pageParam.param) {
        //对象传参时, 有些页面的pageParam会使用pageParam.param内的值
        pageParam = Object.assign({}, pageParam, pageParam.param);
    }

    const paramValuesList = [];
    if (paramSetting && (pageParam || urlParam.length)) {
        paramSetting.forEach(keyName => {
            let param = "";
            if (typeof keyName === "string") param = pageParam[keyName]; // pageParam对象传参
            if (typeof keyName === "number") param = urlParam[keyName] || ""; // url传参，用数字索引取值

            if (param) paramValuesList.push(param);
        });
    }

    // 返回页面分析所需数据
    const resultForCollection = [bsnCode, menusNameStr, ...paramValuesList];
    if (resultForCollection.every(item => !item)) return;
    return resultForCollection.join("||");
};

export default {
    beforeStart: function(App) {
        App.IContainer.inject(new TalkingData());
    },
    afterStart: function(App) {
        // eslint-disable-next-line complexity
        App.DC.on("check", async (e, data) => {
            const param = data.params[0] || {};
            let menuInfo = null;
            let bsnCode = "";
            let bsnName = "";
            let pageId = "";

            switch (data.eventId) {
                case "COMMON_TX_RESULT":
                    menuInfo = await menu.findByUrl(data.url);
                    ({ bsnCode, bsnName } = menuInfo || {});
                    param.CMM_TX_PAGEID = bsnCode;
                    param.CMM_TX_PAGENM = bsnName;
                    break;

                case "page":
                    pageId = await getPageInfo(param.fragmentUrl, param.fragment, param.routerOption || {});
                    if (!pageId) return false;
                    param.pageId = pageId;
                    break;
                case "pageStart":
                    pageId = await getPageInfo(param.newFragmentUrl, param.newFragment, param.newRouterOption || {});
                    if (!pageId) return false;
                    param.pageId = pageId;
                    break;
                case "pageEnd":
                    pageId = await getPageInfo(param.oldFragmentUrl, param.oldFragment, param.oldRouterOption || {});
                    if (!pageId) return false;
                    param.pageId = pageId;
                    break;

                //confirm弹窗
                case "COMMON_COMPENT_CONFORM":
                    menuInfo = await menu.findByUrl(data.url);
                    ({ bsnCode, bsnName } = menuInfo || {});
                    param.CMM_CONFORM_PAGEID = bsnCode;
                    param.CMM_CONFORM_PAGENM = bsnName;
                    break;

                //分享
                case "COMMON_COMPENT_SHARE":
                    menuInfo = await menu.findByUrl(data.url);
                    ({ bsnCode, bsnName } = menuInfo || {});
                    param.CMM_SHARE_PAGEID = bsnCode;
                    param.CMM_SHARE_PAGENM = bsnName;
                    break;

                case "error":
                    Object.assign(param, {
                        //出错文件URL
                        ERR_FILE_URL: param.ERR_FILE_URL ? param.ERR_FILE_URL : param.pageUrl,
                        //出错基本信息
                        ERR_BASE_INF: param.ERR_BASE_INF ? param.ERR_BASE_INF : param.message
                    });
                    break;
                default:
            }
        });
    }
};
