/* eslint-disable */
import { isObject } from "./check";
import { encryptJS } from "@ehas/encrypt";
import { stringUtils, dateUtils } from "@ehas/utils";
/**
 * 将版本号转换为数字
 */
function toNumVersion(clientVersion) {
    // 将字符串转成数组
    let verArr = clientVersion?.split(".");
    // 将数组中只有一位的元素头部填充0
    verArr = verArr?.map(item => {
        return item.padStart(2, '0')
    })
    if (verArr?.length < 3) {
        // 数组长度小于3的部分填充字符串'00'
        const padArr = new Array(3 - verArr.length).fill('00')
        verArr = verArr.concat(padArr)
    }
    const strVer = verArr?.join('')
    return Number(strVer) > 0 ? Number(strVer) : 0;
}

/**
 * 格式化证件有效期
 */
function fmtCertVldDate(date, type) {
    let startDate = date.substring(0, 8);
    let endDate = date.substring(9);
    let resultDate = "";
    if (endDate == "长期") {
        endDate = "99991231";
    }
    if (type == 1) {
        resultDate =
            dateUtils.format(startDate, "YYYYMMDD", "YYYY.MM.DD") +
            "-" +
            dateUtils.format(endDate, "YYYYMMDD", "YYYY.MM.DD");
    } else if (type == 2) {
        resultDate = startDate + "-" + endDate;
    } else if (type == 3) {
        resultDate = startDate + "";
    } else if (type == 4) {
        resultDate = endDate + "";
    }
    return resultDate;
}

/**
 * 将utf16转成utf8
 * @param {*} str 数据
 * @returns {*} obj
 */
function utf16to8(str) {
    let out, i, len, c;
    out = "";
    len = str.length;
    for (i = 0; i < len; i++) {
        c = str.charCodeAt(i);
        if (c >= 0x0001 && c <= 0x007f) {
            out += str.charAt(i);
        } else if (c > 0x07ff) {
            out += String.fromCharCode(0xe0 | ((c >> 12) & 0x0f));
            out += String.fromCharCode(0x80 | ((c >> 6) & 0x3f));
            out += String.fromCharCode(0x80 | ((c >> 0) & 0x3f));
        } else {
            out += String.fromCharCode(0xc0 | ((c >> 6) & 0x1f));
            out += String.fromCharCode(0x80 | ((c >> 0) & 0x3f));
        }
    }
    return out;
}
/**
 * 将base64转义
 * @param {*} json 数据
 * @returns {*} obj
 */
function jsonObjectToBase64(json) {
    var j = isObject(json) ? json : {};
    var s = JSON.stringify(j);
    return encodeURIComponent(base64encode(utf16to8(s)));
}

/**
 * 字符串去除所有空格
 * TODO 待平台抽出
 * @param {*} str
 */
function removeSpace(str) {
    return str.replace(/\s*/g, "");
}

/**
 * 字符长度截取
 * pStr 需要截取的字符串
 * pLen 截取长度
 */
function autoAddEllipsits(pStr, pLen) {
    const _ret = custString(pStr, pLen);
    const _cutStringn = _ret.custstring;

    return _cutStringn;
}
/**
 * 字符长度截取处理
 */
function custString(pStr, pLen) {
    //原字符串长度
    let _strLen = pStr.length,
        _cutString,
        _lenCount = 0,
        _ret = false;
    if (_strLen <= pLen / 2) {
        _cutString = pStr;
        _ret = true;
    }
    if (!_ret) {
        for (let i = 0; i < _strLen; i++) {
            if (isFull(pStr.charAt(i))) {
                _lenCount += 2;
            } else {
                _lenCount += 1;
            }
            if (_lenCount > pLen) {
                _cutString = pStr.substring(0, i);
                _ret = true;
                break;
            } else if (_lenCount == pLen) {
                _cutString = pStr.substring(0, i + 1);
                _ret = true;
                break;
            }
        }
    }
    if (!_ret) {
        _cutString = pStr;
        _ret = true;
    }
    return {
        custstring: _cutString
    };
}
/**
 * 判断字节长度
 */
function isFull(pChar) {
    for (let i = 0; i < pChar.length; i++) {
        if (pChar.charCodeAt(i) > 128) {
            return true;
        } else {
            return false;
        }
    }
}
/**
 * 生成随机数
 */
let uniqueAlphaNumericId = (() => {
    const heyStack = "0123456789abcdefghijklmnopqrstuvwxyz";
    const randomInt = () => Math.floor(Math.random() * Math.floor(heyStack.length));

    return (length = 24) => Array.from({ length }, () => heyStack[randomInt()]).join("");
})();

/**
 * base64解码
 * @param {*} str base64字符串
 */
function base64ToUtf8(str) {
    if (stringUtils.isStrEmpty(str)) {
        return str;
    }
    return encryptJS.base64ToUtf8(str);
}

/**
 * 将数据转成base64编码
 * @param {*} res 数据
 * @returns {*} obj
 */
function base64encode(str) {
    const base64encodechars = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/";
    let out, i, len;
    let c1, c2, c3;
    len = str.length;
    i = 0;
    out = "";
    while (i < len) {
        c1 = str.charCodeAt(i++) & 0xff;
        if (i == len) {
            out += base64encodechars.charAt(c1 >> 2) + base64encodechars.charAt((c1 & 0x3) << 4);
            out += "==";
            break;
        }
        c2 = str.charCodeAt(i++);
        if (i == len) {
            out +=
                base64encodechars.charAt(c1 >> 2) +
                base64encodechars.charAt(((c1 & 0x3) << 4) | ((c2 & 0xf0) >> 4)) +
                base64encodechars.charAt((c2 & 0xf) << 2);
            out += "=";
            break;
        }
        c3 = str.charCodeAt(i++);
        out +=
            base64encodechars.charAt(c1 >> 2) +
            base64encodechars.charAt(((c1 & 0x3) << 4) | ((c2 & 0xf0) >> 4)) +
            base64encodechars.charAt(((c2 & 0xf) << 2) | ((c3 & 0xc0) >> 6)) +
            base64encodechars.charAt(c3 & 0x3f);
    }
    return out;
}

/**
 * md5加密
 * @param {*} str
 */
function md5(str) {
    return encryptJS.MD5(str);
}

/**
 * 5.0报文进行加密
 * @param {*} str
 */
function contextStrTogether(str) {
    if (stringUtils.isStrEmpty(str)) {
        return str;
    }
    return str + "!!!@@@"; // 5.0报文进行加密
}

/**
 * 首页、我的频道页问候语
 * @param {*} oprTime 服务端系统时间
 */
function formatGreetMsg(oprTime) {
    let welcome = "";
    // 811问候语差异化处理
    let greetDiffArr = App?.bank?.flags?.floorHome?.greetDiff || [];
    if(greetDiffArr?.length > 0) {
        // 18：00 - 6：00 跨零点单独处理
        welcome = greetDiffArr[0]?.greetMsg;
        // 其他时段（区间左闭右开）
        greetDiffArr.forEach(item => {
            if (oprTime >= item.start && oprTime < item.end) {
                welcome = item.greetMsg;
            }
        });
    } else {
        // 未配置法人行参数时的默认规则
        // 6:00-11:00为“上午好”，11:00-13:00为“中午好”，13:00-18:00为“下午好”，18:00-6:00为“晚上好”，左闭右开
        welcome = "晚上好!";
        if (oprTime >= 60000 && oprTime < 110000) {
            welcome = "上午好!";
        } else if (oprTime >= 110000 && oprTime < 130000) {
            welcome = "中午好!";
        } else if (oprTime >= 130000 && oprTime < 180000) {
            welcome = "下午好!";
        } 
    }
    return "，" + welcome;
}

/**
 * 截取a标签中的href路径
 * @param {*} str
 */
function extractAgreementUrl(str){
    if (stringUtils.isStrEmpty(str) || !str.includes("href")) {
        return str;
    }
    return str.match(/href="(\S*)"/)[1]
}

/**
 * 截取a标签中的内容
 * @param {*} str
 */
function extractAgreementName(str){
    if (stringUtils.isStrEmpty(str) || !str.includes("</a>")) {
        return str;
    }
    return str.match(/>(\S*)<\/a>/)[1]
}

export {
    toNumVersion,
    fmtCertVldDate,
    utf16to8,
    jsonObjectToBase64,
    removeSpace,
    autoAddEllipsits,
    custString,
    isFull,
    base64ToUtf8,
    base64encode,
    md5,
    contextStrTogether,
    uniqueAlphaNumericId,
    formatGreetMsg,
    extractAgreementUrl,
    extractAgreementName
};
