import { H5ErrorCollection } from "@ehas/h5error-collection";
import { getSdkVersion } from "@ehas/app-jsapi";
import { isLogin as checkIsLogin } from "~/common/utils/check";
export default {
    beforeStart: function(App) {
        App.IContainer.inject(new H5ErrorCollection());
    },
    afterStart: function(App) {
        App.EC.on("error", async (e, param) => {
            let moduleZipVersion = "";
            if (App.zipDownLoadManage && App.zipDownLoadManage.cacheZips[param.zipName]) {
                moduleZipVersion = App.zipDownLoadManage.cacheZips[param.zipName].zipVersion;
            }
            //拦截common/datas/menu.js文件中的报错，防止死循环
            if (param.tagName === "common/datas/menu.js") {
                App.ErrorCollector?.captureGlobalException(param.error, param);
                return true;
            }
            // 获取菜单编码
            window.$G.getMenuId(true).then(menuInfo => {
                let _param = Object.assign(param, {
                    //当前地址所在模块ZIP版本
                    moduleZipVersion: moduleZipVersion,
                    //jsapi版本
                    jsapiVersion: getSdkVersion(),
                    //菜单码
                    menuId: menuInfo ? menuInfo.bsnCode : "",
                    //菜单名称
                    menuName: menuInfo ? menuInfo.bsnName : ""
                });
                //听云捕捉错误
                App.ErrorCollector?.captureGlobalException(_param.error, _param);
                checkIsLogin().then(res => {
                    if (!res) {
                        // 判断是否登录
                        App.ErrorCollector?.addUserInfo({
                            isLogin: false
                        });
                    } else {
                        App.ErrorCollector?.addUserInfo({
                            isLogin: true
                        });
                    }
                });
            });
        });
    }
};
