import { toNumVersion, jsonObjectToBase64, base64encode } from "./format";
import { isLogin as checkIsLogin } from "./check";
import { dealDeviceAuth } from "./error";
import { getloginUserNm } from "./user";
import { getBankName } from "./bank";
import { getUrlParam } from "./https";
import { clickGoodDetail, clickSalaryEasy } from "~/common/js/lifeUtil";
import { urlUtils } from "@ehas/utils";
