import { getSdkVersion } from "@ehas/app-jsapi";
import { goToLogin } from "~/common/utils/login.js";
/**
 * 扫一扫
 * @param {*} flag 自主跳转标志
 * @param {*} param 云闪付联登参数
 * @returns {*} obj
 */
function scanQRcode(flag, param) {
    if (flag) {
        //有flag参数的走行内扫一扫
        return scanQRcodeR(flag);
    } else if (param) {
        //有云闪付联登参数的直接跳转云闪付扫一扫
        unionpayScanQRCode(param);
    } else {
        let TypeofPhone = App.jsApi.isInIOS();
        const phoneVersion = TypeofPhone
            ? App.bank.flags.common?.unionPaySdkIOSVersion
            : App.bank.flags.common?.unionPaySdkAndroidVersion;
        if (phoneVersion) {
            const clientVersion = App.jsApi.getAppVerion();
            const booleanAlert = toNumVersion(clientVersion) < toNumVersion(phoneVersion);
            if (booleanAlert) {
                //不支持云闪付sdk的走行内扫一扫
                scanQRcodeR();
            } else {
                checkUnionpayScan();
            }
        } else if (getSdkVersion() >= 6) {
            checkUnionpayScan();
        } else {
            //不支持云闪付sdk的走行内扫一扫
            scanQRcodeR();
        }
    }
}

/**
 * 支持云闪付的客户端版本是否走云闪付
 */
function checkUnionpayScan() {
    App.jsApi.jsapiExistInApp({ jsApiList: ["unionpayLoginin", "openUnionpayContainer"] }).then(isExistRsp => {
        if (isExistRsp.result?.unionpayLoginin) {
            checkIsLogin().then(data => {
                if (data) {
                    const newSdkVersion = isExistRsp.result?.openUnionpayContainer;
                    App.requester
                        .request("lif/sdknetpay/binduserqry.biz", {
                            useDefaultError: false,
                            data: {
                                sdkVersion: newSdkVersion ? "V2" : ""
                            }
                        })
                        .then(rsp => {
                            //试运行flag 0:不支持云闪付，1:试点版云闪付，V2:企标版云闪付
                            if (newSdkVersion && rsp.sprtFlg == "V2") {
                                //企标版客户端+企标版白名单用户
                                if(rsp.rspCode != "0000000000"){
                                     //云闪付企标版会返回rspCode，不等于0000000000时报错
                                    App.getViewInstance("content").$alert({
                                        message: rsp.errDsc || "云闪付统一移动支付扫一扫服务异常，部分二维码可能无法识别。",
                                        btnText: "我知道了",
                                        closed() {
                                            scanQRcodeR();
                                        }
                                    });
                                } else if (rsp.sn && rsp.validDt) {
                                    //已授权
                                    unionpayScanQRCode(rsp);
                                } else if(App.bank.flags.common?.isSupportQuickPassManage){
                                    //817企标版调用扫一扫时未授权，使用行内扫一扫
                                    scanQRcodeR(); 
                                } else {
                                    //去授权
                                    window.$G.goToNextIgnore("#QuickPassManage/Authorize/Authorize", {
                                        url: "#Scan/QuickPass/QuickPassScan"
                                    });
                                }
                            } else if (!newSdkVersion && rsp.sprtFlg != "0") {
                                //试点版客户端+试点版/企标版白名单用户
                                if (rsp.sn && rsp.validDt) {
                                    //已授权
                                    unionpayScanQRCode(rsp);
                                } else {
                                    //未授权
                                    window.$G.goToNext("#Open/Auth/QuickPassAuth", {
                                        url: "#Scan/QuickPass/QuickPassScan"
                                    });
                                }
                            } else {
                                //不在试运行白名单里的走行内扫一扫
                                scanQRcodeR();
                            }
                        })
                        .catch(() => {
                            unionpayErrorHandling();
                        });
                } else {
                    //未登录的走行内扫一扫
                    scanQRcodeR();
                }
            });
        } else {
            //不支持云闪付sdk的走行内扫一扫
            scanQRcodeR();
        }
    });
}
/**
 * 云闪付扫一扫
 * @param {*} param 云闪付联登参数
 */
function unionpayScanQRCode(param) {
    //云闪付登录
    App.jsApi
        .unionpayLoginin({ sn: param.sn, duration: param.validDt })
        .then(() => {
            //获取sn失败
            App.jsApi.on("onUnionpaySn", () => {
                unionpayErrorHandling();
            });
            //跳转云闪付扫一扫页面
            App.jsApi
                .unionpayScanQRCode()
                .then(() => {
                    App.storage.set("isUnionPayBackFlag", true);
                    App.jsApi.on("onUnionpayScanQRCode", rsp => {
                        if (rsp?.result?.content) {
                            // 新增功能跳转,通过功能码跳转到页面
                            const resType = rsp.result.content.substring(0, 10);
                            const resUrl = rsp.result.content.substring(18, 30);
                            if (resType == "?dType=302") {
                                App.menu.go(resUrl);
                            } else {
                                analysisQRcodeR(rsp.result.content).then(rsp => {
                                    goPageQRcode(rsp);
                                });
                            }
                        }
                    });
                })
                .catch(() => {
                    App.jsApi.off("onUnionpaySn");
                    App.jsApi.unionpayLoginout({ loginoutCode: "0002" });
                    unionpayErrorHandling();
                });
        })
        .catch(() => {
            unionpayErrorHandling();
        });
}
/**
 * 云闪付扫一扫异常处理
 */
function unionpayErrorHandling() {
    App.getViewInstance("content").$alert({
        message: "云闪付统一移动支付扫一扫服务异常，部分二维码可能无法识别。",
        btnText: "我知道了",
        closed() {
            scanQRcodeR();
        }
    });
}
/**
 * 扫一扫
 * @param {*} flag 自主跳转标志
 * @returns {*} obj
 */
function scanQRcodeR(flag) {
    // 判断扫一扫页面是否展示付款码
    const cameraType = App.bank.flags.common.cameraContentStyle ? "1" : "0";
    if (flag) {
        return App.jsApi
            .scanQRCode({ contentStyle: cameraType })
            .then(res => {
                //  返回扫描结果
                if (res.result.actionType == "2") {
                    // 点击【相册】按钮 事件ID：页面ID+001
                    window.$G.getMenuId().then(menuId => {
                        App.DC.onEvent(`${menuId}_001`);
                    });
                    // 新增功能跳转,通过功能码跳转到页面
                    const resType = res.result.qrCode.substring(0, 10);
                    const resUrl = res.result.qrCode.substring(18, 30);
                    if (resType == "?dType=302") {
                        App.menu.go(resUrl);
                    } else {
                        return analysisQRcodeR(res.result.qrCode);
                    }
                } else {
                    // 扫一扫页面点击事件
                    dealQRcodeType(res.result.actionType);
                }
            })
            .catch(res => {
                // 处理错误码-相机
                dealDeviceAuth(res, "2");
            });
    } else {
        App.jsApi
            .scanQRCode({ contentStyle: cameraType })
            .then(res => {
                //  返回扫描结果
                if (res.result.actionType == "2") {
                    // 点击【相册】按钮 事件ID：页面ID+001
                    window.$G.getMenuId().then(menuId => {
                        App.DC.onEvent(`${menuId}_001`);
                    });
                    // 新增功能跳转,通过功能码跳转到页面
                    const resType = res.result.qrCode.substring(0, 10);
                    const resUrl = res.result.qrCode.substring(18, 30);
                    if (resType == "?dType=302") {
                        App.menu.go(resUrl);
                    } else {
                        analysisQRcodeR(res.result.qrCode).then(res => {
                            goPageQRcode(res);
                        });
                    }
                } else {
                    // 扫一扫页面点击事件
                    dealQRcodeType(res.result.actionType);
                }
            })
            .catch(res => {
                // 处理错误码-相机
                dealDeviceAuth(res, "2");
            });
    }
}
/**
 * 扫一扫页面点击处理
 * @param {*} type 付款码-0，收款码-1
 */
function dealQRcodeType(type) {
    // 判断动作
    if (type == "0") {
        // 点击【付款码】按钮 事件ID：页面ID+002
        window.$G.getMenuId().then(menuId => {
            App.DC.onEvent(`${menuId}_002`);
        });
        // 跳转支付平台-付款码（多法人功能码）
        if (App.bank.flags.common.goPayCode) {
            window.App.menu.go(App.bank.flags.common.goPayCode);
        }
    } else if (type == "1") {
        // TODO 跳转收款码
    } else {
        return "";
    }
}
/**
 * 扫一扫数据解析,返回信息
 * @param {*} codeStr 需要解析的字符串
 * @returns {*} obj
 */
async function analysisQRcodeR(codeStr) {
    let bankId = App.bank.flags?.common?.bankId;
    const isLogin = await checkIsLogin();
    let signPhoneNo = (await getloginUserNm()) || "";
    // 加密二维码处理(包含uuid)
    if (codeStr && codeStr.includes("uuid")) {
        try {
            let getCodeStr = JSON.parse(codeStr);
            if (getCodeStr && typeof getCodeStr === "object" && getCodeStr.uuid) {
                let { shrLinkAdr } = await App.requester.request("wlh/share/shrlinkadrqry.biz", {
                    data: {
                        uuid: getCodeStr.uuid
                    }
                });
                codeStr = `${shrLinkAdr}`;
            } else {
                codeStr = JSON.stringify(getCodeStr);
            }
        } catch {
            //默认codeStr
        }
    }
    // codeStr ----> base64
    let baseStr = base64encode(codeStr);
    if (isLogin && bankId == "810") {
        return App.requester
            .request("trn/scancd/scancdrcgz.biz", {
                data: {
                    tdcdOrgnStrInf: baseStr, //二维码信息串
                    signPhoneNo
                }
            })
            .then(rsp => {
                return rsp;
            });
    } else {
        return App.requester
            .request("trn/scancd/scancdrcgz.biz", {
                data: {
                    tdcdOrgnStrInf: baseStr //二维码信息串
                }
            })
            .then(rsp => {
                return rsp;
            });
    }
}

/**
 * 扫一扫跳转页面
 * @param {*} res 解析结果
 * @returns {*} obj
 */
function goPageQRcode(res) {
    const typeObj = {
        1: goPageApp, //6.0App
        2: goPageApp5, //5.0APP
        3: goPageThree, //第三方页面
        4: goPageThreeInner, //第三方内嵌页面
        7: goPageApplet, //积分商城小程序页面
        lon: gotoLoanProduct //贷款
    };
    let type = res.skpTyp; //类型
    let scanData = res.dbResultMap01 || {};

    //通过功能编码获取路由信息
    //智慧厅堂802
    //理财、基金
    if (res.tdType == "201" || res.tdType == "202") {
        scanData.tdType = res.tdType; //扫码标识
        scanData.pdNm = res.pdNm; //产品名称
        if (App.bank.flags?.scan?.scanToSave) {
            scanData.isFromScan = "1"; //扫一扫跳转标识
            scanData.pdCd = res.pdCd;
        }
        //810行 长量基金
        if (
            App.bank.flags?.scan?.canGoChangLiang &&
            (scanData.txNo == "SFUD010100" ||
                scanData.txNo == "SFUD010200" ||
                scanData.txNo == "SFUD010400" ||
                scanData.txNo == "SFUD010500")
        ) {
            let params = `${scanData.txNo}.html?param=${scanData.PrdCode}#/productQuery/detail`;
            App.menu.go("MBOLD" + scanData.txNo, params);
            return;
        }
    }
    //大额存单,存款
    else if (res.tdType == "206") {
        scanData.tdType = res.tdType; //扫码标识
        scanData.chnlPdClsNo = res.chnlPdClsNo; //渠道
        scanData.pdCd = scanData.PrdCode; //产品编码
    }
    //贷款
    else if (res.tdType == "208") {
        type = "lon";
        res = {
            pgNo: res.skpPath,
            jumpTyp: res.skpTyp,
            pdCd: scanData.PrdCode
        };
    }
    //贵金属
    else if (res.tdType == "203") {
        // 贵金属扫码跳转更改为去贵金属列表页 5.0提供跳转路径方式
        // 是否跳6.0新包
        if (App.bank.flags?.scan?.gjsIsGoNewPackage) {
            let nextParam = {
                chnlPdNo: res.chnlPdNo,
                isFromScan: "1"
            };
            App.menu.go(res.funcId, nextParam);
            return;
        } else {
            res.pageId = res.funcId.slice(5) + ".html";
        }
    }
    // 保险
    else if (res.tdType == "204") {
        // 保险跳转5.0的包
        window.App.menu.go({ url: res.funcId, jumpType: res.skpTyp }, { page: res.skpPath });
        return;
    }
    // 810自己积分商城外放新增211
    else if (res.tdType == "211") {
        let param = { pgNo: res.skpPath };
        clickGoodDetail(param);
        return;
    } else if (res.tdType == "26") {
        //目前暂定tdType等于26
        goPageApp(res);
        return;
    } else if (res.tdType == "39") {
        // 817理财
        let param = {
            url: decodeURIComponent(res.funcId)
        };
        App.jsApi.openWebPageByJSBContainer(param);
        return;
    }
    //  对接薪易平台新做处理
    //  802、803、831行
    else if (res.tdType == "45" || res.tdType == "46") {
        let param = {
            pgNo: res.skpPath,
            jumpTyp: res.skpTyp
        };
        clickSalaryEasy(param);
        return;
    }
    if (res.skpTyp == "5" || res.tdType == "40") {
        // 扫码登录  如果skpTyp等于40代表网银扫码登录
        window.$G.goToNext(`#Scan/Login/Login`, {
            uniqueNo: res.dbResultMap01.uniqueNo,
            //与服务端约定规则：如果dbResultMap01对象中存在txCd，则表示二维码认证
            //如果dbResultMap01对象中存在transId，则表示网银扫码认证
            skpTyp: res.skpTyp, //将跳转类型传递到下个页面，用来处理对应的业务流程
            txCd: res.dbResultMap01.txCd,
            tdType: res.tdType //交易类型
        });
        return;
    }
    if (res.skpTyp == "6") {
        let param = { url: res.skpPath };
        App.jsApi.openWebPageByWebContainer(param);
        return;
    } // 齐商积分商城跳转
    if (res.tdType == "15") {
        if (App.bank.flags?.scan?.scanToLsIntegralMall) {
            typeObj[type](res);
        } else {
            App.menu.go(res.funcId);
        }
    } else {
        // 扫一扫
        typeObj[type](res);
    }
}
/**
 * 扫一扫跳积分商城（小程序打开）功能
 * @param {*} res 数据
 * @returns {*} obj
 */
function goPageApplet(res) {
    let param = {
        pgNo: res.skpPath,
        jumpTyp: res.skpTyp
    };
    clickGoodDetail(param);
    return;
}
/**
 * 扫一扫跳转6.0APP功能
 * @param {*} res 数据
 * @returns {*} obj
 */
function goPageApp(rsp) {
    //当通过扫一扫跳转电子回单时,新增传参
    if (rsp.funcId === "MBTSR011202") {
        rsp.dbResultMap01.fromScanQRCode = true;
    }
    //通过功能编码获取路由信息
    window.App.menu.go(rsp.funcId, rsp.dbResultMap01);
}
/**
 * 扫一扫跳转5.0菜单功能
 * @param {*} res 数据
 * @returns {*} obj
 */
function goPageApp5(rsp) {
    // 通过menuId和page跳转5.0菜单
    const dbResultMap = rsp.dbResultMap01; //参数信息
    let pageId = rsp.pageId; //页面链接
    let base64 = ""; //将页面参数转化为base64
    //  处理5.0  贵金属传了参数页面空白问题。去贵金属列表不拼接param参数---tdType == 203 贵金属
    if (rsp.tdType != "203") {
        if (dbResultMap.paramKey) {
            if (dbResultMap.ecrpTyp == "2") {
                //obj参数转化base64
                base64 = jsonObjectToBase64(dbResultMap.param);
            } else {
                base64 = base64encode(dbResultMap.param);
            }
            // 5.0 拼接参数 ？param = "*****"
            pageId = pageId + "?" + dbResultMap.paramKey + "=" + base64;
        }
    }

    window.App.menu.go(rsp.funcId, pageId);
}
/**
 * 扫一扫跳转第三方系统页面
 * @param {*} res 数据
 * @returns {*} obj
 */
function goPageThree(rsp) {
    // url跳转第三方系统页面
    window.App.menu.go(rsp.funcId, rsp.skpPath);
}
/**
 * 扫一扫第三方内嵌页面
 * @param {*} res 数据
 * @returns {*} obj
 */
async function goPageThreeInner(rsp) {
    // 810 209 未登录只跳登录页
    const isLogin = await checkIsLogin();
    let bankId = App.bank.flags?.common?.bankId;
    if (bankId == "810" && rsp.tdType === "209") {
        if (!isLogin) {
            //未登录跳转登录
            await goToLogin("", {}, { type: 1, selfRefreshFlag: true });
        } else {
            let param = { url: rsp.skpPath };
            param.title = " ";
            //已登录，跳转第三方页面
            App.jsApi.openWebPageByJSBContainer(param); //公共的业务逻辑调用
        }
    } else {
        // 跳转第三方页面（长登录机制，无需判断是否登录）
        // App.jsApi.goJSBPage({ url: rsp.skpPath });
        let flag = await checkIsLogin();
        if (flag) {
            let param = { url: rsp.skpPath };
            // 智慧商城页面加返回按钮
            if (rsp.tdType === "209") {
                param.title = " ";
            }
            if (rsp.tdType == "20") {
                param.url = urlUtils.encodeUrl(param.url);
            }
            //微信条码互通需要添加标题
            if (rsp.tdType == "18") {
                param.title = getBankName() + "付款";
            }
            //已登录，跳转第三方页面
            App.jsApi.openWebPageByJSBContainer(param); //公共的业务逻辑调用
        } else {
            //TODO 未登录
            let url = rsp.skpPath;
            let param = "";
            let config = {};
            config.type = rsp.tdType;
            goToLogin(url, param, config);
        }
        // 通过url和mchid查看系统打电话功调用失败的原因
        // App.jsApi.goJSBPage({ url: rsp.skpPath, mchid: rsp.thridMrctNo });
        // window.App.menu.go(rsp.funcId, rsp.thridMrctNo);
        // window.$G.goToNext("#Demo/Ndealt/List");
    }
}
/**
 * 扫一扫跳转贷款产品
 * @item {*} res 数据
 */
function gotoLoanProduct(item) {
    let noTitleLists = [
        "802S1611", // 802 存款质押贷
        "CP016", //802 存款质押贷（经营）
        "CP017", //802 存款质押贷（消费）
        "1110030080",
        "1110035017",
        "101021", // "101021 812e盈贷 "
        "101007", //"101007 812黄河e贷"
        "101009", // "101009 812质盈贷"
        "CP015", // "CP015 810 税云贷（日照）"
        "CP018", //"CP018 818 税易贷"
        "CP028", //"CP028 818物流贷"
        "CP034", // "CP034 818 存单快贷（个人消费）"
        "CP035", //  "CP035 818 存单快贷（生产经营）"
        "CP006" // ”CP006“ 103  幸福e贷
    ];
    let pgNo = item.pgNo;
    if (
        item.jumpTyp !== "3" &&
        item.jumpTyp !== "4" &&
        item.jumpTyp !== "8" &&
        item.pgNo &&
        pgNo.indexOf("http") !== 0 &&
        pgNo.indexOf("https") !== 0
    ) {
        App.getViewInstance("content").$toast("URL不合法：【" + pgNo + "】");
        return false;
    }
    const isInApp = App.jsApi.isInApp();
    // 是手机环境使用jsApi,否则使用路由跳转
    if (isInApp) {
        //jumpTyp === "8" 跳转进入微信小程序
        if (item.jumpTyp === "8") {
            // isHintSkip 跳转微信小程序时是否添加提示  810跳转微信小程序需要添加提示
            if (App.bank.flags?.loan?.isHintSkip) {
                App.getViewInstance("content").$confirm({
                    message: "即将为您打开借款应用，请登录后进行相关操作。",
                    sureText: "我知道了",
                    cancelText: "取消",
                    closed(isSure) {
                        if (isSure) {
                            //从pgNo截取出APPID和path APPID必传 path非必传
                            let appletJump = item.pgNo.split("?");
                            let userName = appletJump[0]; //APPID
                            let path;
                            // isPathJoint path是否需要拼接参数   目前810需要拼接
                            if (App.bank.flags?.loan?.isPathJoint) {
                                //日照行方要求小程序path后拼接参数，custNo客户经理编号 无值  但是日照行方要求 可以没值  但是参数要有 productNo 产品编号   channelType渠道号  固定值A03
                                path = appletJump[1]
                                    ? appletJump[1] + "?" + "custNo=&" + `productNo=${item.pdCd}` + "&channelType=A03"
                                    : "";
                            } else {
                                path = appletJump[1] ? appletJump[1] : "";
                            }
                            App.jsApi.openWechatMiniProgram({ userName, path }).catch(rsp => {
                                //通过code和messge可查看失败的原因
                                if (rsp.code == "EPFN515T") {
                                    ViewBase.prototype.$alert({
                                        message: "检测到您没有安装微信或微信版本太低，请安装或升级至微信的最新版本",
                                        btnText: "确定"
                                    });
                                } else {
                                    ViewBase.prototype.$toast(rsp.cvrtMsg);
                                }
                            });
                        }
                    }
                });
            } else {
                //从pgNo截取出APPID和path APPID必传 path非必传
                let appletJump = item.pgNo.split("?");
                let userName = appletJump[0]; //APPID
                let path;
                // isPathJoint path是否需要拼接参数   目前810需要拼接
                if (App.bank.flags?.loan?.isPathJoint) {
                    //日照行方要求小程序path后拼接参数，custNo客户经理编号 无值  但是日照行方要求 可以没值  但是参数要有 productNo 产品编号   channelType渠道号  固定值A03
                    path = appletJump[1]
                        ? appletJump[1] + "?" + "custNo=&" + `productNo=${item.pdCd}` + "&channelType=A03"
                        : "";
                } else {
                    path = appletJump[1] ? appletJump[1] : "";
                }
                App.jsApi.openWechatMiniProgram({ userName, path }).catch(rsp => {
                    //通过code和messge可查看失败的原因
                    if (rsp.code == "EPFN515T") {
                        ViewBase.prototype.$alert({
                            message: "检测到您没有安装微信或微信版本太低，请安装或升级至微信的最新版本",
                            btnText: "确定"
                        });
                    } else {
                        ViewBase.prototype.$toast(rsp.cvrtMsg);
                    }
                });
            }
        } else {
            /**
             * 这两款产品需要跳转5.0
             * 公积金贷 1110035080
                工资贷 1110035081
                所有产品 允许传title的跳转方式均需传
            */
            let page = "",
                options = {};
            let PrdCodeMap = {
                "1110035081": "1061712", // 工资贷
                "1110035080": "1061711", // 公积金贷
                "PD0001": "1E1040" // 市民贷 801
            };

            if (PrdCodeMap[item.pdCd]) {
                page = `${item.pgNo}.html?param=${jsonObjectToBase64({ PrdCode: PrdCodeMap[item.pdCd] })}`;
            }
            // noTitleLists:取消title的产品集合--删除802存款质押贷:802S1611、企好贷:1110030080,按揭专享贷:1110035017三款产品的联盟导航栏
            // 101021 812e盈贷   101007 812黄河e贷 101009 812质盈贷  CP015 810 税云贷（日照）"CP018 818 税易贷"  "CP028 818物流贷" "CP034 818 存单快贷（个人消费）" "CP035 818 存单快贷（生产经营）"也删除联盟导航栏
            // noTitle：取消title的法人行
            let noTitle = App.bank.flags.loan?.noTitle;
            let noTitleCredit1 = false;
            // noTitleCredit 信贷产品还款跳H5页面取消导航栏的法人行
            if (App.bank.flags.loan?.noTitleCredit && item.sourceNo == "0001") {
                noTitleCredit1 = true;
            }

            if (
                (item.jumpTyp === "5" || item.jumpTyp === "7") &&
                !noTitleLists.includes(item.pdCd) &&
                !noTitle &&
                !noTitleCredit1
            ) {
                options = { title: item.pdNm };
                // 815各产品增加人脸识别  "815JSY"济时雨E贷  "A09"小微信易贷  "AWD"小微贷款  "AX1" 云按揭   "D03"房产抵押贷
                let h5FaceList = ["815JSY", "A09", "AWD", "AX1", "D03"];
                if (h5FaceList.includes(item.pdCd)) {
                    // 济宁济时雨e贷调起人脸认证
                    options.mchid = "h5face";
                }
            }
            // 小程序跳转方式隐藏导航栏
            if (item.jumpTyp === "7" && (noTitleLists.includes(item.pdCd) || noTitle)) {
                options.isHiddenNavigationBar = true;
            }
            // 小程序跳转截取APPID
            if (item.jumpTyp === "7") {
                let appId;
                let searchArr = item.pgNo.split("#");

                for (let i = 0; i < searchArr.length; i++) {
                    appId = getUrlParam(searchArr[i], "appId");
                    if (appId) break;
                }
                if (appId) {
                    page = JSON.stringify({
                        appId,
                        licence: ""
                    });
                }
            }

            let param = page;
            let menuInfo = "";
            //810 行所有产品的路径都需要拼接参数  custNo客户经理编号 无值  但是日照行方要求 可以没值  但是参数要有 productNo 产品编号   channelType渠道号  固定值A03
            // channelType=A80 表示金融小店跳过来拼接过的 手机银行部需要拼接
            let pgNo;
            if (App.bank.flags?.loan?.isPathJoint && item.pgNo.indexOf("channelType=A80") === -1) {
                //没问号
                if (item.pgNo.indexOf("?") === -1) {
                    //没问号没#
                    if (item.pgNo.indexOf("#") === -1) {
                        pgNo = item.pgNo + `?custNo=&productNo=${item.pdCd}&channelType=A03`;
                    } else {
                        //没问号有#
                        let urlArr = item.pgNo.split("#");
                        pgNo = urlArr[0] + `?custNo=&productNo=${item.pdCd}&channelType=A03` + "#" + urlArr[1];
                    }
                } else {
                    // 有？没#
                    if (item.pgNo.indexOf("#") === -1) {
                        pgNo = item.pgNo + `&custNo=&productNo=${item.pdCd}&channelType=A03`;
                    } else {
                        // 有问号有#号
                        let urlArr = item.pgNo.split("?");
                        pgNo = urlArr[0] + `?custNo=&productNo=${item.pdCd}&channelType=A03` + "&" + urlArr[1];
                    }
                }
            } else {
                pgNo = item.pgNo;
            }
            if (item.jumpTyp === "3") {
                menuInfo = pgNo;
                param = { pdCd: item.pdCd };
            } else {
                menuInfo = { url: pgNo, mchid: "", jumpType: item.jumpTyp };
            }
            window.App.menu.go(menuInfo, param, options);
        }
    } else {
        // 跳转链接
        window.open(item.pgNo);
    }
}
export {
    scanQRcode,
    scanQRcodeR,
    checkUnionpayScan,
    unionpayScanQRCode,
    unionpayErrorHandling,
    analysisQRcodeR,
    goPageQRcode,
    dealQRcodeType,
    goPageApp,
    goPageApp5,
    goPageThree,
    goPageThreeInner,
    gotoLoanProduct,
    goPageApplet
};
