/* eslint-disable max-lines *//* eslint-disable max-statements *//* eslint-disable max-lines-per-function *//* eslint-disable max-lines *//* eslint-disable no-case-declarations */import { ViewBase, Log } from "@ehas/core";import { getJson } from "@ehas/request-ajax";import { getAuthInfo } from "~/common/utils/auth";import { getBankName } from "~/common/utils/bank";import { scanQRcode } from "~/common/utils/scan.js";import { isLogin as checkIsLogin, isMenuDisabled, remoteBankMenuDisabled } from "~/common/utils/check";import { transferUrl } from "../js/filters";import { viewed } from "~/common/js/viewed.js";import { globalUtil } from "../js/globalUtil";import { clientVersionCom } from "~/common/utils/device";import { toNumVersion } from "~/common/utils/format.js";import { goToLogin, dealLogin } from "~/common/utils/login.js";import agreement from "../datas/agreement";import { getLocalUserInfo, checkCustomerAuthInfo } from "~/common/utils/user";import { WapWeChatUtil } from "~/common/js/wap";import simpleMenuIdDc from "~/common/datas/simpleMenuIdDc"; //标准版 - 简版菜单映射const BANK_MENU_JSON = "bankMenuJsonFileCache";const MAX_DELAY_TIME = 2000;const randomVer = () => `?v=${Date.now()}`;const bankMenuWithVer = () => {    let bank = App.bank.flags.common.bankId;    return `${ehasProcessCDNServer}/public/${bank}/ecms/menus/A03/bankMenu.json${randomVer()}`;};const JSONParse = str => {    try {        str = JSON.parse(str);    } catch (e) {        // do nothing    }    return str || {};};export default {    data: undefined,    delay: null,    getDataPromise: null,    // 获取所有菜单的列表数据(初始化)    async getData() {        // 判断是否app中，兼容浏览器开发环境        let isInApp = App.jsApi.isInApp();        if (isInApp) {            // 尝试获取缓存数据            this.data = await App.local.get(BANK_MENU_JSON);        }        if (this.data) {            // 有缓存，继续进行下一步，异步请求JSON            this.getJsonFun(bankMenuWithVer())                .then(res => {                    this.getJsonSuccess(res);                })                .catch(e => {                    window.$G.logErrorWithCode("common/datas/menu.js菜单数据", JSON.stringify(e));                });        } else {            // 无缓存，等待JSON请求完成后进行下一步            try {                let res = await this.getJsonFun(bankMenuWithVer());                this.getJsonSuccess(res);            } catch (e) {                window.$G.logErrorWithCode("common/datas/menu.js菜单数据", JSON.stringify(e));            }        }        return this.data || [];    },    // 格式化菜单列表数据    getJsonFun: async url => {        // 判断是否app中，兼容浏览器开发环境        try {            let isInApp = App.jsApi.isInApp();            if (isInApp && 0) {                let res = await App.jsApi.getDecryptedConfigFileInfo({ url });                return {                    data: JSON.parse(res.result.contentString)                };            } else {                let res = await getJson(url);                return res;            }        } catch (error) {            window.$G.logErrorWithCode("common/datas/menu.js菜单数据第一次请求失败", "菜单数据第一次请求失败" + JSON.stringify(error));            try {                let isInApp = App.jsApi.isInApp();                if (isInApp && 0) {                    let res = await App.jsApi.getDecryptedConfigFileInfo({ url });                    return {                        data: JSON.parse(res.result.contentString)                    };                } else {                    let res = await getJson(url);                    return res;                }            } catch (error) {                window.$G.logErrorWithCode("common/datas/menu.js菜单数据第二次请求失败", "菜单数据第二次请求失败" + JSON.stringify(error));            }        }    },    // 缓存或更新菜单列表数据    getJsonSuccess(res) {        let dict = (this.dict = res.data.menus);        // 使用请求返回数据        this.data = this._formatData(dict);
//EHAS_RESULT_BRANCH_START true 
        this.getTestData(dict);
//EHAS_RESULT_BRANCH_END 
        // 更新缓存        App.local.set(BANK_MENU_JSON, this.data);    },    // 获取菜单列表数据    requestData() {        if (this.data) return this.data;        this.getDataPromise = this.getDataPromise || this.getData();        return this.getDataPromise;    },
//EHAS_RESULT_BRANCH_START true 
    // 获取测试环境中超级App链接    async getTestData(dict) {        const bank = App.bank.flags.common.bankId;        try {            const appInfoObj = await this.getJsonFun(                `${ehasProcessCDNServer}/public/999/super_app/config/${bank}.json`            );            const appInfo = appInfoObj.data;            if (appInfo) {                for (let i = 0; i < appInfo.length; i++) {                    let bsnInfo = appInfo[i].bsnInfo;                    for (let j = 0; j < bsnInfo.length; j++) {                        let { code, url } = bsnInfo[j];                        if (dict[code]) {                            dict[code].url = url;                        }                    }                }            }        } catch (error) {            Log.warn("测试环境中超级App链接配置下载失败，无影响");        }    },
//EHAS_RESULT_BRANCH_END 
    // 根据bsnCode获取菜单信息    async findById(id) {        if (this.dict) return this.dict[id];        const menuList = await this.requestData();        return menuList.find(menu => menu.bsnCode == id);    },    // 根据hash路由获取菜单信息    async findByUrl(hashRouter) {        if (!hashRouter) return;        const hashArray = hashRouter.split("/");        const [moduleName, menuName, pageName = ""] = hashArray;        const router = `${moduleName}/${menuName}/${pageName}`;        const menuList = await this.requestData();        let menu = null;        // 正常匹配不到，处理特殊情况        if (hashRouter.length > 3) {            // YuEBao/Index/Detail/000719 潍小宝和筝飞e盈等带参数路由的匹配方法            menu = menuList.find(item => hashRouter === item.replacedUrl);        }        if (!menu) {            menu = menuList.find(item => router === item.replacedUrl);        }        return menu;    },    // 以当前菜单为根结点依次查找父节点, 返回完整菜单链    async getMenusChain(menuInfo) {        const menuList = await this.requestData();        const parentMenu = menuList.find(menu => menu.bsnCode === menuInfo.parentId);        const parentMenus = parentMenu ? await this.getMenusChain(parentMenu) : [];        return [...parentMenus, menuInfo];    },    // 判断是否需要跳转登录(非内部跳转)    async willGoToLogin(menu, param) {        if (menu.loginFlag == "1" && menu.jumpType != "3") {            const isLogin = await checkIsLogin();            if (!isLogin) {                goToLogin(menu.bsnCode, param, { type: 3 });                return true;            }        }    },    // 跳转菜单    // eslint-disable-next-line complexity    async go(id, param = "", options) {        const connected = await this.checkNetWorkStatus(); // 检查网络状态        if (!connected) return false;        if (this.delay) return;        this.delay = true;        window.clearTimeout(this.timeout);        this.timeout = setTimeout(() => {            this.delay = false;        }, MAX_DELAY_TIME);        // 兼容贷款跳转        let menu = {};        if (typeof id === "object") {            // 贷款跳转            menu = id;        } else {            // 简版时 如果有对应简版菜单，则跳转简版菜单            id = (await this.exchangeSimpleId(id)) || id;            menu = await this.findById(id);            const checkMenuAuthPassed = await this.checkMenuAuth(id, param, menu); // 菜单权限检查            // 菜单权限检查失败退出            if (!checkMenuAuthPassed) {                this.delay = false;                return false;            }        }        //1:打开浏览器,2:JSAPI,3:内部,4:5.0-Cordova,5:5.0-jsBrage,6:超级APP,7:小程序        const { jumpType } = menu;        if (jumpType === "1") App.jsApi.openWebpageByBrowser({ url: menu.url });        if (jumpType === "2") this._openJsapi(menu);        if (jumpType === "3") this._goRouter(menu, param, options);        if (jumpType === "4") this._goCordova(menu, param || menu.param);        if (jumpType === "5") this._goJSBPage(menu, param, menu.param, options);        //超级app传值需要特殊设置功能码        if (jumpType === "6") this._goWebPage(menu, param || menu.param, options);        if (jumpType === "7") this._openApplet(menu, param || menu.param, options);        const resultData = { menu, param };        // 在浏览记录中缓存有图标的菜单        if (jumpType !== "3") {            viewed.cacheViewedHistory(resultData);        }        return resultData;    },    /**     * 检查网络状态     */    async checkNetWorkStatus() {        const networkStatus = await App.storage.get("onNetworkStatus");        if (networkStatus === false) {            App.getViewInstance("content").$confirm({                message: "网络异常，请检查您的网络设置",                sureText: "查看详情",                cancelText: "知道了",                closed: isSure => {                    if (isSure) {                        App.jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });                    }                }            });            return false;        }        return true;    },    /**     * 菜单权限检查     */    async checkMenuAuth(id, param, menu) {        // 获取菜单信息        if (!menu) throw new Error(`菜单跳转，未找到${id}`);        //菜单是否禁用        const menuDisabled = isMenuDisabled(menu);        if (menuDisabled) {            this.delay = false;            return;        }        // 810我的页签视频银行控制ios6.0.0点击报敬请期待，其他版本能正常使用        if (remoteBankMenuDisabled(menu.bsnCode)) {            this.delay = false;            return false;        }        //是否需要跳转登录        const willGoToLogin = await this.willGoToLogin(menu, param);        if (willGoToLogin) {            this.delay = false;            return;        }        Log.info("判断菜单登录权限");        // 长登陆校验token,无token跳转登录        // 银联二维码付款码        let MBTRN_CODE = ["MBTRN000100", "MBTRN000200", "MBTRN000300", "MBTRN000400"];        if (MBTRN_CODE.includes(menu.bsnCode)) {            let isToken = await App.jsApi                .getkeepLoginJWToken()                .then(res => {                    return res.result.token ? true : false;                })                .catch(() => {                    return false;                });            if (!isToken) {                goToLogin();                return false;            }        }        Log.info("判断菜单权限");        // 登录状态（银联二维码付款码校验长登陆token）        if (menu.jumpType != "3" || MBTRN_CODE.includes(menu.bsnCode)) {            Log.info("判断菜单用户等级权限");            const customerAuthCheckPassed = await this.customerAuthCheck({ menu: menu });            if (!customerAuthCheckPassed) {                return false;            }        }        return true;    },    _goRouter(menu, param, options = null) {        if (menu.bsnCode == "MBTSR015300") {            // 判断是否支持NFC插件            App.jsApi.jsapiExistInApp({ jsApiList: ["getNFCStatus"] }).then(res => {                if (res.result?.getNFCStatus) {                    // 判断手机是否支持NFC功能                    App.jsApi.getNFCStatus().then(res => {                        if (res.result?.nfcStatus == "0") {                            // 不支持                            App.getViewInstance("content").$toast("本手机不支持NFC功能！");                            return false;                        } else if (res.result?.nfcStatus == "1") {                            App.router.go(menu.url, { keepSelfalive: options?.keepSelfalive ? true : false });                        } else if (res.result?.nfcStatus == "2") {                            // 未开启-去开启                            App.jsApi.setNFC();                            return false;                        }                    });                } else {                    //如果不存在该方法，则提示去更新                    App.getViewInstance("content").$confirm({                        message: `请升级至最新版本使用本功能`,                        sureText: `去更新`,                        cancelText: "取消",                        modalId: "sessionOut",                        closed: isSure => {                            if (isSure) {                                //点击去更新，点击跳转“关于我们“                                window.$G.goToNext("#Setting/AboutUs/Index");                            }                        }                    });                    return false;                }            });        } else {            if (param && typeof param !== "string") {                let menuUrl = menu.url;                //扫码跳转                //基金，理财                if (param.tdType == "201" || param.tdType == "202") {                    param.from = "favorities";                    param.pdCd = param.PrdCode; // 产品代码                    App.router.go(menuUrl, {                        keepSelfalive: options?.keepSelfalive ? true : false,                        pageParam: { param }                    });                    //存款，大额存单                } else if (param.tdType == "206") {                    App.router.go(menuUrl, { keepSelfalive: options?.keepSelfalive ? true : false, pageParam: param });                } else {                    App.router.go(menuUrl, {                        keepSelfalive: options?.keepSelfalive ? true : false,                        pageParam: { param }                    });                }            } else {                App.router.go(menu.url, { keepSelfalive: options?.keepSelfalive ? true : false });            }        }        this.delay = false;    },    async _goCordova(menu, param) {        const { url } = menu;        const cdnParam = window.encodeURIComponent(`ehasProcessCDNServer=${ehasProcessCDNServer}/`);        const menusWithoutCDN = [            "174000",            "110040",            "135020",            "SFUD010400",            "SFUD010300",            "SFUD010100",            "SFUD010200",            "SFUD010500",            "105140",            "1050A0",            "105110",            "120200", // 三方存管            "1D8101",            "1D8090",            "1F9000",            "SFUD020100", //私募产品            "1D8010",            "1A7000",            "119010",            "1F1000"        ]; // 5.0无需接收cdn参数的菜单页面        let agreementList = await agreement.getData(); //获取协议数据        let agreementData = [];        //跳转5.0功能，根据功能码获取对应协议数据        for (let i = 0; i < agreementList.length; i++) {            if (agreementList[i].pgCd == menu.bsnCode) {                agreementData.push({                    gdCntnt: transferUrl(agreementList[i].gdCntnt),                    subPgCd: agreementList[i].subPgCd                });            }        }        agreementData = window.encodeURIComponent(`agreementData=${JSON.stringify(agreementData)}`);        // 使用url传参 使5.0菜单页面能够使用cdn地址获取6.0菜单        let page = "";        if (menusWithoutCDN.includes(url)) {            page = param || "";        } else {            if (!param) {                page = `${url}.html?${cdnParam}&${agreementData}`;            } else {                page = param.includes("?")                    ? `${param}&${cdnParam}&${agreementData}`                    : `${param}?${cdnParam}&${agreementData}`;            }        }                // 保险单独处理 810-1D8101 812-1D8090 818-1F9000 817-1D8010        if (["1D8101", "1D8090", "1F9000", "1D8010", "1A7000", "1F1000"].includes(url)) {            //校验登录            const isLogin = await dealLogin();            //保险跳转前需要校验用户等级            if (isLogin && (await checkCustomerAuthInfo())) {                // 保险page可能是对象格式参数，此时取此对象中的page值                if (page?.page) {                    page = page.page;                }                // 保险跳转-传参格式示例: {"menuId":"1F9000","page":"1F9000.html?prdCode=SR6&cpdm=9990000209#InsuranceDetailPage"}                App.jsApi.openWebPageByCordovaContainer({ menuId: url, page: page }).finally(() => {                    // 加延迟的原因是：从jsApi响应成功到真正打开新webview还有个时间差，此时间差不可控                    setTimeout(() => {                        this.delay = false;                    }, 500);                });            }        } else if(App.bank.flags?.common?.socialMenuClickConfirm&&menu.bsnCode == "MBOLD1M9000"){            // 166行，社保缴费点击时增加提示            ViewBase.prototype.$confirm({                message: `您即将进入社保缴费流程，社保缴费流程中需您提供姓名、身份证号码进行身份信息查询，是否确定继续`,                sureText: "确定",                cancelText: "取消",                closed(isSure) {                    if (isSure) {                        App.jsApi.openWebPageByCordovaContainer({ menuId: url, page: page }).finally(() => {                            // 加延迟的原因是：从jsApi响应成功到真正打开新webview还有个时间差，此时间差不可控                            setTimeout(() => {                                this.delay = false;                            }, 500);                        });                    }                }            });          }else {            App.jsApi.openWebPageByCordovaContainer({ menuId: url, page: page }).finally(() => {                // 加延迟的原因是：从jsApi响应成功到真正打开新webview还有个时间差，此时间差不可控                setTimeout(() => {                    this.delay = false;                }, 500);            });        }    },    _goJSBPage(menu, param, menuParam, options = {}) {        let jsApiOptions = {            url: menu.url,            ...options,            ...JSONParse(param),            ...JSONParse(menuParam)        };        //如果功能码为MBUSR030400，则需要传空title，显示跳转的下个页面返回箭头        if (menu.bsnCode == "MBUSR030400") {            jsApiOptions.title = " ";        }        App.jsApi.openWebPageByJSBContainer(jsApiOptions).finally(() => {            // 加延迟的原因是：从jsApi响应成功到真正打开新webview还有个时间差，此时间差不可控            setTimeout(() => {                this.delay = false;            }, 500);        });    },    _goWebPage(menu, param, options) {        let optionStr = "";        if (App.bank.flags.common?.goWebPageMenus?.includes(menu.bsnCode)) {            let tempArry = [];            //如果功能码配置了传参跳转            if (typeof options === "object" && isNaN(options.length)) {                for (let key in options) {                    tempArry.push(key + "=" + options[key]);                }            }            if (tempArry.length > 0) {                optionStr = "?" + tempArry.join("&");            }        }        const jsApiOptions = { url: menu.url + optionStr, mchid: param };        App.jsApi.openWebPageByWebContainer(jsApiOptions).finally(() => {            // 加延迟的原因是：从jsApi响应成功到真正打开新webview还有个时间差，此时间差不可控            setTimeout(() => {                this.delay = false;            }, 500);        });    },    // eslint-disable-next-line max-statements    async _openJsapi(menu) {        switch (menu.url) {            case "scanQRCode":                //扫一扫菜单打开后，清空扫一扫delay                await scanQRcode();                setTimeout(() => {                    this.delay = false;                }, 500);                break;            case "openSelectBankPage":                App.jsApi.openSelectBankPage();                break;            case "complaintPhone":                let complaintPhone = App.bank.flags?.IntelligentService?.complaintPhone;                let phoneMsg = `<p class="T16 LH24 Bd" style="text-align: center;">${complaintPhone.phoneShow}</p>`;                App.getViewInstance("content").$confirm({                    type: "t3",                    message: `<p class="T16 LH24 Bd" style="text-align: center;">您是否要拨打直接投诉电话</p>${phoneMsg}`,                    sureText: "拨打",                    cancelText: "取消",                    closed(isSure) {                        if (isSure) {                            this.app.jsApi.openNativeApp({ url: `tel://${complaintPhone.phoneNo}` }).then(res => {                                if (res.code != "0000") {                                    // 通过code和message查看系统打电话功调用失败的原因                                    window.$G.jsApiErrorMsg(res);                                }                            });                        }                    }                });                break;            case "servicePhone":                // eslint-disable-next-line no-case-declarations                const bankPhone = App.bank.flags.IntelligentService?.servicePhone;                // eslint-disable-next-line no-case-declarations                let msg = "";                if (bankPhone.length !== undefined) {                    bankPhone.forEach(item => {                        msg += `<p class="T16 LH24 Bd" style="text-align: center;">${item.phoneShow}</p>`;                    });                } else {                    msg = `<p class="T16 LH24 Bd" style="text-align: center;">${bankPhone.phoneShow}</p>`;                }                // eslint-disable-next-line no-case-declarations                let PhoneMsg = App.bank.flags.IntelligentService?.creditPhone ? "借记卡热线" : "客服电话";                App.getViewInstance("content").$confirm({                    type: "t3",                    message:                        // eslint-disable-next-line max-len                        `<p class="T16 LH24 Bd" style="text-align: center;">您是否要拨打${PhoneMsg}</p>${msg}`,                    sureText: "拨打",                    cancelText: "取消",                    closed(isSure) {                        if (isSure) {                            if (bankPhone.length !== undefined) {                                this.app.getViewInstance("content").$refs.servicePhone.show();                            } else {                                this.app.jsApi.openNativeApp({ url: `tel://${bankPhone.phoneNo}` }).then(() => {                                    // 通过code和message查看系统打电话功调用失败的原因                                });                            }                        }                    }                });                break;            case "creditPhone":                // eslint-disable-next-line no-case-declarations                const creditPhone = App.bank.flags.IntelligentService?.creditPhone;                App.getViewInstance("content").$confirm({                    type: "t3",                    message:                        // eslint-disable-next-line max-len                        '<p class="T16 LH24 Bd" style="text-align: center;">您是否要拨打信用卡热线</p><p class="T16 LH24 Bd" style="text-align: center;">' +                        creditPhone.creditPhoneShow +                        "</p>",                    sureText: "拨打",                    cancelText: "取消",                    closed(isSure) {                        if (isSure) {                            this.app.jsApi.openNativeApp({ url: `tel://${creditPhone.creditPhoneNo}` }).then(() => {                                // 通过code和message查看系统打电话功调用失败的原因                            });                        }                    }                });                break;            case "openWechatMiniProgram":                App.jsApi.openWechatMiniProgram(JSON.parse(menu.urlParam)).catch(rsp => {                    //通过code和messge可查看失败的原因                    if (rsp.code == "EPFN515T") {                        ViewBase.prototype.$alert({                            message: "检测到您没有安装微信或微信版本太低，请安装或升级至微信的最新版本",                            btnText: "确定"                        });                    } else {                        ViewBase.prototype.$toast(rsp.cvrtMsg);                    }                });                break;            default:                if (menu.url) {                    //如果视频银行的功能码存在，则跳转到对应的页面                    this.openVideo(menu);                }                break;        }    },    /**     * 远程视频银行跳转     */    openVideo(menu) {        //跳转行方自己页面列表        const selfBankList = ["openVideoBank"];        //统建视频银行接入列表（打开联盟视频银行）        const totalVideoList = ["openVideoBankBySCCBAVB"];        if (selfBankList.includes(menu.url)) {            //跳转行方自己视频页面            App.jsApi.openVideoBank();        }        //统建视频银行添加兼容处理 如果urlParam字段中sdkName有值，则优先按照该字段跳转，否则按原先逻辑跳转视频银行        let linkUrl = "";        if (menu.urlParam) {            //如果urlParam有值，则优先获取该sdk的值            linkUrl = JSON.parse(menu.urlParam)?.sdkName;        }        if (totalVideoList.includes(linkUrl) || totalVideoList.includes(menu.url)) {            //跳转统建视频银行（打开联盟视频银行）            App.jsApi.openVideoBankBySCCBAVB({ url: JSON.parse(menu.urlParam).url });        }    },    _openApplet(menu, param, options = {}) {        if (menu.bsnCode == "MBFIN290000") {            //如果是潍家缴费菜单，则判断是否为最新包提醒弹框更新            let TypeofPhone = App.jsApi.isInIOS();            //ios小于6.0.05版本，或者安卓小于6.0.0.4版本则弹框提示更新客户端            const phoneVersion = TypeofPhone ? "6.0.05" : "6.0.0.4";            const clientVersion = App.jsApi.getAppVerion();            //判断当前客户端版本是否小于6.0.0.0版本            const booleanAlert = toNumVersion(clientVersion) < toNumVersion(phoneVersion);            if (booleanAlert) {                clientVersionCom(phoneVersion);                return;            }        }        //查看版本是否需要更新客户端包        if (menu.bsnCode == "MBCMM030000") {            //如果是视频银行，则判断是否为最新包提醒弹框更新            let TypeofPhone = App.jsApi.isInIOS();            //ios小于6.1.8版本，或者安卓小于6.1.7.7版本则弹框提示更新客户端            const phoneVersion = TypeofPhone ? "6.1.08" : "6.1.7.8";            const clientVersion = App.jsApi.getAppVerion();            //判断当前客户端版本是否小于6.0.0.0版本            const booleanAlert = toNumVersion(clientVersion) < toNumVersion(phoneVersion);            if (booleanAlert) {                clientVersionCom(phoneVersion);                return;            }        }        // 软uk视频认证方案        if (param?.businessType == "V420") {            if (menu.url.indexOf("businessType") < 0) {                menu.url = menu.url + "?businessType=V420";            }        } else {            menu.url = menu.url.replace("?businessType=V420", "");        }        if (typeof param === "string") {            param = JSON.parse(param);        }        if (!param?.title && (options?.title || menu?.bsnName)) {            options.title = options.title || menu.bsnName;        }        const jsApiOptions = { url: menu.url, ...param, ...options };        App.jsApi.openApplet(jsApiOptions).catch(rsp => {            //通过code和messge可查看调用失败的原因            if (rsp.code == "EPFJ002W") {                ViewBase.prototype.$alert({                    message: "该版本手机银行不支持此功能，请升级最新版本手机银行",                    btnText: "确定"                });            } else {                ViewBase.prototype.$toast(rsp.cvrtMsg);            }        });    },    _formatData(json) {        let data = [];        for (const key in json) {            let item = json[key];            item = {                ...item,                replacedUrl: item.url.replace(/#/, ""),                param: item.urlParam            };            data.push(item);            json[key] = item;        }        return data;    },    // 转化菜单列表数据 为 menu-panel组件可使用的格式    transMenu(menu) {        return {            id: menu.bsnCode,            text: menu.bsnName,            src: menu.icon ? transferUrl(menu.icon) : "",            rightFlag: menu.rightFlag ? transferUrl(menu.rightFlag) : "",            markLocation: menu.markLocation        };    },    // 根据栏位ID 获取当前菜单内对应栏位的菜单列表数据    async fetchMenuList(anchorCode, slientFlag) {        // 获取菜单列表数据        try {            if (WapWeChatUtil.isWXBusiness()) {                throw new Error("806wap微信业务走下面");            }            const userInfo = (await getLocalUserInfo()) || {};            const { branchNo = "" } = userInfo;            const reqData = {                data: {                    body: { anchorCode, branchNo },                    header: {}                },                slient: slientFlag,                useDefaultError: false            };            const rspData = await App.requester.request("cms/MC0015.do", reqData);            const menuList = (rspData.body && rspData.body.menus) || [];            return menuList.map(menu => this.transMenu(menu));        } catch (error) {            // 获取异常时拉取兜底数据            try {                const { bankId } = App.bank.flags.common;                const fetchUrl = `${ehasProcessCDNServer}/public/${bankId}/ecms/menus/A03/${anchorCode}.json`;                const safeRspData = await this.getJsonFun(fetchUrl);                const safeRspMenuList = (safeRspData && safeRspData.data && safeRspData.data.menus) || [];                return safeRspMenuList.map(menu => this.transMenu(menu));            } catch (error) {                globalUtil.popErrorMsg(error);                return [];            }        }    },    /**     * 菜单权限校验     */    async permissionCheck({ routerUrl, menuInfo } = {}) {        // 如果菜单是非正常状态，弹出提示信息        if (isMenuDisabled(menuInfo)) {            return false;        }        // 810我的页签视频银行控制ios6.0.0点击报敬请期待，其他版本能正常使用        if (remoteBankMenuDisabled(menuInfo.bsnCode)) {            return false;        }        // 菜单权限配置为需要登录时        if (menuInfo.loginFlag === "1") {            // 用户未登录需要跳转登录            const hasLogined = await checkIsLogin();            Log.info(routerUrl);            if (!hasLogined) {                goToLogin(routerUrl);                return false;            }            // 用户已登陆则需要判断是否允许访问该菜单            if (menuInfo.customerAuth) {                try {                    return this.customerAuthCheck({ menu: menuInfo });                } catch (error) {                    return false;                }            }        }        return true;    },    /**     * 需要登录的菜单判断用户是否有权限访问该菜单     * @param  {Object}  menu 菜单码对象     * @param  {Boolean} closeProgressBar 是否关闭读条     */    async customerAuthCheck({ menu } = {}) {        if (WapWeChatUtil.isWXBusiness()) return true;        if (menu?.loginFlag == "1" || menu?.loginFlag == "2") {            const userLevel = {};            const userInfo = await App.local.get("m_user_info");            const localUserInfo = await getLocalUserInfo();            let mCardTypeName = App.bank.flags?.account?.mCardTypeNameFlag ? "信用卡" : "公务卡";            let level = userLevel?.result?.m_userLevel || localUserInfo?.userLevel;            if (menu.customerAuth == "C" && userInfo?.hasTkgDwnCcrdFlg == "N") {                ViewBase.prototype.$confirm({                    message: `该功能需要添加${getBankName() + mCardTypeName}后使用`,                    sureText: "立即添加",                    cancelText: "取消",                    closed(isSure) {                        if (isSure) {                            window.$G.goToNext("#Account/MyAccount/MyAcctList");                        } else {                            window.$G.backMktplatWeb();                        }                    }                });                return false;            } else if (parseInt(level) < parseInt(menu.customerAuth)) {                const authInfo = getAuthInfo(menu);                ViewBase.prototype.$confirm({                    message: authInfo.title,                    sureText: authInfo.sureBtnTxt,                    cancelText: authInfo.cancelBtnTxt,                    closed(isSure) {                        if (isSure) {                            if (authInfo.sureBtnUrl != "") {                                window.$G.goToNext(authInfo.sureBtnUrl);                            }                        } else {                            // 营销活动处理                            window.$G.backMktplatWeb();                        }                    }                });                return false;            } else {                return true;            }        }        // 不需要登录的菜单返回true        return true;    },    /**     * 搜索功能跳转宝贝卡模块进行绑卡判断进行拦截     * @param  {Object}  routerInfo 路由信息     */    async childIntercept(routerInfo = {}) {        if (            routerInfo.newFragment.moduleName === "ChildCard" &&            (routerInfo.newFragmentUrl === "ChildCard/Task/Task" ||                routerInfo.newFragmentUrl === "ChildCard/Finance/Hold" ||                routerInfo.newFragmentUrl === "ChildCard/Index/Manage" ||                routerInfo.newFragmentUrl === "ChildCard/LuckyMoney/LuckyMoney")        ) {            //获取宝贝卡绑卡列表            let { babyCardBindQryEntityList: list } = await App.requester.request("dep/babycard/babycardbindqry.biz");            if (!list.length) {                App.getViewInstance("content").$confirm({                    message: `您当前无绑定的宝贝卡，请先前往申请或绑定宝贝卡。`,                    cancelText: `知道了`,                    sureText: `去绑定`,                    closed: isSure => {                        if (isSure) {                            //去绑定                            window.$G.goToNext("#ChildCard/Binding/Binding", { backRouter: location.hash });                        }                    }                });                return false;            }        }        return true;    },    /**     * 获取标准版菜单对应的简版菜单     *  1 标准版 或 无对应简版菜单 返回空字符串     *  2 简版 且 有对应简版菜单 返回简版菜单     * @param {*} id 菜单     */    async exchangeSimpleId(id) {        // 非简版时返回空字符串        if (App.globalFlag.simpleFlag !== "Y") return "";        // 简版菜单        const simpleId = simpleMenuIdDc[id];        // 有对应简版菜单时，返回简版菜单        if (simpleId && (await this.findById(simpleId))) {            return simpleId;        }        // 无简版菜单时返回空字符串        return "";    }};