import { ViewBase } from "@ehas/core";
import { WapWeChatUtil } from "~/common/js/wap";
import { globalUtil } from "~/common/js/globalUtil";

export default {
    beforeStart: function() {
        ViewBase.mixin({
            created() {
                //wap微信页面须先单独处理一下
                if (WapWeChatUtil.isWXBusiness() && WapWeChatUtil.getRouterParam()) {
                    this.pageParam = WapWeChatUtil.getRouterParam();
                }

                if (this.pageParam && this.pageParam.param) {
                    for (let k in this.pageParam.param) {
                        this.pageParam[k] = this.pageParam.param[k];
                    }
                } else if (Object.keys(this.pageParam).length > 0 && !this.pageParam.param) {
                    //this.pageParam有值，并且param不存在
                    // 路由传参接受方式兼容
                    // 将pageParam的参数赋值给pageParam.param
                    this.pageParam.param = {};
                    for (let k in this.pageParam) {
                        if (k !== "param") {
                            this.pageParam.param[k] = this.pageParam[k];
                        }
                    }
                }
            }
        });
    }
};
