import jsApi from "@ehas/app-jsapi";import { pictureJump } from "~/common/utils/ad";import { onPause as loginOnPause, onResume as loginOnResume } from "~/common/utils/login.js";import { getUrlParams } from "~/common/utils/https.js";import { onPageResume, CopyTextCheck, getClipboardText, setWai } from "~/common/utils/device";import { getPopupWindow } from "~/common/utils/dialog";import { globalUtil } from "~/common/js/globalUtil";import { transformPushMessage, pullMessage, messageJump, setMessageAsRead } from "~/common/js/message";import menu from "~/common/datas/menu";import { ViewBase } from "@ehas/core";import agreement from "~/common/datas/agreement";import warmTip from "~/common/datas/warmTip";// 工具函数import { LoanUtil } from "~/common/js/loanUtil";import { toFeedBack } from "~/common/utils/feebBack.js";import { isNewStartup, isHomeView } from "~/common/utils/check";import { CustomEvent } from "~/common/utils/customEvent.js";import { versionSwitch } from "~/common/utils/versionSwitch";/* eslint-disable */let landScapeShake = "";// 更新运营位、协议和温馨提示async function updateTipsAndAgreements(result) {    const type = result.t;    if (type === "o3") agreement.getData(true); // 更新协议    if (type === "o2") warmTip.getData(true); // 更新温馨提示    // 更新运营位    if (type === "o1") {        try {            // const userInfo = await App.local.get("m_user_info");            // const { signPhoneNo } = userInfo || {};            const signPhoneNo = await App.local.get("loginUserNm");            const adInfo = await App.requester.request("cmm/opcl/opclmanage.biz", {                data: { signPhoneNo: signPhoneNo }            });            App.jsApi.saveAdInfoToStorage({ adInfo: adInfo.hashMap, intrvDays: adInfo.intrvDays });        } catch (error) {            window.$G.logErrorWithCode("src/config/jsapiSetting.js更新运营位异常", JSON.stringify(error));        }    }}/** * 判断当前客户端是否支持原生下拉刷新功能 */async function checkNativePulldown() {    try {        const jsApiList = ["enablePulldownRefresh", "disablePulldownRefresh", "closePullRefresh"];        const rsp = await App.jsApi.jsapiExistInApp({ jsApiList });        return jsApiList.every(api => rsp.result[api]);    } catch (error) {        globalUtil.logWarnWithCode("src/config/jsapiSetting.js检测客户端是否支持下拉刷新", "当前客户端不支持下拉刷新功能");        return false;    }}// APP唤起打开的url进行白名单校验，防止钓鱼网站被打开function urlWhiteListCheck(skpLinkAdr, sendType = "") {    return App.requester.request("wlh/share/shareurlcheckqry.biz", { data: { skpLinkAdr, sendType } });}// 根据uuid换取url分享链接function getShrLinkAdr(uuid) {    return App.requester.request("wlh/share/shrlinkadrqry.biz", {        data: {            uuid: uuid        }    });}//主webview 关掉Appfunction exitApp() {    ViewBase.prototype.$confirm({        message: "是否退出当前程序",        sureText: "确定",        cancelText: "取消",        closed(isSure) {            if (isSure) {                App.jsApi.exitApp();            }        }    });}async function initSafeKeyboardShock() {    const rootFlag = await App.jsApi.isRootView(); // 判断是否为主webview    if (rootFlag && App.bank.flags?.setting?.isShowSafeKeyboardShock) {        const isFirstStart = await App.jsApi.getNativeInfo();        if (isFirstStart.result?.nativeInfo?.clientInfo?.isfirst == "Y") {            App.jsApi.jsapiExistInApp({ jsApiList: ["setSafeKeyboardShock"] }).then(res => {                if (res.result?.setSafeKeyboardShock) {                    App.jsApi.setSafeKeyboardShock({ isShock: true });                }                App.requester.request("cmm/keyboard/keyboardstschg.biz", {                    data: {                        type: "1"                    },                    slient: true //不加等待层                });            });        }    }}/** * 新客户端摇一摇弹窗跳转页面 * @param {*} data */function landScapeOnPageResume(data) {    if (isNewStartup() && App.jsApi.isRootView()) {        landScapeShake = false;        const ee = new CustomEvent();        ee.emit("customEventPageResume", data);        if (data?.result?.components == "shake") {            if (data.result.name == "set") {                window.App.menu.go("MBUSR020000");            } else if (data.result.name == "clickjump") {                const item = data.result.data;                if (item.id) {                    if (item.id == "MBECM030000") {                        window.$G.getMenuId().then(MenuId => {                            if (MenuId == "MBECM030000") {                                App.jsApi.changeTabbar({ bsnCode: "MBECM030000" });                            } else {                                window.App.menu.go(item.id);                            }                        });                    } else {                        window.App.menu.go(item.id);                    }                } else {                    versionSwitch();                }            }        }    }}let onResume = false;let onTabbarChangeFlag = false;export default {    beforeStart: async function(App) {        App.mbank = {};        App.jsApi = jsApi;        // 返回监听        App.jsApi.back(() => {            const dialog = document.querySelectorAll("div[dialog-mark]"); //获取弹框元素            if (dialog.length >= 1) {                //有弹框时禁止物理返回键                return;            }            // 求助组件展示截屏时禁止物理返回            const isScreenFlag = App.getViewInstance("floatHelp") || {};            if (Object.keys(isScreenFlag)?.length != 0 && !isScreenFlag.isScreen) {                return;            }            // 处理二楼            const subPage = App.getViewInstance("subPage") || {};            if (subPage.active && typeof subPage.onBack === "function") {                subPage.onBack();                return;            }            // 当前页面如果有协议预览弹出层，在物理返回时只关闭预览层            const contentView = App.getViewInstance("content") || {};            const { pdfInstance } = contentView;            if (pdfInstance) {                const instanceKeys = Object.keys(pdfInstance).filter(key => pdfInstance[key].visible);                if (instanceKeys.length) return instanceKeys.forEach(key => pdfInstance[key].hide());            }            const { historyList = [] } = App.router || {};            const rootFlag = App.jsApi.isRootView(); // 判断是否为主webview            const isAndroid = App.jsApi.isInAndroid();            if (historyList.length >= 1) {                const navBar = App.getViewInstance("navBar") || {};                /**                 * 路由历史不为空的返回场景：                 * 1.如果页面有标题栏，则触发返回按钮                 * 2.如果没有标题栏且当前页面为简版首页时，如果为主webview则退出App，否则关闭当前webview                 * 3.否则返回上个路由记录                 */                if (navBar.iconLeft) {                    navBar.iconLeft();                } else if (                    (App.globalFlag.simpleFlag === "Y" && App.router.fragmentUrl === "SimpleHome/Home/Index") ||                    App.router.fragmentUrl === "Huinong/Home/Index"                ) {                    //如果是在简版首页或者惠农版首页，则弹出是否退出挡墙程序                    if (rootFlag) {                        isAndroid && exitApp();                    } else {                        App.jsApi.closeWebContainer();                    }                } else {                    // 新首页，频道页切换第一次加载，页面被存到路由历史内，导致点击返回，回到了首页没有提示退出程序                    if (isNewStartup() && rootFlag) {                        isAndroid && exitApp();                    } else {                        App.router.back();                    }                }            } else {                /**                 * 路由历史为空的返回场景：                 * 1.如果主webview则退出App                 * 2.否则关闭当前webview                 */                if (rootFlag) {                    isAndroid && exitApp();                } else {                    App.jsApi.closeWebContainer();                }            }        });        App.jsApi.on("onAdClick", function(data) {            // 用户点击了启动广告            const { advgNo, advgTtl } = data.result;            App.DC.onEvent("OPCL0403", {                OPCL_ADVG_NO: advgNo,                OPCL_ADVG_TTL: advgTtl            });            App.mbank.adFlag = true;            pictureJump(data.result);        });        // app切换后台事件        App.jsApi.on("onPause", function() {            loginOnPause();        });        // app唤起事件        App.jsApi.on("onResume", data => {            onResume = true;            setTimeout(() => {                onResume = false;            });            loginOnResume();            CopyTextCheck();            getClipboardText();            // 800P_wangenzh：按照目前统一格式添加“router:”，方便之后统一替换            App?.region?.triggerGlobalEvent("router:onResume", data);        });        //监听页面重新可见        App.jsApi.on("onPageResume", data => {            // 新客户端摇一摇弹窗跳转页面            landScapeOnPageResume(data);            if (App.jsApi.isInAndroid() && onResume) {                // 安卓切换app返回应用不但会触发onResume，还会触发onPageResume                // zhangguangqian没空处理                onResume = false;                return;            }            // 无障碍            setWai();            if (data?.result?.loginStatus) {                // 新客户端，登录关闭，缓存参数传给预加载页面，防止预加载关闭，接收不到参数                App.globalFlag.closeLoginStateDate = data.result;            }            if (data?.result?.themeId) {                // 新客户端，设置主题后，频道页为单独webview,需重新刷新                if (App.jsApi.isInApp()) {                    App.theme.setUserThemeId(data.result.themeId);                }            }            if (onTabbarChangeFlag) {                // 频道页tab切换触发onTabbarChange事件后，不在执行onPageResume                onTabbarChangeFlag = false;                return;            }            if (data?.result?.preloadWebpageClose) {                // 新客户端预加载页面关闭，通知到老客户端web页面切换“router:after”事件，不执行onPageResume                App?.region?.triggerGlobalEvent("router:after", data);                App?.region?.triggerGlobalEvent("router:onPageResume", data);                return;            }            // 公共事件处理            onPageResume(data);            // 供业务模块自己调用            App?.region?.triggerGlobalEvent("router:onPageResume", data);        });        //监听语音返回事件        App.jsApi.on("onVoiceRecordResult", data => {            App?.region?.triggerGlobalEvent("router:onVoiceRecordResult", data);        });        //监听语音返回分贝事件        App.jsApi.on("onVoiceDecibelChanged", data => {            App?.region?.triggerGlobalEvent("router:onVoiceDecibelChanged", data);        });        //监听语音录音机是否调用成功        App.jsApi.on("onVoiceRecordStart", data => {            App?.region?.triggerGlobalEvent("router:onVoiceRecordStart", data);        });        //监听截屏事件        App.jsApi.on("onScreenShot", data => {            window.$G.goToNext("#Setting/Setting/Feedback", data.result);        });        // 视频银行        App.jsApi.on("onVideoBankHangup", data => {            App?.region?.triggerGlobalEvent("router:onVideoBankHangup", data);        });        //监听设置防截屏事件        App.jsApi.on("onForbidSrceenShot", data => {            //注册全局事件            App?.region?.triggerGlobalEvent("onScreenEvent", data);            //ios录屏、截屏提示            if (App.jsApi.isInIOS()) {                //type 1是截屏 2是录屏                let type = data.type || data.result.type; //兼容客户端两套数据格式                let screenType = type == "1" ? "截屏" : "录屏";                ViewBase.prototype.$toast(                    `检测到${screenType}，${screenType}中可能存在您的个人信息，为了您的资金安全，请不要将${screenType}分享给陌生人`                );            }        });        // 分享唤起        App.jsApi.on("onScheme", async data => {            //长按扫一扫、收支明细、转账、财户总览            if (data.result?.param == "EMAS_CLIENT_SHORTCUT") {                // 通过功能码跳转                App.menu.go(data.result.uri);            } else {                // 唤起手机App                App.mbank.schemeFlag = true;                let param = data.result.param;                let sharedparam = data.result.sharedparam;                let shrJumpTyp; // uuid对应的跳转方式，只有7（小程序）的时候有值                let shrLinkAdr; // uuid对应的跳转存储链接                let ifUuid = false; // 控制是否为uuid跳转过来                let uri = data.result.uri; // 通过uri传递的功能码、页面链接                let expdParamCntnt = {};                try {                    expdParamCntnt = JSON.parse(decodeURIComponent(sharedparam))?.expdParamCntnt;                } catch (err) {                    window.$G.logErrorWithCode("src/config/jsapiSetting.js唤起APP解析异常：", sharedparam);                    expdParamCntnt = "";                }                // 通过uuid换取url分享链接                if (expdParamCntnt.uuid) {                    let ret = await getShrLinkAdr(expdParamCntnt.uuid);                    shrLinkAdr = ret.shrLinkAdr;                    shrJumpTyp = ret.jumpTyp;                    ifUuid = true;                    sharedparam = getUrlParams(shrLinkAdr.split("#")[0], "shareparam");                    try {                        sharedparam = decodeURIComponent(sharedparam);                        expdParamCntnt = JSON.parse(sharedparam)?.expdParamCntnt;                    } catch (err) {                        window.$G.logErrorWithCode("src/config/jsapiSetting.js唤起APP解析异常：", sharedparam);                        expdParamCntnt = "";                    }                }                // 三方唤起只传uri、param，将param参数添加到expdParamCntnt对象上                if (uri && uri != "undefined" && param && !sharedparam) {                    try {                        expdParamCntnt = Object.assign({}, expdParamCntnt, JSON.parse(param));                    } catch (err) {                        window.$G.logErrorWithCode(                            "src/config/jsapiSetting.js三方唤起只传uri、param，将param参数添加到expdParamCntnt对象上,param格式异常：",                            param                        );                    }                }                // 将通过uri传过来的功能码或链接统一添加到expdParamCntnt对象上                if (uri && uri != "undefined") {                    expdParamCntnt.pgNo = uri;                }                const jumpTyp = expdParamCntnt ? expdParamCntnt.jumpTyp : ""; // 跳转方式                const paramType = expdParamCntnt ? expdParamCntnt.paramType : ""; // 传参方式 1 路由传参， 2url 传参                let pgNo = expdParamCntnt ? expdParamCntnt.pgNo : ""; // 页面功能码或三方链接                let URI = expdParamCntnt ? expdParamCntnt.URI : ""; // 金融小店贷款使用：expdParamCntnt.URI = "loan"                // APP6.0 传参跳转                if (Number(jumpTyp) === 3) {                    // 简爱版禁止跳转基金页面可以跳转理财                    if (App.globalFlag.simpleFlag === "Y" && pgNo === "MBFUN050100") {                        return;                    }                    // APP6.0内部跳转路由传参                    if (Number(paramType) === 1) {                        // 通过expdParamCntnt.value传参，JSON格式字符串                        if (expdParamCntnt.value) {                            try {                                menu.findById(pgNo).then(rsp => {                                    window.$G.goToNext(rsp.url, JSON.parse(expdParamCntnt.value));                                });                            } catch (err) {                                window.$G.logErrorWithCode(                                    "src/config/jsapiSetting.js通过expdParamCntnt.value传参，JSON格式字符串expdParamCntnt.value异常：",                                    expdParamCntnt?.value                                );                            }                        } else {                            // 基金、理财分享                            menu.findById(pgNo).then(rsp => {                                window.$G.goToNext(rsp.url, { ...expdParamCntnt });                            });                        }                    }                    // APP6.0内部跳转，不需要传参                    else if (Number(paramType) === 2) {                        menu.findById(pgNo).then(rsp => {                            window.$G.goToNext(rsp.url);                        });                    } else {                        // 通过功能码跳转                        menu.go(pgNo);                    }                }                // cordava跳转                if (Number(jumpTyp) === 4) {                    menu.go(pgNo, decodeURIComponent(expdParamCntnt.paramString));                }                // 跳转贷款三方H5链接                if (URI && URI.indexOf("loan") !== -1) {                    let item = expdParamCntnt ? expdParamCntnt : {};                    if (item.jumpTyp !== "4") {                        let { inqRslt } = await urlWhiteListCheck(item.pgNo, "loan");                        if (inqRslt === "N") {                            return;                        }                    }                    LoanUtil.gotoLoanProduct(item);                }                // 5.0容器方式打开                if (Number(jumpTyp) === 5) {                    let { inqRslt } = await urlWhiteListCheck(pgNo, "");                    if (inqRslt === "N") {                        return;                    }                    App.jsApi.openWebPageByJSBContainer({ url: pgNo, title: expdParamCntnt?.title || "" });                }                // 超级APP                if (Number(jumpTyp) === 6) {                    let { inqRslt } = await urlWhiteListCheck(pgNo, "");                    if (inqRslt === "N") {                        return;                    }                    App.jsApi.openWebPageByWebContainer({ url: pgNo });                }                // 小程序uuid形式                if (shrJumpTyp && Number(shrJumpTyp) === 7 && ifUuid) {                    let appId = getUrlParams(shrLinkAdr, "appId");                    let isHiddenNavigationBar = getUrlParams(shrLinkAdr, "isHiddenNavigationBar");                    let { inqRslt } = await urlWhiteListCheck(pgNo, "");                    if (inqRslt === "N") {                        return;                    }                    menu.go(                        { url: pgNo, jumpType: shrJumpTyp },                        { appId: appId, isHiddenNavigationBar: isHiddenNavigationBar }                    );                }                // 小程序非uuid形式                if (Number(jumpTyp) === 7 && !ifUuid) {                    let appId = getUrlParams(pgNo, "appId");                    let isHiddenNavigationBar = getUrlParams(pgNo, "isHiddenNavigationBar");                    menu.go(                        { url: pgNo, jumpType: jumpTyp },                        { appId: appId, isHiddenNavigationBar: isHiddenNavigationBar }                    );                }                // 分享数据上送到服务端                if (sharedparam) {                    App.jsApi.writeToMemoryStorage({ key: "sharedparam", value: sharedparam });                }            }        });        // 【客户端在线】客户端在前台时 回调给web信息 数据位透传的所有内容，通知栏由h5调用插件实现        App.jsApi.on("onPushReceive", data => {            // 判断是否为根目录            const rootFlag = App.jsApi.isRootView();            // 非新首页仅根目录调用，新首页仅在首页调用            if ((!isNewStartup() && rootFlag) || (isNewStartup() && isHomeView())) {                const { result = {} } = data;                const channel = result.ch;                // 指定渠道的推送(针对刷新的推送)                if (channel) return updateTipsAndAgreements(result);                // 处理推送展示                transformPushMessage(result);            }        });        //【客户端离线】点击通知栏 客户端主动给web回到  数据为透传的所有内容        App.jsApi.on("onNotifyClick", async data => {            // 判断是否为根目录            const rootFlag = App.jsApi.isRootView();            if ((!isNewStartup() && rootFlag) || (isNewStartup() && isHomeView())) {                //每次进入点击通知栏消息均重新拉取数据                await pullMessage(true);                // 标为已读                const taskId = JSON.parse(data.result.busData).taskId;                //将data.result数据打印出来                await setMessageAsRead(taskId, data.result);                // 是,处理点击跳转                messageJump(data.result);            }        });        //无网事件监听        App.jsApi.on("onNetworkStatus", data => {            // 没有网络            App.storage.remove("onNetworkStatus", data.result.networkStatus);            App.storage.set("onNetworkStatus", data.result.networkStatus);            if (!data.result.networkStatus) {                let pageUrl = ["FloorHome", "SimpleHome", "Wealth"];                // 判断是否为频道页, 或财富总览页                if (pageUrl.indexOf(App.router.fragment.moduleName) == -1) {                    App.jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });                } else {                    App?.region?.triggerGlobalEvent("router:onNetworkStatus", data);                }            } else {                App?.region?.triggerGlobalEvent("router:onNetworkStatus", data);            }        });        // 【移动威胁感知】客户端态势感知SDK集成        App.jsApi.on("onDXRiskNotification", data => {            const action = data.result.action;            if (action == "logout") {                App.jsApi.exitApp();            }        });
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 
        // 监听摇一摇        let shake;        App.jsApi.on("onDeviceShaking", async function() {            const shakeCheck = await App.local.get("shakeCheck");            const osVer = App.jsApi.getOsVer();            const isAndroid = App.jsApi.isInAndroid();            // 7.1.1 安卓系统默认关闭摇一摇            if (isAndroid && osVer === "7.1.1" && shakeCheck !== "1") {                return;            } else if (shakeCheck === "0") {                // 用户主动关闭摇一摇                return;            }            let length = getPopupWindow();            const { moduleName } = App.router?.fragment;            if (!shake && length < 1 && moduleName && moduleName != "Login") {                const rootFlag = await App.jsApi.isRootView();                // 判断是否为主webview                if (isNewStartup() && rootFlag && App.globalFlag.simpleFlag != "Y" && App.globalFlag.huinong != "Y") {                    if (!landScapeShake) {                        landScapeShake = true;                        const MenuId = await window.$G.getMenuId();                        await App.jsApi.openWebpageByPreload({                            url:                            "#LandScape/Index/Index?param=" +                            encodeURIComponent(                                JSON.stringify({                                    param: {                                        type: "04"                                    }                                })                            ),                            type: "2",                            bsnCode: MenuId                        });                    }                } else {                    if (typeof ViewBase.prototype?.$shake === "function") {                        shake = ViewBase.prototype?.$shake({                            closed: () => {                                shake = "";                            }                        });                    }                }            }        });        //监听蓝牙key签名中间状态        App.jsApi.on("onUkeyLoading", data => {            App?.region?.triggerGlobalEvent("router:onUkeyLoading", data);        });        //监听客户端传的菜单码并跳转菜单        App.jsApi.on("onUnionpayBankBusiness", rsp => {            if (rsp?.result?.code) {                menu.go(rsp.result.code);            }        });        // 新客户端意见反馈        App.jsApi.on("onFeedback", rsp => {            const imageBase64String = rsp.result.imageBase64String;            const imagePath = rsp.result.imagePath;            toFeedBack(imagePath, imageBase64String, () => {});        });        // 客户端点击断网顶部按钮，通知h5，跳转到网络监测页面        App.jsApi.on("onJumpNetworkConfig", rsp => {            App.jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });        });        // 新客户端频道页tab切换通知H5        App.jsApi.on("onTabbarChange", async data => {            /**             * 新客户端 切换主题后再进入其他的已访问过的频道页，引入样式的link未变化，需要再次主动判断一下             */            if (isNewStartup() && App.jsApi.isInApp()) {                //缓存的主题id                const localThemeId = (await App.local.get("m_theme_id")) || "";                //当前主题样式的link元素(无则为默认主题)                const linkEl = document.querySelector("#floor-theme-style");                //至少有一方有主题 且 二者主题不相同                if ((localThemeId || linkEl) && !linkEl?.classList?.contains(localThemeId)) {                    //设置缓存的主题                    App.theme.setUserThemeId(localThemeId || "0");                }            }            onTabbarChangeFlag = true;            // 新客户端tab切换，通知到老客户端web页面切换“router:after”、"router:onPageResume"事件，保证新老客户端tab切换触发事件一致            App?.region?.triggerGlobalEvent("router:after", data);            App?.region?.triggerGlobalEvent("router:onPageResume", data);        });        // 新客户端适老化，选中客户端，取消H5的框选        App.jsApi.on("onPlaySuitable", rsp => {            window.$wai?.Reset();        });        // 新客户端点击浮窗运营位        App.jsApi.on("onFloatWindowClick", rsp => {            const { type, params } = rsp.result;            // 浮窗运营位埋点            // 0 点击叉号 1 点击图片            if (type == 1) {                if (params.opclId) {                    App.DC.onEvent(params.opclId, {                        OPCL_ADVG_NO: params.advgNo || "",                        OPCL_ADVG_TTL: params.advgTtl || ""                    });                }                pictureJump(JSON.parse(params));            }        });    },    afterStart: async function(App) {        App.jsApi = jsApi;        const enableLogOutputFlag = !ehasProcessIsProduct;        if (App.router && App.router.fragment) {            App.jsApi.initJsapi({                enableLogOutput: enableLogOutputFlag,                beforeExec: function(param) {                    param.serveName = App.router.fragment.moduleName;                    param.currentPageRouter = App.router.fragmentUrl;                    return param;                }            });        }        // 检测当前客户端是否支持原生下拉刷新        globalUtil.hasNativePulldown = await checkNativePulldown();        // 开启键盘震动        initSafeKeyboardShock();    }};