export default {
    requestPromise: null,
    cacheData: null,
    async getData(cache) {
        if (cache && this.cacheData) {
            return this.cacheData;
        }
        let data = await this.requestData();
        return data;
    },

    // 请求更新数据
    requestData(){
        if (!this.requestPromise) {
            this.requestPromise = new Promise(resolve => {
                App.requester.request("cmm/msgsgn/msgsgnqry.biz", {
                    data: {},
                    slient: true,
                    cancelWhenRouting: false
                }).then(data => {
                    this.cacheData = data;
                    this.requestPromise = null;
                    resolve(data)
                }).catch(() => {
                    this.requestPromise = null;
                    resolve({})
                })
            })
        }
        
        return this.requestPromise;
    }
}