/**
 * 格式化手机号中间四位为尾号
 */
function formatMobileText(text) {
    let groups = text.match(/(?<head>\d{3})\s?\*{4}\s?(?<tail>\d{4})/);
    if (!groups) return; // 未匹配到手机号，直接退出
    let head = groups[1]; // 首号
    let tail = groups[2]; // 尾号
    let textArr = text.split(groups[0]); // 根据要替换的字符串做分割，最后再用替换后的字符串合并join
    let mobileText = "";
    // 拼接手机号字符串
    if (head && tail) {
        mobileText = head + "尾号" + tail;
        mobileText = formatMessageText(mobileText);
    }
    return textArr.join(mobileText);
}
/**
 * 格式化账号中间6位为尾号
 */
function formatAccountText(text) {
    let groups = text.match(/(?<head>\d{4,6})\s?[\*\s]{6,12}\s?(?<tail>\d{4})/);
    if (!groups) return; // 未匹配到账号，直接退出
    let head = groups[1]; // 首号6位
    let tail = groups[2]; // 尾号4位
    let textArr = text.split(groups[0]); // 根据要替换的字符串做分割，最后再用替换后的字符串合并join
    let accountText = "";
    // 拼接手机号字符串
    if (head && tail) {
        accountText = head + "尾号" + tail;
        accountText = formatMessageText(accountText);
    }
    return textArr.join(accountText);
}
/**
 * 将座机转义成报文读取
 */
function formatLandline(text) {
    let groups = text.match(/(?<head>\d{3})-(?<middle>\d{2})-(?<tail>\d{5})/);
    if (!groups) return;
    let head = groups[1];
    let middle = groups[2];
    let tail = groups[3];
    let textArr = text.split(groups[0]); // 根据要替换的字符串做分割，最后再用替换后的字符串合并join
    let tel = "";
    if (head && tail) {
        tel = head + "。" + middle + "。" + tail;
        tel = formatMessageText(tel);
    }
    return textArr.join(tel);
}
/**
 * 将数字转义成报文朗读
 */
function formatMessageText(str) {
    return str
        .replace(/0/g, "零")
        .replace(/1/g, "一")
        .replace(/2/g, "二")
        .replace(/3/g, "三")
        .replace(/4/g, "四")
        .replace(/5/g, "五")
        .replace(/6/g, "六")
        .replace(/7/g, "七")
        .replace(/8/g, "八")
        .replace(/9/g, "九");
}

/**
 * 格式化日期年月、月日
 * @param {*} text
 */
function formatDateText(text) {
    // 年月日
    let groups = text.match(/(?<year>\d{4})[.-](?<month>\d{1,2})[.-](?<day>\d{1,2})/);
    let dateText = "";
    if (groups) {
        const { year, month, day } = groups["groups"];
        dateText = `${year}年${month}月${day}日`;
    }
    if (!groups) {
        // 年月
        groups = text.match(/(?<year>\d{4})[.-](?<month>\d{1,2})/);
        if (groups) {
            const { year, month } = groups["groups"];
            dateText = `${year}年${month}月`;
        }
    }
    if (!groups) {
        // 月日
        groups = text.match(/(?<month>\d{1,2})[.-](?<day>\d{1,2})/);
        if (groups) {
            const { month, day } = groups["groups"];
            dateText = `${month}月${day}日`;
        }
    }
    if (groups) {
        let textArr = text.split(groups[0]); // 根据要替换的字符串做分割，最后再用替换后的字符串合并join
        text = textArr.join(dateText);
        return formatDateText(text); // 递归处理
    }
    return text;
}
/**
 * 格式化利率，兼容ios带百分号小数点后超过三位不读取
 */
function formatRateText(text) {
    let groups = text.match(/(?<rate>-?\d+\.\d+)%/);
    if (groups) {
        let rateArr = groups[1].split(".");
        let textArr = text.split(groups[0]); // 根据要替换的字符串做分割，最后再用替换后的字符串合并join
        let rateText = "。百分之" + rateArr[0] + "点" + formatMessageText(rateArr[1]);
        return formatRateText(textArr.join(rateText));
    }
    // replace(/^。/, "") ios 句号开头会读出来
    return text
        .replace(/~。/g, "到")
        .replace(/～。/g, "到")
        .replace(/到。百分之/g, "到百分之")
        .replace(/^。+/, "");
}

/**
 * 过滤特殊字符
 */
function filterSpecialCharacters(str) {
    function _transformSlash(_str) {
        // 将中文间的斜杠“/”替换成句号“。”
        let groups = _str.match(/[\u4e00-\u9fa5](?<slash>\/)[\u4e00-\u9fa5]/);
        if (groups) {
            _str = _str.replace("/", "。");
            return _transformSlash(_str);
        }
        return _str;
    }
    str = _transformSlash(str);
    return str
        .replace("我要给***转账", "我要给某某某转账")
        .replace(/</g, "小于")
        .replace(/>/g, "大于")
        .replace(/\s+/g, "。")
        .replace(/[\r\n]/g, "。")
        .replace(/。+%/g, "%")
        .replace(/[()（）]/g, " ")
        .replace(/[^~～|￥$&ⅠⅡⅢ\.%:\：\-\-A-Za-z0-9\+\-\*\/,，;；。、?？《》≤≥*=\u4e00-\u9fa5]/g, "")
        .replace(/、/g, "。"); // 过滤掉英文、汉字、数字之外的内容，防止语音播放sdk崩溃
}

/**
 * 处理多音字
 */
function translationOfPolyphonicWords(str) {
    return str
        .replace(/还没有/g, "孩没有")
        .replace(/转/g, "竱")
        .replace(/还/g, "环")
        .replace(/为您的/g, "位您的")
        .replace(/重置/g, "虫置")
        .replace(/进行/g, "进形")
        .replace(/执行/g, "执形")
        .replace(/运行/g, "运形")
        .replace(/实行/g, "实形")
        .replace(/施行/g, "施形")
        .replace(/出行/g, "出形")
        .replace(/银行为/g, "银杭位")
        .replace(/我行为/g, "我杭位")
        .replace(/它行为/g, "它杭位")
        .replace(/他行为/g, "他杭位")
        .replace(/本行为/g, "本杭位")
        .replace(/招行为/g, "招杭位")
        .replace(/大行为/g, "大杭位")
        .replace(/发款行为/g, "发款杭位")
        .replace(/收款行为/g, "收款杭位")
        .replace(/行为/g, "形维")
        .replace(/行/g, "杭");
}
/**
 * 处理英文字符
 */
function translationOfEnglishWords(str) {
    return str
        .replace(/\+￥/g, "收入人民币")
        .replace(/\-￥/g, "支出人民币")
        .replace(/￥/g, "人民币")
        .replace(/RMB/g, "人民币")
        .replace(/GBP/g, "英镑")
        .replace(/HKD/g, "港币")
        .replace(/USD/g, "美元")
        .replace(/CHF/g, "瑞士法郎")
        .replace(/SGD/g, "新加坡元")
        .replace(/JPY/g, "日元")
        .replace(/CAD/g, "加拿大元")
        .replace(/AUD/g, "澳元")
        .replace(/EUR/g, "欧元")
        .replace(/KRW/g, "韩元")
        .replace(/km/gi, "千米")
        .replace(/HUAWEI/gi, "华为")
        .replace(/向ta转账/gi, "向他转账")
        .replace(/筝飞e盈/gi, "筝飞一盈")
        .replace(/Ⅰ/gi, 1)
        .replace(/Ⅱ/gi, 2)
        .replace(/Ⅲ/gi, 3);
}

function wai(jsApi) {
    /**
     * 移除所有元素的e-wai属性
     */
    function removeEWai() {
        let waiDoms = document.querySelectorAll("[e-wai=true]"); // 获取所有属性e-wai等于true的dom元素
        waiDoms.forEach(waiDom => {
            waiDom && waiDom.removeAttribute("e-wai"); // 移除所有元素的e-wai属性
        });
    }
    /**
     * 给当前元素添加e-wai属性
     */
    function setEWai(e) {
        function _set(dom) {
            if (
                (dom.getAttribute("id") && dom.getAttribute("id").indexOf("content") != -1) ||
                (dom.parentElement.getAttribute("id") && dom.parentElement.getAttribute("id").indexOf("content") != -1)
            ) {
                return;
            }
            dom.setAttribute("e-wai", true);
            return true;
        }
        let parent = getParent(e);
        // 如果父元素设置aria-atomic，整体读
        if (parent) {
            return _set(parent);
        } else {
            return _set(e.target);
        }
    }
    /**
     * 播放语音
     */
    function playText(e) {
        let text = getText(e);
        let mobileText = formatMobileText(text);
        // 手机号播放
        if (mobileText) {
            text = mobileText;
        }
        // 账号播放
        let accountText = formatAccountText(text);
        if (accountText) {
            text = accountText;
        }
        // 座机播放
        let telText = formatLandline(text);
        if (telText) {
            text = telText;
        }
        if (text) {
            text = filterSpecialCharacters(text);
            text = translationOfEnglishWords(text);
            text = translationOfPolyphonicWords(text);
            text = formatRateText(text);
        }
        // 默认文本播放
        if (text) {
            jsApi.playSuitableVoiceWithText({ text: text });
        }
    }
    /**
     * 获取要播放的文本
     */
    function getText(e) {
        let text = getChildNodes(e.target).join(" ");
        let parent = getParent(e);
        // 如果父元素设置aria-atomic，整体读
        if (parent) {
            text = getChildNodes(parent).join(" ");
        }
        if (text == "--") {
            text = "";
        }
        return text;
    }

    /**
     * 获取所有子节点
     */
    function getChildNodes(dom) {
        let nodes = [];
        let waiDate = false;
        let waiMessge = false;
        let role = "";
        let label = "";
        let waiText = "";
        let waiHidden = "";
        function handleText(text) {
            if (waiDate == "true") {
                // 转义成日期格式朗读数字
                text = formatDateText(text);
                waiDate = false;
            } else if (waiMessge == "true") {
                // 转义成报文格式朗读数字
                text = formatMessageText(text);
                waiMessge = false;
            }
            return text;
        }
        function handleDom(el) {
            let text = "";
            // 处理attribute属性
            if (typeof el.getAttribute === "function") {
                label = el.getAttribute("aria-label");
                role = el.getAttribute("role");
                waiDate = el.getAttribute("wai-date"); // 日期文本
                waiMessge = el.getAttribute("wai-message"); // 按照报文格式朗读
                waiText = el.getAttribute("wai-text");
                waiHidden = el.getAttribute("wai-hidden");
            }
            // 隐藏不读
            if (waiHidden == "true") {
                return true;
            }
            // 整体播放
            if (waiText) {
                waiText = handleText(waiText);
                nodes.push(waiText);
                return true;
            }
            // 获取节点文本
            let nodeNameWords = getNodeName(el);
            // wai-label > role > nodeName
            if (label) {
                text = text + label;
                label = "";
            } else if (role) {
                // 自定义标签属性
                let roleMap = {
                    tel: "短信验证码输入框",
                    password: "密码输入框",
                    select: "选择框",
                    button: "按钮"
                };
                text = text + roleMap[role];
                role = "";
            } else if (nodeNameWords) {
                if (nodeNameWords === "输入框") {
                    text = el.value ? "文本输入框，当前输入" : "文本输入框，";
                } else {
                    text = nodeNameWords + text;
                }
            }
            // nodeValue > value > placeholder
            if (el.nodeValue) {
                text = text + el.nodeValue;
            } else if (el.value) {
                text = text + el.value;
            } else if (el.placeholder) {
                text = text + el.placeholder;
            }
            // 日期、报文转义
            if (text) {
                text = handleText(text);
            }
            if (text) {
                nodes.push(text);
            }
        }

        let isWaiText = handleDom(dom);
        if (!isWaiText) {
            for (let i = 0; i < dom.childNodes.length; i++) {
                if (dom.childNodes[i].childNodes.length > 0) {
                    // 递归遍历子节点
                    if (getChildNodes(dom.childNodes[i])) {
                        nodes.push(...getChildNodes(dom.childNodes[i]));
                    }
                } else {
                    handleDom(dom.childNodes[i]);
                }
            }
        }
        return nodes;
    }
    /**
     * 停止播放语音
     */
    function stopPlay() {
        jsApi.stopPlaySuitableVoice();
    }
    /**
     * 插入无障碍样式
     */
    function insertWaiStyle() {
        let style = document.createElement("style");
        style.setAttribute("type", "text/css");
        style.setAttribute("id", "wai-style");
        style.innerHTML = "[e-wai]{outline: solid 2px #000000 !important;}";
        document.head.appendChild(style);
    }
    /**
     * 移除无障碍样式
     */
    function removeWaiStyle() {
        let style = document.getElementById("wai-style");
        style && document.head.removeChild(style);
    }
    /**
     * 获取父级设置aria-atomic属性的元素
     */
    function getParent(e) {
        let parent = e.target.parentElement;
        for (let i = 0; i < 10; i++) {
            if (!parent) {
                return null;
            }
            if (parent.getAttribute("aria-atomic") == "true") {
                return parent;
            }
            if (parent.nodeName == "LI") {
                return parent;
            }
            parent = parent.parentElement;
        }
        return null;
    }
    /**
     * 获取当前节点名称
     */
    function getNodeName(e) {
        let nodeNameWordsMap = {
            BUTTON: "按钮",
            INPUT: "输入框",
            TEXTAREA: "输入框"
        };
        let nodeName = e.nodeName;
        return nodeNameWordsMap[nodeName];
    }
    // 812 连续双击确认
    let doubleClickConfirm = App.bank.flags?.setting?.doubleClickConfirm;
    /**
     * 重置无障碍
     */
    function reset() {
        stopPlay(); // 停止语音播放
        resetDom();
    }
    function resetDom() {
        removeEWai(); // 移除所有dom元素e-wai属性
        removeWaiStyle(); // 移除无障碍样式
        if(doubleClickConfirm) {
            clearDoubleClick();
        }
    }
    let doublClickCount = 0;
    let doublClickTimer = null;
    function clearDoubleClick() {
        if(doublClickTimer) {
            clearTimeout(doublClickTimer);
        }
        doublClickCount = 0;
        doublClickTimer = null;
    }

    /**
     * 拦截点击事件，第一次阅读，第二次跳转
     */
    function waiHandler(e) {
        if (e.target) {
            if (
                e.target.getAttribute("e-wai") == "true" ||
                (getParent(e) && getParent(e).getAttribute("e-wai") == "true")
            ) {
                if(doubleClickConfirm) {
                    // 812 连续双击确认

                    doublClickCount = doublClickCount + 1;
                    if(doublClickCount == 2){
                        reset();
                    } else {
                        doublClickTimer = setTimeout(() => {
                            reset();
                        }, 500);
                        e.preventDefault(); // 阻止默认事件
                        e.stopPropagation(); // 阻止事件冒泡
                    }
                } else {
                    reset();
                }
            } else {
                reset();
                // 禁止页面整体播放
                if (e.target.getAttribute("aria-atomic") == "false") {
                    return;
                }
                // 父元素禁止播放子元素
                if (e.target.getAttribute("wai-hidden") == "true" && !getParent(e)) {
                    return;
                }

                // 第一次点击朗读
                e.preventDefault(); // 阻止默认事件
                e.stopPropagation(); // 阻止事件冒泡

                // 给当前元素设置e-wai属性
                if (setEWai(e)) {
                    insertWaiStyle(); // 插入无障碍样式
                    playText(e); // 播放语音
                }
            }
        }
    }

    function waiStart() {
        waiStop();
        document.body?.addEventListener("click", waiHandler, true);
    }

    function waiStop() {
        reset();
        document.body.removeEventListener("click", waiHandler, true);
    }

    return {
        /**
         * 开启无障碍
         */
        Start: function() {
            waiStart();
        },
        /**
         * 关闭无障碍
         */
        Stop: function() {
            waiStop();
        },
        /**
         * 重置无障碍
         */
        Reset: function() {
            resetDom();
        }
    };
}

module.exports = wai;
