let bankAnnounceFlg = "";
// 广告插屏广告是否显示，切换页面之后不再展示。
let announceFlgShow = "";
// 直接打开pdf不做跳转的菜单 72：银行询证函
let openPdfList = ["PDF/Index/Index/70", "PDF/Index/Index/71", "PDF/Index/Index/72", "PDF/Index/Index/08"];
// 获取当前菜单信息
async function getMenuId(fragment) {
    let menuId = "";
    const { moduleName, menuName, pageName } = fragment;
    const h5Url = `${moduleName}/${menuName}/${pageName}`;
    const menuInfo = await menu.findByUrl(h5Url);
    if (menuInfo && menuInfo.bsnCode) menuId = menuInfo.bsnCode;
    return menuId;
}

// 按播放顺序重新组合引导图数据
function resetImgsList(imgsList, menuId) {
    // 指定图片的尺寸限制，安卓、IOS区别处理
    let limitSize = "";
    if (App.jsApi.isInAndroid()) {
        limitSize = "1242*2688";
    } else {
        const { bottom } = App.jsApi.getSafeArea();
        if (!Number(bottom)) {
            limitSize = window.devicePixelRatio >= 3 ? "1242*2208" : "750*1334";
        } else {
            limitSize = window.screen.height > 2500 ? "1242*2688" : "1125*2436";
        }
    }

    // 获取属于当前菜单且符合设备尺寸的图片，同时计算需要显示的图片张数
    const menuImgsList = imgsList.filter(img => img?.pageId === menuId);
    const validImgsList = menuImgsList.filter(img => img.pctSize === limitSize);
    const seqs = validImgsList.map(img => Number(img.pctPlaySeq)).sort((a, b) => a - b);

    // 对符合尺寸的图片按播放顺序排序
    return seqs.reduce((acc, int) => {
        const img = validImgsList.find(img => img.pctPlaySeq == int);
        return img ? [...acc, img] : acc;
    }, []);
}

// 引导图的点击事件
// 点击展示下一张引导图，点击最后一张关闭所有引导图
function guideImgsClickHandler(e) {
    const className = "visible";
    const targetEle = e.target;

    if (targetEle.classList.contains("guide-img")) {
        const navImgItem = targetEle.parentElement;
        const nextNavImgItem = navImgItem.nextElementSibling;

        if (nextNavImgItem) {
            navImgItem.classList.remove(className);
            nextNavImgItem.classList.add(className);
        } else {
            const imgsContainer = navImgItem.parentElement.parentElement;
            window.removeEventListener("click", guideImgsClickHandler);
            document.body.removeChild(imgsContainer);
        }
    } else if (targetEle.classList.contains("jumpBtn")) {
        window.removeEventListener("click", guideImgsClickHandler);
        const existedWrapper = document.querySelector(".nav-images-container");
        document.body.removeChild(existedWrapper);
    }
}

// 生成并插入引导图DOM
function createGuideImgs(list) {
    const className = "nav-images-container";
    const existedWrapper = document.querySelector(className);
    const imgsListHtml = list.reduce((htmlStr, image, index) => {
        const url = transferUrl(image.pctUrl);
        return `${htmlStr}<li class="${!index ? " visible" : ""}"><img class="guide-img" src="${url}" wai-text="${
            image.advgDsc
        }"/></li>`;
    }, "");

    if (!existedWrapper) {
        const imgsWrapper = document.createElement("div");
        const listWrapper = document.createElement("ul");
        const btn = document.createElement("button");
        btn.classList.add("jumpBtn");
        btn.innerText = "跳过";
        btn.setAttribute("wai-text", "跳过");
        listWrapper.innerHTML = imgsListHtml;
        imgsWrapper.classList.add(className);
        imgsWrapper.appendChild(listWrapper);
        imgsWrapper.appendChild(btn);
        document.body.appendChild(imgsWrapper);
    } else {
        existedWrapper.innerHTML = imgsListHtml;
    }
}

// 展示当前菜单的引导图
async function showNavImgs(fragment) {
    if (App.jsApi.isInApp()) {
        try {
            // 获取引导图列表
            const menuId = await getMenuId(fragment);
            const rspData = await App.jsApi.getSpecifiedAdInfoByCache({ adType: "OPCL0404" });

            let imgsList = [];
            if (rspData.result && rspData.result.adInfo) imgsList = rspData.result.adInfo;
            if (!imgsList.length) return;

            // 获取已展示过引导图的菜单集合
            let menusWithNavImgs = await App.local.get("menusWithNavImgs");
            menusWithNavImgs = menusWithNavImgs || [];

            // 当前菜单已配置引导图且未展示过时，展示引导图并绑定点击事件
            const menuImgsList = resetImgsList(imgsList, menuId);
            if (menuImgsList.length && !menusWithNavImgs.includes(menuId)) {
                if (isNewStartup() && App.jsApi.isRootView()) {
                    App.local.set("menusWithNavImgs", [...menusWithNavImgs, menuId]);
                    return App.jsApi.showMaskGuidePage({ pageId: menuId });
                }
                createGuideImgs(menuImgsList);
                window.addEventListener("click", guideImgsClickHandler);
                App.local.set("menusWithNavImgs", [...menusWithNavImgs, menuId]);
                return true;
            }
        } catch (error) {
            globalUtil.logErrorWithCode("src/config/routerSetting.js查询当前菜单配置的引导图", "未正常获取到引导图");
        }
    }
}

// 判断当前菜单是否配置了插屏广告
async function isAdPage(fragment) {
    if (App.jsApi.isInApp()) {
        try {
            // 获取插屏广告列表
            const menuId = await getMenuId(fragment);
            const adInfoList = await App.jsApi.getSpecifiedAdInfoByCache({ adType: "OPCL0201" });
            const adList = adInfoList.result.adInfo || [];
            // 用户分享唤起或者点击了启动广告之后不弹出插屏广告
            if ((menuId == "MBECM030000" || menuId == "MBECM010000") && (App.mbank.adFlag || App.mbank.schemeFlag))
                return;
            // 查找是否为当前菜单配置了插屏广告
            return adList.find(item => item?.pageId === menuId);
        } catch (error) {
            window.$G.logErrorWithCode("src/config/routerSetting.jsrouterSetting.js  isAdPage", JSON.stringify(error));
        }
    }
}

/**
 * 缓存插屏广告
 * @param {Object} imgData
 */
async function InsertImageAd(moduleName, imgData = {}) {
    moduleName = moduleName.toLowerCase();
    window.imageAdCache[moduleName].push({
        picUrl: transferUrl(imgData.pctUrl),
        pctLinkUrl: imgData.pctLinkUrl,
        pctLinkTyp: imgData.pctLinkTyp,
        bscode: "", //参数待确认
        pageId: imgData.pageId,
        pctDlgFrq: imgData.pctDlgFrq,
        // 标题
        advgTtl: imgData.advgTtl,
        //唯一id
        advgNo: imgData.advgNo,
        //是否展示导航栏
        nvgFldFlg: imgData.nvgFldFlg,
        //展示标题
        nvgFldNm: imgData.nvgFldNm,
        // 类型11跳转微信小程序的小程序ID
        sourceId: imgData?.sourceId
    });
}

/**
 * 展示插屏广告
 */
function ShowImageAd(imgData, fragment) {
    if (window.imageAdCache.login) {
        _ShowImageAdList(window.imageAdCache.login, fragment);
        window.imageAdCache.login = [];
    }
    if (window.imageAdCache.setting) {
        _ShowImageAdList(window.imageAdCache.setting, fragment);
        window.imageAdCache.setting = [];
    }
    _ShowImageAd(imgData, fragment);
}

function _ShowImageAdList(list, fragment) {
    for (let i = 0; i < list.length; i++) {
        let item = list[i];
        _ShowImageAd(item, fragment);
    }
}

async function _ShowImageAd(imgData = {}, fragment) {
    const picUrl = transferUrl(imgData.pctUrl);
    if (picUrl) {
        const data = {
            picUrl: picUrl,
            pctLinkUrl: imgData.pctLinkUrl,
            pctLinkTyp: imgData.pctLinkTyp,
            bscode: "", //参数待确认
            pageId: imgData.pageId,
            pctDlgFrq: imgData.pctDlgFrq,
            // 标题
            advgTtl: imgData.advgTtl,
            //唯一id
            advgNo: imgData.advgNo,
            //是否展示导航栏
            nvgFldFlg: imgData.nvgFldFlg,
            //展示标题
            nvgFldNm: imgData.nvgFldNm,
            // 无障碍阅读内容
            advgDsc: imgData.advgDsc,
            advgDsc: imgData.advgDsc,
            //存入行级天数
            intrvDays: imgData.intrvDays,
            // 类型11跳转微信小程序的小程序ID
            sourceId: imgData?.sourceId
        };
        if (isNewStartup() && App.jsApi.isRootView()) {
            const menuId = await getMenuId(fragment);
            App.jsApi.openWebpageByPreload({
                url: "#LandScape/Index/Index?param=" + encodeURIComponent(JSON.stringify({ type: "01", data: data })),
                type: "2",
                bsnCode: menuId
            });
        } else {
            announceFlgShow = ViewBase.prototype.$imageAd(data);
        }
    }
}

/**
 * 调用globalUtils下面的函数。
 * 生产上报globalUtil.pushStartupTimeLine is not a function，原因未知，故加此方法检查。
 * @param {*} functionName 函数名称
 * @param {*} args 函数调用的参数
 * @param {*} args 函数执行环境
 */
function callFuntion(args, functionName = globalUtil.pushStartupTimeLine, env = globalUtil) {
    if (typeof functionName == "function") functionName.call(env, ...args);
}

/**
 * 开启客户端下拉刷新时需要的数据
 * scrollYMap: 楼层页面上次浏览时的滑动位置映射
 * routersNeedNativePulldown: 需要开启下拉刷新的楼层页面的路由地址
 */
const scrollYMap = {};
const homeRouter = "FloorHome/Home/Index";
const routersNeedNativePulldown = [
    homeRouter,
    "FloorHome/Financial/Index",
    "FloorHome/Life/Index",
    "FloorHome/CreditCard/Index",
    "FloorHome/Goods/Index",
    "FloorHome/Loan/Index"
];
routersNeedNativePulldown.forEach(router => (scrollYMap[router] = 0));

/**
 * 首页、信用卡、生活、理财页面开启客户端下拉刷新后
 * 正常滚动时需要触发各楼层组件注册的回调(保持和原来调用betterScroll的交互效果一致)
 * 所有回调均注册在当前所在的楼层内容实例(FloorHome/common/components/templatelist || floorRender)的scrollCallBack列表内
 */
function scrollHandler() {
    const scrollClass = "scrolling";
    const tlist = App.region?.areas?.content?.view?.$refs?.tlist;

    // bugfix: 楼层头部导航的fixed定位在webview下拉滚动时候无法跟随
    // 因此需要用absolute修复无法跟随的问题  在正常滑动时重置其为fixed定位
    if (App.jsApi.isInIOS) {
        if (window.scrollY <= 0) {
            tlist?.$el?.classList.remove(scrollClass);
        } else {
            tlist?.$el?.classList.add(scrollClass);
        }
    }

    tlist?.scrollCallBack && tlist.scrollCallBack?.forEach(cb => cb(window.scrollY));
}

/**
 * 页面跳转后 根据当前路由决定是否要开启客户端的下拉刷新功能
 * @param {router} string 当前页面路由
 */
function setPulldownRefresh(router) {
    // 首页、信用卡、生活、理财页面需要开启客户端下拉刷新
    // 其他页面则禁止客户端处理下拉刷新(保持h5使用betterScroll处理下拉刷新)
    if (routersNeedNativePulldown.includes(router)) {
        const options = {
            pulldownText: "下拉刷新",
            willRefreshText: "松手刷新",
            refreshingText: "刷新中"
        };
        if (router === homeRouter) {
            options.pulldownText = "下拉进入二楼";
            options.willRefreshText = "松手进入二楼";
            options.refreshingText = "松手进入二楼";
            options.refreshingIcon = false;
        }

        // bugfix: 使用客户端进行下拉刷新后 webview无法保存页面浏览位置
        // 因此需要在跳转前保存当前浏览位置 再次跳回时需要手动恢复到上次的浏览位置
        window.scrollTo(0, scrollYMap[router]);

        window.addEventListener("scroll", scrollHandler);
        App.jsApi.enablePulldownRefresh(options);
    } else {
        window.removeEventListener("scroll", scrollHandler);
        App.jsApi.disablePulldownRefresh();
    }
}

/**
 * 判断是否需要添加浏览倒计时
 * @param {*} e
 */
function checkBrowseCountdown(e) {
    //未添加倒计时
    if (document.getElementsByClassName("m-browse-countdown").length == 0) {
        //类似qry的路由参数，/?后会解析为对象参数,格式为：
        //#Finance/Detail/Buying/LHJZG02/?from=market&watchTm=30&chnlPdNo=0299FIN803zqkf0100
        const pageParam = e.target.routerOption.pageParam || {};
        //营销-浏览倒计时
        if (pageParam.from == "market" && pageParam.watchTm) {
            Log.info("【路由】", `【mbank-H5】after切面，需要加载营销倒计时`);
            ViewBase.prototype.$browseCountdown({
                watchTm: pageParam.watchTm,
                chnlPdNo: pageParam.chnlPdNo || ""
            });
        }
    }
}
/**
 * 查询公告弹框是否需要展示
 */
function isBankAnnounce() {
    try {
        if (!bankAnnounceFlg) {
            bankAnnounceFlg = ViewBase.prototype.$bankAnnounceComp();
        }
    } catch (error) {
        globalUtil.logErrorWithCode("src/config/routerSetting.js银行公告弹框报错,错误信息为：", JSON.stringify(error));
    }
}

// 路由切面事件结束时 在时间线进行埋点
function routerEventHandler(routerInfo, timeDetails) {
    if (["FloorHome/Home/Index", "SimpleHome/Home/Index"].includes(routerInfo.newFragmentUrl)) {
        callFuntion(timeDetails);
    }
}

/**
 * 显示插屏广告
 */
async function showAdImageOnScreen(routerInfo, announceFlgShow, moduleName) {
    // 根据配置显示引导图显示插屏广告
    let adImgData = await isAdPage(routerInfo.fragment);
    // 解决push消息离线点击通知栏到详情页还显示广告的问题
    if (announceFlgShow && typeof announceFlgShow?.hide == "function") {
        // 隐藏插屏广告
        announceFlgShow.hide();
    }
    if (adImgData) {
        if (moduleName === "Setting") {
            //如果是设置模块，则缓存插屏广告
            InsertImageAd(moduleName, adImgData);
        } else {
            //其他模块则直接展示插屏广告
            ShowImageAd(adImgData, routerInfo.fragment);
        }
    }
}
export default {
    // eslint-disable-next-line max-lines-per-function
    afterStart: function(App) {
        // eslint-disable-next-line
        App.router.on("before", async (e, routerInfo) => {
            let routerUrl = routerInfo.newFragmentUrl; // 获取跳转路径
            Log.info("【路由】", `【mbank-H5】进入before切面:${routerUrl}`);
            const startTime = globalUtil.getCurrentTime();
            if (isNewStartup()) {
                // 新开webview需重新获取简版缓存数据
                let mVersionInfo = await App.local.get("m_version_info");
                if (mVersionInfo) {
                    App.globalFlag.simpleFlag = mVersionInfo.simpFlg || "N";
                    App.globalFlag.autoChangeSimple = mVersionInfo.autoChangeSimple || "N";
                }
            }
            /**需要对跳转进行拦截的场景:**/
            // 我的页面打开公务卡
            if (
                isNewStartup() &&
                routerUrl === "FloorHome/CreditCard/Index" &&
                App.jsApi.isRootView() &&
                !App.bank.flags.floorHome.creditCardPageFlag
            ) {
                App.jsApi.openWebpageByPreload({
                    url:
                        "#" +
                        routerUrl +
                        "?param=" +
                        encodeURIComponent(JSON.stringify(routerInfo?.newRouterOption?.pageParam)),
                    type: "1"
                });
                return false;
            }
            // 新客户端，跳转返回首页进行拦截并关闭当前webview
            // 全部菜单页面打开信用卡页面不拦截
            if (
                routerUrl.indexOf("FloorHome") !== -1 &&
                routerInfo.oldFragmentUrl &&
                routerUrl !== "FloorHome/CreditCard/Index" &&
                routerInfo.oldFragmentUrl.indexOf("FloorHome") === -1 &&
                isNewStartup() &&
                !App.jsApi.isRootView()
            ) {
                let backData = {
                    preloadWebpageClose: true
                };
                if (routerInfo?.newRouterOption?.pageParam?.param) {
                    // 单独webview,主题需刷新
                    backData.themeId = routerInfo.newRouterOption.pageParam.param.themeId;
                }
                App.jsApi.closeWebContainer({ backData: backData });
                return false;
            }
            // 检测是否开启无障碍
            setWai();
            // 新客户端频道页切换
            if (
                isNewStartup() &&
                routerInfo.oldFragmentUrl.indexOf("FloorHome") != -1 &&
                routerInfo.oldFragmentUrl !== "FloorHome/DefaultIndex/Index" &&
                App.jsApi.isRootView() &&
                routerUrl?.indexOf("FloorHome") != -1
            ) {
                // 登录成功60岁客户自动跳转简版，处理中间页打开方式
                if (routerUrl == "FloorHome/DefaultIndex/Index") {
                    App.jsApi.openWebpageByPreload({
                        url:
                            "#" +
                            routerUrl +
                            "?param=" +
                            encodeURIComponent(JSON.stringify(routerInfo?.newRouterOption?.pageParam)),
                        type: "1"
                    });
                    return false;
                } else {
                    const menuItem = await menu.findByUrl(routerUrl);
                    await App.jsApi.changeTabbar({ bsnCode: menuItem.bsnCode });
                    return false;
                }
            }
            if (
                isNewStartup() &&
                routerInfo?.newFragmentUrl == "SimpleHome/Home/Index" &&
                routerInfo?.oldFragmentUrl.indexOf("FloorHome") != -1
            ) {
                versionSwitch();
                return false;
            }
            // 如果是语音播报功能
            if (routerUrl == "Search/Search/VoiceSearch") {
                let TypeofPhone = await App.jsApi.isInIOS();
                if (TypeofPhone) {
                    let systemVersion = await App.jsApi.getOsVer();
                    const clientVersion = await App.jsApi.getAppVerion();
                    // ios大于等于16.1版本
                    let booleanAlert = toNumVersion(systemVersion) >= toNumVersion("16.1");
                    // 启用语音银行功能的最小APP版本
                    let enableVoiceSearchVersion = App.bank.flags?.common?.enableVoiceSearchVersion;
                    if (booleanAlert && enableVoiceSearchVersion) {
                        let enableVoiceSearch = toNumVersion(clientVersion) < toNumVersion(enableVoiceSearchVersion);
                        if (enableVoiceSearch) {
                            App.getViewInstance("content").$confirm({
                                message: `该版本手机银行不支持此功能，请升级最新版本手机银行`,
                                sureText: `确定`,
                                cancelText: ""
                            });
                            return false;
                        }
                    } else if (booleanAlert) {
                        // 其他行不变
                        App.getViewInstance("content").$confirm({
                            message: `正在升级中，敬请期待`,
                            sureText: `确定`,
                            cancelText: ""
                        });
                        return false;
                    }
                }
            }
            // 新客户端频道页通过打开新的webview加载二级页面
            if (isNewStartup() && routerInfo.oldFragmentUrl?.indexOf("FloorHome") != -1 && App.jsApi.isRootView()) {
                App.jsApi.openWebpageByPreload({
                    url:
                        "#" +
                        routerUrl +
                        "?param=" +
                        encodeURIComponent(JSON.stringify(routerInfo?.newRouterOption?.pageParam)),
                    type: "1"
                });
                return false;
            }

            // 1.如果当前所在webview不是主webview时，对跳转或返回首页进行拦截并关闭当前webview
            if (routerUrl === "FloorHome/Home/Index" && !App.jsApi.isRootView() && App.jsApi.isInApp()) {
                App.jsApi.closeWebContainer();
                globalUtil.pushStartupTimeLine(
                    "routerBefore_break",
                    "routerBefore中断: 不是主webview被关闭",
                    startTime
                );
                return false;
            }

            // 新客户端跨webview传参处理
            if (isNewStartup()) {
                let paramArr = routerInfo.newFragmentUrl.split("?param=") || [];
                let newStartupParams = paramArr[1];
                if (newStartupParams && newStartupParams != 'undefined') {
                    let newUrl = paramArr[0];
                    try {
                        newStartupParams = JSON.parse(decodeURIComponent(newStartupParams));
                        App.router.go("#" + newUrl, { pageParam: newStartupParams});
                        return false;
                    } catch {
                    }
                   
                }
            }

            // 如果是打开PDF不做跳转
            if (openPdfList.includes(routerUrl)) {
                let agrTyp = routerInfo?.newFragment.params[0];
                getPdfqry(agrTyp);
                return false;
            }
            // 2.如果当前为主webview且为简版，对跳转或返回首页进行拦截并重定向到简版首页
            if (routerUrl === "FloorHome/Home/Index" && App.globalFlag && App.globalFlag.simpleFlag === "Y") {
                Log.info("【路由】", `【mbank-H5】before切面中止:${routerUrl}，跳转至简版`);
                App.router.go("#SimpleHome/Home/Index");
                // 推送时间线至客户端
                callFuntion(["routerBefore_break", "routerBefore中断: 跳转到简版", startTime]);
                return false;
            }
            if (routerUrl === "FloorHome/Home/Index" && App.globalFlag && App.globalFlag.huinong === "Y") {
                Log.info("【路由】", `【mbank-H5】before切面中止:${routerUrl}，跳转至惠农版`);
                App.router.go("#Huinong/Home/Index");
                // 推送时间线至客户端
                callFuntion(["routerBefore_break", "routerBefore中断: 跳转到惠农版", startTime]);
                return false;
            }

            // 3.如果网络异常，阻止跳转进行
            const networkStatus = await App.storage.get("onNetworkStatus");
            // 统计获取网络状态耗时
            callFuntion(["routerBefore", "routerBefore: 获取判断网络状态", globalUtil.getCurrentTime()]);
            if (networkStatus === false) {
                App.getViewInstance("content").$confirm({
                    message: "网络异常，请检查您的网络设置",
                    sureText: "查看详情",
                    cancelText: "知道了",
                    closed: isSure => {
                        if (isSure) {
                            App.jsApi.openWebPageByWebContainer({
                                url: "./network_exception_boot_page.html"
                            });
                        }
                    }
                });
                return false;
            }
            // 4.财富总览跳转处理, 如果配置了使用新的版本则跳转到配置的版本, 默认使用最初版本
            // 财富总览首页;
            if (routerUrl === "Wealth/Index/Index" && App.bank.flags.wealth?.newWealth) {
                App.menu.go(App.bank.flags.wealth.WealthIndexMenu);
                return false;
            }
            // 卡视角页
            if (routerUrl === "Wealth/Index/Card" && App.bank.flags.wealth?.newWealth) {
                App.menu.go(App.bank.flags.wealth.WealthCardMenu);
                return false;
            }

            /**跳转前的操作:**/
            // 1.跳转前记录当前页面的浏览位置
            scrollYMap[routerInfo.oldFragmentUrl] = window.scrollY;

            // 2.触发全局router:before事件
            App.region?.triggerGlobalEvent("router:before", routerInfo);

            // 3.开启顶部进度条的加载
            ViewBase.prototype.$triggerProgressBar("start", routerInfo);

            // 4.如果跳转页面为首页，需要首先响应用户在App外部的文本复制操作
            if (routerUrl === "FloorHome/Home/Index" || routerUrl === "SimpleHome/Home/Index") {
                CopyTextCheck();
                callFuntion(["routerBefore_routerSetting", "routerBefore: 页面跳转前的全局配置", startTime]);
                return true;
            }

            // 806微信基金业务
            if (WapWeChatUtil.isWXBusiness()) {
                if (!(await WapWeChatUtil.routerBefore(routerUrl))) {
                    return false;
                }
            }

            // 5.根据跳转路由获取对应的菜单信息，对菜单进行是否允许跳转的校验
            // menu.findByUrl封装了对潍小宝之类地址传参的处理，能够正常返回menuInfo(前提是传入正确参数)

            const beginTimeGetMenu = globalUtil.getCurrentTime();

            // 新首页routerInfo.newFragmentUrl格式为#LandScape/Index/Index?param=xxxx
            // 需要单独处理，否则findByUrl找不到对应菜单无法进行权限校验
            if(isNewStartup()) {
                routerUrl = routerUrl.split("?")[0]
            }

            const menuInfo = await menu.findByUrl(routerUrl); // 根据路由匹配菜单信息
            callFuntion(["routerBefore_getMenu", "路由before切面获取菜单信息", beginTimeGetMenu]);

            if (menuInfo) {
                // 缓存访问信息，供二楼“最近使用”展示使用
                viewed.cacheViewedHistory({
                    menu: menuInfo,
                    param: routerInfo.newRouterOption
                });

                const beginCheckTime = globalUtil.getCurrentTime();
                // 菜单权限校验
                const permissionCheckPassed = await menu.permissionCheck({ routerUrl, menuInfo });
                callFuntion(["routerBefore_permissionCheck", "路由before切面菜单权限校验", beginCheckTime]);

                if (permissionCheckPassed) {
                    const beginTimeChildCheck = globalUtil.getCurrentTime();
                    // 搜索功能跳转宝贝卡模块进行绑卡判断进行拦截
                    const childCheckResult = await menu.childIntercept(routerInfo);
                    callFuntion(["routerBefore_babyCardCheck", "路由before切面宝贝卡绑卡判断", beginTimeChildCheck]);

                    if (!childCheckResult) {
                        // 绑卡校验未通过(不继续跳转)时要终止进度条的加载
                        ViewBase.prototype.$triggerProgressBar("done");
                    }
                    return childCheckResult;
                } else {
                    // 菜单权限校验未通过(不继续跳转)时要终止进度条的加载
                    ViewBase.prototype.$triggerProgressBar("done");
                }
                return permissionCheckPassed;
            }
            Log.info("【路由】", `【mbank-H5】完成before切面:${routerUrl}`);
        });

        // 跳转中断需要删除进度条
        App.router.on("break", e => {
            // 如果在触发router before切面之前已经中断
            // 此时进度条dom尚未通过start插入 无需进行进度条结束的操作
            if (e.beforeRouterbefore) return;
            ViewBase.prototype.$triggerProgressBar("done");
        });
        App.region.on("break", (e, param) => {
            const { code, errorId, orignMsg, cvrtMsg } = param;

            // 启动并跳转首页，首页加载失败时，镜像页不会关闭导致加载报错的h5错误弹框被遮挡
            // 因此需要由h5调起客户端弹框显示加载失败的信息
            if (App.router.fragmentUrl === "FloorHome/Home/Index") {
                App.jsApi.appLaunchPopDialog({
                    title: "温馨提示",
                    desc: orignMsg,
                    buttonList: ["确定"]
                });
            } else {
                window.$G.popErrorMsg(param);
            }
        });

        App.router.on("after", async (e, routerInfo) => {
            const startTime = globalUtil.getCurrentTime();
            Log.info("【路由】", `【mbank-H5】进入after切面:${routerInfo.fragmentUrl}`);

            // 如果当前客户端支持原生下拉刷新 则判断当前跳转后的页面是否需要开启客户端下拉刷新
            if (globalUtil.refreshByNativePulldown?.()) setPulldownRefresh(routerInfo.fragmentUrl);

            App.jsApi.setStatusBarStyle({ style: 0 });
            addHistoryInfo(routerInfo);
            App.region?.triggerGlobalEvent("router:after", routerInfo);

            // 判断当前页面是否需要开启防截/录屏
            if (App.globalFlag.noScreenCaptureUrl.includes(routerInfo.fragmentUrl)) {
                // 在每次开启防录屏时，先关闭一下防录屏
                // 防止因客户端被开启多个防录屏，导致不需要防录屏的页面也会出现防录屏提示
                App.jsApi.setForbidScreenShot({ isForbid: false });
                App.jsApi.setForbidScreenShot({ isForbid: true });
            } else {
                App.jsApi.setForbidScreenShot({ isForbid: false });
            }

            // 结束导航栏为进度条加载
            ViewBase.prototype.$triggerProgressBar("done");
            const { moduleName } = routerInfo.fragment;
            //首先判断是否有公告弹框，如果有则弹出公告弹框
            let isInApp = await App.jsApi.isInApp();
            if (isInApp) {
                //如果在app内，则请求银行公告接口
                isBankAnnounce();
            }
            // 新客户端频道页切换通过监听页面重新可见弹插屏广告
            if (moduleName === "FloorHome" && isNewStartup()) {
                let fragmentUrl = routerInfo?.fragmentUrl;
                const ee = new CustomEvent();
                ee.on("customEventPageResume", function(data) {
                    const result = data?.result;
                    // 关闭弹窗的页面重新可见事件，不再重复弹窗
                    // 只有当前fragmentUrl与缓存fragmentUrl一致才弹窗
                    // 因为hash 会变，所以用fragmentUrl
                    if (result?.type != "ad" && fragmentUrl == routerInfo?.fragmentUrl) {
                        showAdImageOnScreen(routerInfo, announceFlgShow, moduleName);
                    }
                });
            }
            // 根据配置显示引导图 引导图显示后不再显示插屏广告
            const isNavImgsExisted = await showNavImgs(routerInfo.fragment);
            if (isNavImgsExisted) return;

            await showAdImageOnScreen(routerInfo, announceFlgShow, moduleName);

            // 806微信基金业务
            if (WapWeChatUtil.isWXBusiness()) {
                WapWeChatUtil.routerAfter();
            }

            // 判断是否需要添加浏览倒计时
            checkBrowseCountdown(e);

            if (routerInfo.fragmentUrl == "FloorHome/Home/Index" || routerInfo.fragmentUrl == "SimpleHome/Home/Index") {
                // 推送时间线给客户端
                callFuntion(["routerAfter_routerSetting", "routerAfter: 页面跳转后的全局配置", startTime, false]);
            }
            Log.info("【路由】", `【mbank-H5】完成after切面:${routerInfo.fragmentUrl}`);
        });

        /**
         * 路由周期时间线打点
         * 时间节点顺序：routerBefore -> loadModuleConfig -> loadRouter & routerAfter
         * 只有等每个节点相关的所有切面事件执行结束后，才会进入一个节点
         */
        let routerBeforeBeginTime = "";
        App.router.once("before_routerBefore", () => {
            routerBeforeBeginTime = globalUtil.getCurrentTime();
        });
        App.router.once("after_routerBefore", (e, routerInfo) => {
            routerEventHandler(routerInfo, [
                "routerBefore",
                "routerBefore: 所有routerBefore切面事件总耗时",
                routerBeforeBeginTime
            ]);
        });

        // 获取模块配置并解析加载的耗时
        let loadModuleBeginTime = "";
        App.router.once("before_loadModuleConfig", () => {
            loadModuleBeginTime = globalUtil.getCurrentTime();
        });
        App.router.once("after_loadModuleConfig", (e, routerInfo) => {
            routerEventHandler(routerInfo, [
                "loadModuleConfig",
                "loadModuleConfig: 获取模块配置的总耗时",
                loadModuleBeginTime
            ]);
        });

        // 获取路由配置并解析加载的耗时
        let loadRouterBeginTime = "";
        App.router.once("before_loadRouter", () => {
            loadRouterBeginTime = globalUtil.getCurrentTime();
        });
        App.router.once("after_loadRouter", (e, routerInfo) => {
            routerEventHandler(routerInfo, ["loadRouter", "loadRouter: 获取页面路由配置的总耗时", loadRouterBeginTime]);
        });
    }
};
