import { ViewBase } from "@ehas/core";
import { isLogin } from "~/common/utils/check";
import { transferUrl } from "../../common/js/filters";
import { downloadPic } from "~/common/utils/https.js";
import { updateUserInfo } from "~/common/utils/user";
import { getLocalUserInfo } from "~/common/utils/user";
import { div } from "~/common/js/calculation.js";
import { isNewStartup } from "~/common/utils/check";

class LoginUtil {
    /**
     * 跳转登录页面，登录成功或者取消登录后会返回对应url
     * @param {*} url 登录成功之后需要跳转的页面(链接获取菜单码)
     * @param {*} param 跳转参数
     * @param {*} type 跳转类型(1:跳转6.0内部页面,传参方式url/param;2:跳转6.0内部页面,参数为url,param;3:跳转6.0之外页面)
     * @param {*} selfRefreshFlag 是否主动调用刷新（局部刷新）true：主动局部刷新 false：不主动刷新 我的页面登录后还需要留在我的页面，需自我刷新
     */
    async goToLogin(
        url = "FloorHome/DefaultIndex/Index",
        param,
        config = { type: "1", selfRefreshFlag: false, sessionout: false }
    ) {
        try {
            // loginView:标识当前是否正在调起登录窗口
            // Y代表正在调起登录, 空值代表未登录
            if (App.globalFlag.loginView === "Y") return;
            App.globalFlag.loginView = "Y";
            //登录前不检查用户信息
            App.jsApi.writeToMemoryStorage({ key: "m_login", value: "N" });
            // 获取登录返回路由
            const { router } = await App.jsApi.goLogin();
            App.globalFlag.loginView = "";
            //获取客户端缓存,如果是标志返回的是Y，默认跳转到老年版
            const userMessage = await getLocalUserInfo();
            if (userMessage?.attSts == "1") {
                //如果attSts为1，则表示关注中，进行弹框提示客户信息状态异常，请到柜面处理
                App.getViewInstance("content").$confirm({
                    message: `客户信息状态异常，请到柜面处理`,
                    sureText: `我知道了`,
                    cancelText: ""
                });
            }
            // 用于 登录之后设置路由标记,检查用户证件信息
            App.jsApi.writeToMemoryStorage({ key: "m_login", value: "Y" });
            // 更新插屏广告和运营位数据
            try {
                const signPhoneNo = await App.local.get("loginUserNm");
                const adInfo = await App.requester.request("cmm/opcl/opclmanage.biz", {
                    data: { signPhoneNo: signPhoneNo }
                });

                App.jsApi.saveAdInfoToStorage({ adInfo: adInfo.hashMap });
            } catch (error) {
                window.$G.logErrorWithCode("common/js/login.js更新运营位异常", JSON.stringify(error));
            }
            // 809待办事项中修改密码单独查询，在首页提示
            if (App.bank.flags?.common?.homeFooterPswTip) {
                await this.checkPswInfo();
            }
            //跳转待处理事项,如果客户端返回的路由不存在则查询待办事项
            if (!router) {
                this.checkUserInfo(userMessage);
            }
            let MenuId = await window.$G.getMenuId();
            if (userMessage.simpFlg == "Y" && App.globalFlag.autoChangeSimple == "N") {
                if (userMessage.skpTyp) {
                    // 服务端返回skpTyp为1遮罩展示, 0或没有值为空还是默认进简版
                    let rspData = await App.jsApi.getSpecifiedAdInfoByCache({ adType: "OPCL0201" });
                    let adList = rspData?.result?.adInfo || [];
                    // 遮罩引导图片运管以登录页面ID配置
                    let versionSwitchAd = adList.find(item => item?.pageId === "MBLGN010000");
                    if (!versionSwitchAd) {
                        return;
                    }
                    let picUrl = transferUrl(versionSwitchAd.pctUrl);
                    if (picUrl) {
                        ViewBase.prototype.$simpleGuide({
                            picUrl: picUrl
                        });
                    }
                } else if (!router && (MenuId == "MBUSR050000" || MenuId == "MBECM030000")) {
                    if (App.bank.flags.login?.isSwitchEdition) {
                        //如果是816行，则增加弹框提示，是否跳转简版
                        App.getViewInstance("content").$confirm({
                            title: `尊敬的客户：`,
                            message: `我行推出简爱版手机银行，界面更简洁、操作更简单，欢迎体验！`,
                            sureText: `立即体验`,
                            cancelText: "暂不体验",
                            modalId: "sessionOut",
                            closed: sure => {
                                App.globalFlag.autoChangeSimple = "Y";
                                App.local.set("m_version_info", { autoChangeSimple: "Y" });
                                if (sure) {
                                    App.globalFlag.simpleFlag = "Y";
                                    App.local.set("m_version_info", { simpFlg: "Y", autoChangeSimple: "Y" });
                                    window.$G.goToNext("#FloorHome/DefaultIndex/Index");
                                }
                            }
                        });
                    } else {
                        //非816行
                        //如果当前菜单为我的页面或者首页，服务端接口返回的是Y并且缓存中记录的非N的情况下，则跳转到简版
                        App.globalFlag.simpleFlag = "Y";
                        App.globalFlag.autoChangeSimple = "Y";
                        App.local.set("m_version_info", { simpFlg: "Y", autoChangeSimple: "Y" });
                        window.$G.goToNext("#FloorHome/DefaultIndex/Index");
                        return;
                    }
                }
            }

            if (router) {
                //关闭检查用户证件信息，防止弹出待处理事项
                App.jsApi.writeToMemoryStorage({ key: "m_login", value: "N" });
                // 登录返回路由时跳转该路由(强制修改手机号)
                if (param) {
                    window.$G.goToNext(router, param);
                } else {
                    window.$G.goToNext(router);
                }
            } else {
                // 没有返回路由跳转初始目标页面
                if (config.selfRefreshFlag) {
                    return 1;
                } else {
                    this.dealLoginReturn(url, param, config);
                }
            }
        } catch ({ result: { router } }) {
            App.globalFlag.loginView = "";
            const rootFlag = await App.jsApi.isRootView(); //判断是否为主webview
            if (isNewStartup() && !rootFlag) {
                let backData = {};
                if (Object.keys(App.globalFlag?.closeLoginStateDate).length !== 0) {
                    backData = App.globalFlag.closeLoginStateDate;
                }
                App.jsApi.closeWebContainer({ backData: backData });
            } else {
                // 取消登录若有路由则跳转该页面(跳转客服)
                if (config.sessionout) {
                    //如果超时，则跳转到首页
                    window.$G.goToNext("#FloorHome/DefaultIndex/Index");
                } else if (router) {
                    //其他情况，若有路由则跳转该页面(跳转客服)
                    window.$G.goToNext(router);
                }
            }
        }
    }
    // 809查询密码修改信息
    async checkPswInfo() {
        await App.requester
            .request("usr/check/lgncstinfoshowtipchk.biz", {
                data: {
                    pswdModChkFlg: "Y"
                },
                slient: true // 去掉默认loading
            })
            .then(async rsp => {
                //809首页底部弹窗
                App.storage.remove("login_check_lgncstinfo");
                App.storage.set("login_check_lgncstinfo", rsp);
            });
    }
    // 判断是否要查询用户信息
    checkUserInfo(userMessage = {}) {
        App.requester
            .request("usr/check/lgncstinfoshowtipchk.biz", {
                slient: true, // 去掉默认loading
                useDefaultError: false
            })
            .then(async rsp => {
                //获取当前时间
                const nowData = new Date().getTime();
                //获取缓存时间
                const localData = (await App.local.get("login_localTime_time")) || "";
                let isShowAlert = "";
                // 缓存时间存在,已经弹过弹框，并判断是不是在返回天内，是则弹，不是则不弹
                if (localData) {
                    // 缓存时间戳天数是否小于接口返回天数
                    isShowAlert =
                        parseInt(div(Number(nowData) - Number(localData), 86400000)) >= Number(rsp.intrvDays)
                            ? true
                            : false;
                }
                this._updateAvatar(userMessage, rsp.certEndFlg, () => {
                    // 809过滤掉修改密码
                    if (App.bank.flags?.common?.homeFooterPswTip) {
                        rsp.LIST = rsp.LIST?.filter(item => item.rmndTyp != "05");
                    }
                    if (rsp.LIST && rsp.LIST.length) {
                        // 待办跳转
                        window.$G.goToNext("#Mine/Setting/MatterList", rsp.LIST);
                    } else if (rsp?.checkShowTipDaoList?.length > 0) {
                        // 缓存时间不存在或者缓存时间时间大于接口时间
                        if (!localData || isShowAlert) {
                            // 登录短信提醒、缓存不存在或者缓存天数
                            // 存入弹框时间戳
                            App.local.remove("login_localTime_time");
                            App.local.set("login_localTime_time", new Date().getTime());
                            App.getViewInstance("content").$confirm({
                                title: rsp?.checkShowTipDaoList[0]?.remark || "登录短信提醒",
                                message:
                                    rsp?.checkShowTipDaoList[0]?.rmndCntnt ||
                                    "尊敬的客户您好，您当前已开通登录短信提醒功能，若想关闭该业务，可通过“安全中心-登录管理”功能关闭登录短信提醒开关。",
                                sureText: rsp?.checkShowTipDaoList[0]?.btnNm || "去关闭",
                                cancelText: "我知道了",
                                closed: isSure => {
                                    if (isSure) {
                                        window.App.menu.go(rsp.checkShowTipDaoList[0]?.btnFuncId || "MBCTF010200");
                                    }
                                }
                            });
                        }
                    }
                });
            })
            .catch(() => this._updateAvatar(userMessage, ""));
    }
    _updateAvatar(rsp, certEndFlg, callback) {
        //如果数据库中，头像地址存在，则下载图片
        if (rsp.databasePath) {
            downloadPic(rsp.databasePath, false)
                .then(res => {
                    const info = { avatar: "file://" + res };
                    info.certEndFlg = certEndFlg;
                    updateUserInfo(info, callback);
                })
                .catch(() => {
                    updateUserInfo({ certEndFlg }, callback);
                });
        } else {
            //其他情况
            updateUserInfo({ certEndFlg }, callback);
        }
    }

    /**
     * 处理登录返回逻辑
     */
    dealLoginReturn(url, param, config) {
        const { sessionout, type } = config;
        const { moduleName } = App.router.fragment;
        let timeoutReturnRouters = {
            // 超时返回路由配置
            Transfer: "#Transfer/Transfer/Index", //转账
            Account: "#Account/MyAccount/MyAcctList", //账户
            Fund: "#Fund/Index/Index", // 基金
            Wealth: "#FloorHome/DefaultIndex/Index", //
            //解决自动理财返回存款主页问题，存款模块超时重新登录跳转首页
            Deposit: "#FloorHome/DefaultIndex/Index", //
            Finance: "#Finance/Index/Index", //
            FinanceHS: "#FinanceHS/Index/Index", //
            FinanceQL: "#FinanceQL/Index/Index", //
            ChildCard: "#ChildCard/Index/Index", //
            YuEBao: "#FloorHome/Financial/Index", //
            CDTS: "#CDTS/Index/Index", //
            Loan: "#FloorHome/DefaultIndex/Index" //
        };
        const timeoutReturnRouter = timeoutReturnRouters[moduleName];

        // 重新操作手机时弹窗提醒超时，可重新登录或返回app首页，登录后保持在当前页，保存页面录入信息，取消登录返回app首页。
        // 验密页或其他认证页重新操作手机时弹窗提醒超时，可重新登录或返回app首页，登录后返回至功能首页，取消登录返回app首页。
        if (sessionout && timeoutReturnRouter) return window.$G.goToNext(timeoutReturnRouter);

        const nextUrl = url.includes("#") ? url : `#${url}`;
        if (type == 2) {
            window.$G.goToNext(nextUrl, param);
            return;
        }
        if (type == 3) {
            App.menu.go(url, param);
            return;
        }
        if (type == 25) {
            return App.jsApi.openWebPageByJSBContainer({ url }); //支付平台页面
        }
        window.$G.goToNext(nextUrl);
    }

    /**
     * app切换后台事件
     */
    onPause() {
        App.local.set("m_pause_time", new Date());
    }

    getAppWaitTime() {
        return App.local?.get("m_app_time") ?? {};
    }
    /**
     * app唤起事件
     */
    onResume() {
        isLogin().then(res => {
            if (res) {
                App.local.get("m_pause_time").then(rsp => {
                    //从缓存中获取到后台在线时长
                    this.getAppWaitTime().then(AppWtime => {
                        let tpause = 0;
                        if (AppWtime) {
                            //获取到后台设置的在线时长转换为毫秒数
                            tpause = AppWtime * 60 * 1000;
                        } else {
                            tpause = 5 * 60 * 1000;
                        }
                        //获取到唤起APP的时间，转换为毫秒
                        let mNowTime = new Date();
                        let mNowTimeMs = mNowTime.getTime();
                        //获取到APP切换后台的时间
                        let mPauseTimeMs = new Date(rsp);
                        mPauseTimeMs = mPauseTimeMs.getTime();
                        //定义一个等式计算时间差
                        let interval = mNowTimeMs - mPauseTimeMs;
                        if (interval > tpause) {
                            // 超过后台在线时长退出不调退出接口，直接清除客户端会话
                            // 同时跳转频道页首页，不再跳转登录窗口
                            App.jsApi.updateLoginInfo();
                            window.$G.goToNext("#FloorHome/DefaultIndex/Index");
                        }
                    });
                });
            }
        });
    }
    /**
     * 清除登录标识
     */
    async clearLoginFlag() {
        await App.requester
            .request("usr/logout/userlogout.biz", { resolution: true })
            .finally(() => {
                App.jsApi.updateLoginInfo();
            })
            .catch(() => {});
    }
}

const loginUtil = new LoginUtil();
export { loginUtil };
