import { View } from "@ehas/core";
import { isLogin } from "~/common/utils/check";
import template from "./templates/Index.vhtml";
export let option = {
    className: "e-version-switch"
};
/* eslint-disable */
class MVersionSwitch extends View {
    template = template;
    data = function () {
        return {
            iconCheck: `${ehasProcessCDNServer}/public/${App.bank.flags.common.bankId}/h5/mbank/Common/components/m-version-switch/icon_check.png`,
            iconChecked: `${ehasProcessCDNServer}/public/${App.bank.flags.common.bankId}/h5/mbank/Common/components/m-version-switch/icon_checked.png`,
            versions: [
                {
                    thumb: `${ehasProcessCDNServer}/public/${App.bank.flags.common.bankId}/h5/mbank/Common/components/m-version-switch/standard_thumb.png`,
                    bg: `${ehasProcessCDNServer}/public/${App.bank.flags.common.bankId}/h5/mbank/Common/components/m-version-switch/standard_bg.png`,
                    title: "标准版",
                    desc: App.bank.flags.mine?.isRewriteWord ? "此刻尽享智慧生活" : "全面服务，智享生活",
                    checked: App.globalFlag?.simpleFlag != "Y" && App.globalFlag?.huinong != "Y",
                    type: "standard"
                },
                {
                    thumb: `${ehasProcessCDNServer}/public/${App.bank.flags.common.bankId}/h5/mbank/Common/components/m-version-switch/basic_thumb.png`,
                    bg: `${ehasProcessCDNServer}/public/${App.bank.flags.common.bankId}/h5/mbank/Common/components/m-version-switch/basic_bg.png`,
                    title: "关爱版",
                    desc: App.bank.flags.mine?.isRewriteWord ? "此刻更享温情陪伴" : "清晰简洁，关爱长辈",
                    checked: App.globalFlag?.simpleFlag == "Y",
                    type: "simpFlg"
                },
                {
                    thumb: `${ehasProcessCDNServer}/public/${App.bank.flags.common.bankId}/h5/mbank/Common/components/m-version-switch/huinong_thumb.png`,
                    bg: `${ehasProcessCDNServer}/public/${App.bank.flags.common.bankId}/h5/mbank/Common/components/m-version-switch/huinong_bg.png`,
                    title: "乡情版",
                    desc: App.bank.flags.mine?.isRewriteWord ? "此刻乐享惠农金融" : "绿色金融，助力三农",  //"绿色金融，助力三农",
                    checked: App.globalFlag?.huinong == "Y",
                    type: "huinong"
                }
            ],
            closeImg: `${ehasProcessCDNServer}/public/${App.bank.flags.common.bankId}/h5/mbank/Common/components/m-version-switch/icon_close.png`
        };
    };
    methods = {
        async show() {
            this.$refs.versionSwitch && this.$refs.versionSwitch.show();
        },
        async hide() {
            this.$refs.versionSwitch && this.$refs.versionSwitch.hide();
        },
        bindClickVersion(version) {
            this.versions.forEach(item => {
                item.checked = false;
            });
            version.checked = true;
            this.setVersion(version.type);
        },
        setVersion(version) {
            let flags = { simpFlg: "N", huinong: "N" };
            flags[version] = "Y";
            let key = version == "simpFlg" ? "simpleFlag" : version;
            App.globalFlag.autoChangeSimple = version == "simpFlg" ? "N" : "Y";
            flags["autoChangeSimple"] = App.globalFlag.autoChangeSimple;
            App.globalFlag.simpleFlag = "N";
            App.globalFlag.huinong = "N";
            App.globalFlag[key] = "Y";
            // 缓存简版标志
            App.local.set("m_version_info", flags).then(() => {
                this.hide();
                window.$G.goToNext("#FloorHome/Home/Index");
            });
            isLogin().then(res => {
                if (res) {
                    this.request("usr/simpmbank/simpmbankchg.biz", {
                        data: {
                            simpFlg: version == "huinong" ? "F" : App.globalFlag.simpleFlag
                        }
                    });
                }
            });
        }
    };
}

export default async function install(App, ViewBase) {
    let viewOption = await new MVersionSwitch()._transformParam();
    let versionSwitchComponent = ViewBase.extend(viewOption);

    ViewBase.prototype.$versionSwitch = function (option) {
        let tempContainer = document.createElement("div");
        document.body.appendChild(tempContainer);

        let ins = new versionSwitchComponent({
            el: tempContainer,
            propsData: option
        });

        setTimeout(() => {
            ins.show();
        });

        return ins;
    };
}
