import { ViewBase } from "@ehas/core";import Nprogress from "nprogress";export default {    install() {        // A页面->B页面: 在当前页面A配置并显示进度条(即routerbefore)，在跳转页面B隐藏进度条(即routerafter)        // eslint-disable-next-line max-statements        ViewBase.prototype.$triggerProgressBar = (status, routerInfo = {}) => {            const navBarSelector = "#nav-bar";            const navBar = document.querySelector(navBarSelector);            if (!navBar) return; // 默认只在有导航栏的页面显示或隐藏进度条            const options = { showSpinner: false, parent: navBarSelector };            const { newFragment = {} } = routerInfo; // 即将跳转的模块            const modulesWithoutNavBar = ["FloorPage", "FloorHome", "SimpleHome", "Huinong"]; // 不包含导航栏的模块
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 
            // 当前A页面为带导航栏的页面，跳转页面B为无导航栏的页面时(如频道页)，A页面不再配置显示进度条            // 原因：进度条加载为异步，跳转后仍然会有DOM操作，没有导航栏时会抛出异常            if (status === "start" && !modulesWithoutNavBar.includes(newFragment.moduleName)) {                Nprogress.configure(options);                Nprogress.start();                return;            }            if (status === "done") {                const routerList = App.router.historyList;                const routerLen = routerList.length;                const prevRouter = routerList[routerLen - 2];                const configuredInCurrentPage =                    (!prevRouter || modulesWithoutNavBar.includes(prevRouter.url.split("/")[0])) &&                    !modulesWithoutNavBar.includes(App.router.fragment.moduleName);                if (configuredInCurrentPage) {                    // 当前页面A为无导航栏页面(如频道页)，B为带导航栏页面时                    // 因为在A页面未进行进度条的配置和显示，所以在要在B页面完成配置、显示、隐藏(模拟完整的进度加载)                    Nprogress.configure(options);                    Nprogress.start();                    setTimeout(() => {                        if (document.querySelector(navBarSelector)) Nprogress.done();                    }, 200);                } else {                    if (document.querySelector(navBarSelector)) Nprogress.done();                }            }        };    }};