/**
 * 浏览倒计时
 * 营销跳转页面,路径后面拼接/?from=market&watchTm=30
 * 如果是频道页，则离开初始页面倒计时会暂停，返回时倒计时继续
 * 非频道页，切换页面时、倒计时会继续进行
 */
import { View } from "@ehas/core";
import template from "./templates/index.vhtml";

export let option = {
    className: "m-browse-countdown",
    default: {}
};

class BrowseCountdown extends View {
    template = template;
    props = {
        //倒计时总值
        watchTm: {
            type: String | Number,
            default: 30
        },
        //产品渠道码
        chnlPdNo: {
            type: String,
            default: ""
        }
    };
    data = function() {
        return {
            //宝箱
            chestImg: `${ehasProcessCDNServer}/public/800/h5/mbank/Common/m-browse-countdown-chest.png`,
            //进度条
            progress: "0",
            //当前倒计时
            countdown: 0,
            //倒计时
            timer: null,
            //完成倒计时
            finish: false,

            //拖动相关
            sourceElTop: 0, //点击时$el的top值
            sourceClientY: 0, //点击时鼠标的top值

            //初始页面功能码
            menuCd: "",
            //初始页面是否为频道页
            isFloorHome: false
        };
    };
    onCreated() {
        this.countdown = Number(this.watchTm);
        //设置倒计时
        this.setCountdown();
        //初始化功能码、判断是否为频道页
        window.$G.getMenuId().then(MenuId => {
            this.menuCd = MenuId;
            App.local.get("floorNavData").then(floorNavData => {
                this.isFloorHome = floorNavData.navSources.some(item => item.bsnCode == MenuId);
            });
        });

        //监听路由变化
        this.onGlobal("router:after", () => {
            this.routerAfter();
        });
    }
    methods = {
        /**
         * 设置倒计时
         */
        setCountdown() {
            if (this.finish) return;
            this.timer = setTimeout(() => {
                this.countdown--;
                this.progress = `${(this.watchTm - this.countdown) / this.watchTm}`;

                if (this.countdown <= 0) {
                    //完成
                    this.record();
                } else {
                    //继续倒计时
                    this.setCountdown();
                }
            }, 1000);
        },
        /**
         * 切换页面后，此方法在routerSetting中调用
         */
        routerAfter() {
            //如果浏览的初始页面是频道页
            if (this.isFloorHome) {
                //暂停定时器
                clearTimeout(this.timer);
                this.timer = null;
                window.$G.getMenuId().then(MenuId => {
                    //是原页面，恢复执行
                    if (this.menuCd == MenuId) {
                        this.setCountdown();
                    }
                });
            }
        },
        // 拖动-开始点击
        touchstart(e) {
            //元素初始top值
            this.sourceTop = this?.$el?.getBoundingClientRect().top;
            //拖动时鼠标初始位置
            this.sourceClientY = e.targetTouches[0].clientY;
        },
        // 拖动-移动中
        touchmove(e) {
            // y轴移动值
            let yValue = e.targetTouches[0].clientY - this.sourceClientY;
            //修改元素top值
            this.$el.style.top = this.sourceTop + yValue + "px";
        },
        // 完成后点击，关闭webview
        click() {
            if (this.finish) {
                window.$G.backMktplatWeb(() => {
                    App.router.back();
                });
            }
        },
        /**
         * 倒计时完成，记录
         */
        record() {
            this.request("cmm/mkt/mktrcd.biz", {
                data: {
                    watchTm: `${this.watchTm}`,
                    menuCd: this.menuCd,
                    chnlPdNo: this.chnlPdNo
                }
            }).then(() => {
                this.finish = true;
            });
        }
    };
}

export default async function install(App, ViewBase) {
    let viewOption = await new BrowseCountdown()._transformParam({});
    let BrowseCountdownComponent = ViewBase.extend(viewOption);

    ViewBase.prototype.$browseCountdown = function(option) {
        let tempContainer = document.createElement("div");
        document.body.appendChild(tempContainer);

        let ins = new BrowseCountdownComponent({
            el: tempContainer,
            propsData: option
        });

        return ins;
    };
}
