/**
 * @Date: 2021/10/29
 * @Author: Bus lupe
 * 基于big.js实现加减乘除数字运算，解决js的精度问题
 * import { plus, minus, times, div } from "~/common/js/calculation.js";
 */

let Big = require("big.js");

/**
 * 工厂函数
 */

function factory() {
    let args = Array.from(arguments);
    let key = args.shift(); // 运算类型: puls、minus、times、div
    let ret = new Big(args.shift()); // 参与运算的第一项，转成Big实例
    for (let i = 0; i < args.length; i++) {
        // for循环将剩余的项进行运算
        ret = ret[key](args[i]);
    }
    return ret.toString();
}

/**
 * 加法运算
 * 接收多个参数，返回计算后的结果
 * plus(0.1, 0.2) // 0.3
 */
export function plus() {
    return factory("plus", ...arguments);
}

/**
 * 减法运算
 * 接受多个参数，返回计算后的结果
 * minus(0.3, 0.1) // 0.2
 */
export function minus() {
    return factory("minus", ...arguments);
}

/**
 * 乘法运算
 * 接收多个参数，返回计算后的结果
 * times(0.6, 3) // 1.8
 */
export function times() {
    return factory("times", ...arguments);
}

/**
 * 除法运算
 * 接收多个参数，返回计算后的结果
 * div(295346.86, 10000) // 29.534686
 */
export function div() {
    return factory("div", ...arguments);
}

/**
 * 取余数运算
 * 接收多个参数，返回计算后的结果
 * mod(3.03, 1.01) // 0
 */
export function mod(){
    return factory("mod", ...arguments);
}
