/**
 * 806微信基金走wap服务
 * 微信路由配置信息
 *
 * beforeEnter 是806微信基金业务逻辑，如果后期806基金要上线6.0，相关逻辑可移动到基金模块
 */
import { transferUrl } from "~/common/js/filters.js"; //过滤器
import { ViewBase } from "@ehas/core";
import "@components/m-pdf/Index.js";

const wxRouterConfig = {
    //持仓详情
    "#Fund/Position/Modify": {
        /**
         * 当路由从key跳转至ignoreNextList列表中某路由时，忽略当前历史
         * 防止交易已完成，页面获取已过期参数
         */
        ignoreNextList: [
            //基金转换
            "#Fund/Position/Conversion",
            //基金赎回
            "#Fund/Position/Redeeming"
        ]
    },
    //定投详情
    "#Fund/Detail/My": {
        ignoreNextList: [
            //定投修改
            "#Fund/Buying/AIP"
        ]
    },
    //基金搜索
    "#Fund/Search/Index": {
        /**
         * 正常进入基金搜索时页面无参数，基金pk进入时有参数，且正常进入时不能用基金pk页面的参数
         * 因此不传参数时覆盖原缓存参数
         */
        defaultEmptyObjectParam: true
    },
    //基金风评页面
    "#Fund/RiskAssessment/Questions": {
        beforeEnter: async () => {
            //判断是否已进行风评
            const rskRsp = await App.requester.request(
                //接口
                "fud/fundserve/risklevelqry.biz",
                {
                    data: {
                        // 1.公墓基金（普通基金） 3.券商集合（券商资管）
                        busnTypCd: "1",
                        //0普通基金 1理财 B资管类 C类余额宝
                        busnCd: "0"
                    }
                }
            );
            //0当日不可以风评 1 当日可以
            if (rskRsp.busnFlg == "0") {
                ViewBase.prototype.$alert("今天您已进行风险评估，不能再次操作。");
                return false;
            }

            //进入风评答题页面前需先阅读协议
            const rsp = await App.requester.request("fud/detail/fundpdagrlistqry.biz");
            const riskAgreement = rsp.dctParamEntityList.find(item => item.dctVal == "FPXZ");
            //需要协议但 未找到协议
            if (!riskAgreement) {
                window.$G.logErrorWithCode(
                    "common/js/wapWeChatRouter.js《个人客户风险承受能力评估问卷使用须知》-获取协议失败",
                    `基金进入风评答题页面前需先阅读协议《个人客户风险承受能力评估问卷使用须知》-获取协议失败:fud/detail/fundpdagrlistqry.biz返回dctVal != FPXZ`
                );
                ViewBase.prototype.$alert("获取《个人客户风险承受能力评估问卷使用须知》失败，请稍后再试！");
                return false;
            }
            return new Promise(resolve => {
                //答题之前需要先阅读协议
                ViewBase.prototype.$previewPdf({
                    url: transferUrl(riskAgreement.dctMpngVal),
                    title: riskAgreement.dctRvlNm,
                    timer: 5000,
                    forceEveryTime: false,
                    hideBtnAfterRead: false,
                    confirm: () => {
                        resolve(true);
                    }
                });
            });
        }
    },
    //基金签约页面
    "#Fund/Services/Signing": {
        beforeEnter: async () => {
            //用户签约信息
            const custinfo = await App.requester.request("fud/fundserve/custinfoqry.biz", {
                data: {
                    // 客户信息类型  1.公墓基金 3.券商集合 APP6.0一期固定送1
                    busnTypCd: "1"
                }
            });
            // 签约修改正常跳转
            if (custinfo.sts === "0") {
                return true;
            }

            // 首次签约时需阅读权益须知
            const rsp = await App.requester.request("fud/detail/fundpdagrlistqry.biz");
            const rightsNotice = rsp.dctParamEntityList.find(item => item.dctVal == "QYXZ");
            // 需要协议但 未找到协议
            if (!rightsNotice) {
                window.$G.logErrorWithCode(
                    "common/js/wapWeChatRouter.js《基金投资人权益须知》-获取协议失败",
                    "基金首次签约时需阅《基金投资人权益须知》-获取协议失败:fud/detail/fundpdagrlistqry.biz返回dctVal != QYXZ"
                );
                ViewBase.prototype.$alert("获取《基金投资人权益须知》失败，请稍后再试！");
                return false;
            }
            // 协议弹窗
            return new Promise(resolve => {
                ViewBase.prototype.$previewPdf({
                    url: transferUrl(rightsNotice.dctMpngVal),
                    title: rightsNotice.dctRvlNm,
                    timer: 0,
                    confirm: () => {
                        resolve(true);
                    }
                });
            });
        }
    }
};
/**
 *  忽略当前历史的跳转
 *  相当于 App.router.go(router, { igSelf: true });
 *  传参采用缓存，跳转采用history.replaceState + location.reload
 * @param {*} router 哈希路由 '#Fund/Index/Index'
 * @param {*} pageParam 路由参数
 */
function goToNextIgnore(router) {
    //跳转：历史路由不增加 && 跳转目标页面
    history.replaceState("", "", location.href.replace(location.hash, router));
    location.reload();
}
/**
 * 缓存下一路由参数
 * @param {*} router 路由
 * @param {*} pageParam 参数
 */
function cacheRouterParam(router, pageParam = null) {
    //获取路由存储
    const paramStore = App.storage.get("WXRouterParam") || {};
    //key为目标路由，value为路由参数
    paramStore[router] = pageParam;

    App.storage.remove("WXRouterParam");
    App.storage.set("WXRouterParam", paramStore);
}
/**
 * 获取路由参数
 * src\config\vueMixinSetting.js 页面使用
 */
function getRouterParam() {
    const paramStore = App.storage.get("WXRouterParam") || {};
    //返回和当前路由匹配的路由参数
    return paramStore[location.hash] || null;
}
/**
 * 当前页面是否忽略下一页面的路由
 * 防止交易已完成，页面获取已过期参数
 * @param {*} router 下一页面的路由
 */
function isIgnoreRouter(router) {
    return wxRouterConfig[location.hash]?.ignoreNextList?.includes(router);
}

/**
 * 初始化路由跳转配置
 */
function initWXRouter() {
    // 原来的方法
    const originAppRouterGo = App.router.go;
    // 重写App.router.go方法
    App.router.go = async function(router, options = null, ...args) {
        //没加#手动添加
        router = router.replace(/^#?/, "#");

        //如果有进入前校验
        if (wxRouterConfig[router]?.beforeEnter) {
            /**
             * goToNextIgnore进入页后也会调用一次此方法，并且router == location.hash，消除重复调用beforeEnter的问题
             * 进入前校验不通过不跳转
             */
            if (router != location.hash && !(await wxRouterConfig[router]?.beforeEnter())) {
                return;
            }
        }

        //配置了默认空对象参数,用于不传参数时覆盖原缓存参数
        if (wxRouterConfig[router]?.defaultEmptyObjectParam) {
            options = options || {};
            options.pageParam = options.pageParam || {};
        }

        //缓存路由参数
        options?.pageParam && cacheRouterParam(router, options?.pageParam);

        //设置了忽略下一页面，或者在配置忽略路由列表中
        if (options?.igSelf || isIgnoreRouter(router)) {
            //忽略历史路由时，使用重写的方法
            goToNextIgnore(router, options?.pageParam);
        } else {
            //其他情况使用 原来的方法
            originAppRouterGo.call(App.router, router, options, ...args);
        }
    };

    // 重写返回逻辑
    App.router.back = function() {
        window.history.back();
    };
}

export { getRouterParam, initWXRouter };
