import { isLogin, isNewStartup } from "./check"; // 工具函数
import { ViewBase } from "@ehas/core";
const setSimpmbankchg = simpFlg => {
    let autoChangeSimple = simpFlg == "N" ? "Y" : "N";
    App.globalFlag.simpleFlag = simpFlg;
    App.globalFlag.autoChangeSimple = autoChangeSimple;
    App.local
        .set("m_version_info", { simpFlg: simpFlg, huinong: false, autoChangeSimple: autoChangeSimple })
        .then(() => {
            // 上线字体大小调整功能的行，切换到简版需要自动变更为大字版
            // 866 生产已上线字体大小功能，合并处理
            let existCustomFont = null;
            window.App.menu
                .fetchMenuList("100000018")
                .then(res => {
                    existCustomFont = res.find(item => {
                        return item.id == "MBUSR021400";
                    });
                    // 809单独处理了切换到简版放大字体，切换到简版后不再开启大字版
                    if (App.globalFlag && App.globalFlag.simpleFlag === "Y" && !App.bank.flags?.common?.dzFontSizeFlg) {
                        if (existCustomFont) {
                            setFontSize("1");
                        }
                        _toSimple();
                    } else {
                        if (existCustomFont) {
                            setFontSize("0");
                        }
                        _toStandard();
                    }
                })
                .catch(() => {
                    if (App.globalFlag && App.globalFlag.simpleFlag === "Y") {
                        _toSimple();
                    } else {
                        _toStandard();
                    }
                });
        });
};
export function setFontSize(size) {
    App.local.remove("customFontSize");
    App.local.set("customFontSize", { key: size, value: size });
    document.body.setAttribute("data-size", size);
    isNewStartup() && App.jsApi.changeTypeSize({size: size})
}
function _toSimple() {
    _factory("#SimpleHome/Home/Index", "2");
}

function _toStandard() {
    _factory("#FloorHome/Home/Index", "1");
}

function _factory(path, styleVersion) {
    if (isNewStartup()) {
        App.jsApi.changeStyleVersion({
            path: path,
            styleVersion: styleVersion
        });
    } else {
        window.$G.goToNext(path);
    }
}
const sendSimpmbankchg = simpFlg => {
    simpFlg = simpFlg ? "N" : "Y";
    isLogin().then(res => {
        if (res) {
            App.requester
                .request("usr/simpmbank/simpmbankchg.biz", {
                    data: {
                        simpFlg: simpFlg
                    }
                })
                .then(() => {
                    setSimpmbankchg(simpFlg);
                });
        } else {
            setSimpmbankchg(simpFlg);
        }
    });
};

export function versionSwitch() {
    if (App.bank.flags?.common?.huinong) {
        return ViewBase.prototype.$versionSwitch();
    } else {
        const simpleFlag = App.globalFlag.simpleFlag == "Y" ? true : false;
        const simpleVersionTitle = App.bank.flags.mine?.simpleVersionTitle || "简爱版";
        const message = simpleFlag ? "即将为您切换回标准版" : `即将为您切换${simpleVersionTitle}`;
        return ViewBase.prototype.$confirm({
            title: "",
            message: message,
            sureText: "确认",
            cancelText: "取消",
            closed: isSure => {
                if (isSure) {
                    window.$G.getMenuId().then(MenuId => {
                        App.DC.onEvent(MenuId + "_014");
                    });
                    sendSimpmbankchg(simpleFlag);
                }
            }
        });
    }
}
export function versionSwitchWithoutDialog() {
    const simpleFlag = App.globalFlag.simpleFlag == "Y" ? true : false;
    window.$G.getMenuId().then(MenuId => {
        App.DC.onEvent(MenuId + "_014");
    });
    sendSimpmbankchg(simpleFlag);
}
