import { dateUtils } from "@ehas/utils";
import { Log } from "@ehas/core";
import "@ehas/app-jsapi-native";
import jsApi from "@ehas/app-jsapi";
import { globalUtil } from "../common/js/globalUtil";
// 启动
window.startTime = globalUtil.getCurrentTime();
// 可保存时间线的标志
window.saveStartTimelineFlag = true;

/**
 * 调用globalUtils下面的函数。
 * 生产上报globalUtil.pushStartupTimeLine is not a function，原因未知，故加此方法检查。
 * @param {*} functionName 函数名称
 * @param {*} args 函数调用的参数
 * @param {*} env 函数执行环境
 */
function callFuntion(args, functionName = globalUtil.pushStartupTimeLine, env = globalUtil) {
    if (typeof functionName == "function") functionName.call(env, ...args);
}

// 启动时间监听标志
let bindStartupEventFlag = false;

export default {
    renewApp: null,
    /**
     * 客户端秒开，加载缓存的页面。
     * 判断有没有缓存页面，若有缓存，则将缓存加载至iframe中，并在3s后删除iframe节点，展示真实页面。
     * 若无缓存，直接删除iframe节点，展示真实页面加载过程。
     *
     * 注：startupReady 0未启动 1已启动
     */
    loadHomePage() {
        let matchStart = navigator.userAgent.match(/@startupReady\((\d+)\)/);
        const startupReady = matchStart && matchStart[1];

        let matchNet = navigator.userAgent.match(/@networkStatus\((\d+)\)/);
        const networkStatus = matchNet && matchNet[1];

        //获取客户端版本startupVer(2) 表示新版
        let matchVer = navigator.userAgent.match(/@startupVer\((\d+)\)/);
        const startupVer = matchVer && matchVer[1];
        // 推送时间线给客户端
        callFuntion(["prepare", "启动前准备", window.startTime]);
        // 渲染iframe镜像   -------2022年02月21日 启动逻辑优化，去掉之前H5启动镜像逻辑
        // this.renderIframe();

        // 未启动 且 有网的时候监听启动状态的变更、无网的时候直接渲染镜像
        if (startupReady == "0" && networkStatus == "1") {
            console.info(
                `${dateUtils.nowDate("HH:mm:ss:SSS")}-【mbank-H5】启动状态为0且网络状态为1,等待客户端通知变更`
            );
            this.bindReadyStartupStatus();
        } else if (networkStatus == "0") {
            //老版客户端需要重新获取网络状态，有两种情况 startupVer不存在或startupVer=1
            if (!startupVer || startupVer == "1") {
                console.info(`${dateUtils.nowDate("HH:mm:ss:SSS")}-【mbank-H5】老版客户端，重新获取网络状态`);
                jsApi
                    .getCurrentNetworkType()
                    .then(rsp => {
                        if (rsp.result.networkType != "NOTREACHABLE") {
                            if (startupReady == "0") {
                                console.info(
                                    `${dateUtils.nowDate(
                                        "HH:mm:ss:SSS"
                                    )}-【mbank-H5】老版客户端，启动状态为0且网络状态为1,等待客户端通知变更`
                                );
                                //有网，未启动
                                this.bindReadyStartupStatus();
                            } else {
                                console.info(
                                    `${dateUtils.nowDate(
                                        "HH:mm:ss:SSS"
                                    )}-【mbank-H5】老版客户端，有网且客户端已准备完毕，实例化APP`
                                );
                                //有网，启动完成
                                this.renewApp();
                            }
                        } else {
                            console.info(
                                `${dateUtils.nowDate("HH:mm:ss:SSS")}-【mbank-H5】老版客户端，无网络，监听网络变化`
                            );
                            //获取网络状态失败，按无网处理
                            this.bindNetwordStatus(startupReady);
                        }
                    })
                    .catch(() => {
                        console.info(
                            `${dateUtils.nowDate(
                                "HH:mm:ss:SSS"
                            )}-【mbank-H5】老版客户端，获取网络状态异常，监听网络变化`
                        );
                        //获取网络状态失败，按无网处理
                        this.bindNetwordStatus(startupReady);
                    });
            } else {
                console.info(`${dateUtils.nowDate("HH:mm:ss:SSS")}-【mbank-H5】新版客户端，无网络，监听网络变化`);

                // 无网时监听网络变化，有网后去实例化
                this.bindNetwordStatus(startupReady);
            }
        } else {
            console.info(`${dateUtils.nowDate("HH:mm:ss:SSS")}-【mbank-H5】有网且客户端已准备完毕，实例化APP`);
            this.renewApp();
        }
    },

    /**
     * 渲染镜像页面
     */
    renderIframe() {
        let iframe = document.querySelector("#catchlayout");
        let flag = window.$G.startByHtmlCache;
        // 秒开开关不开，不渲染镜像
        if (!flag || location.hash !== "") {
            iframe?.remove();
            console.info(`${dateUtils.nowDate("HH:mm:ss:SSS")}：【mbank-H5】秒开开关为${flag},hash:${location.hash}`);
            return;
        }

        let matchNet = navigator.userAgent.match(/@networkStatus\((\d+)\)/);
        const networkStatus = matchNet && matchNet[1];

        const localHtml = localStorage.getItem("localHtml");
        const baseFontSize = localStorage.getItem("baseFontSize");
        // 未启动完成，有缓存，加载缓存
        if (localHtml && baseFontSize) {
            // 为镜像页面iframe添加内容
            this.setIframeContent(localHtml, baseFontSize, networkStatus);
            if (networkStatus != "0") {
                // 未启动完成且有网
                setTimeout(() => {
                    iframe?.remove();
                    console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}：【mbank-H5】有镜像-removed iframe`);
                }, 5000);
            } else {
                console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}：【mbank-H5】有镜像-无网-iframe stay`);
            }
        } else {
            // 未启动完成，无缓存，展示骨架屏，待楼层组件挂载完成后展示展示页面
            if (networkStatus == "0") {
                console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}：【mbank-H5】无镜像-无网-跳转诊断页`);
                iframe?.remove();
                // 无网无缓存，直接跳网络诊断页
                jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });
            }
        }
    },
    /**
     * 为镜像页面iframe添加内容
     * @param {*} localHtml  上次运行时缓存的dom节点
     * @param {*} baseFontSize  缓存Html的基础字体大小，用于转换rem
     * @param {*} networkStatus  网络状态
     */
    async setIframeContent(localHtml, baseFontSize, networkStatus) {
        let iframe = document.querySelector("#catchlayout");
        if (!iframe) return;
        console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}-【mbank-H5】开始填充iframe内容`);
        iframe.contentWindow.document.querySelector("html").style.fontSize = baseFontSize;
        iframe.contentWindow.document.querySelector("html").setAttribute("class", "hairlines");
        iframe.contentWindow.document.querySelector("html").setAttribute("lang", "zh-CN");
        iframe.contentWindow.document.documentElement.innerHTML = localHtml;
        let bodyHtml = iframe.contentWindow.document.querySelector("body").innerHTML;
        iframe.contentWindow.document.querySelector("body").innerHTML = "";
        let PromiseArr = [];
        iframe.contentWindow.document.querySelectorAll("link")?.forEach(link => {
            let promise = new Promise((resolve, reject) => {
                link.onload = function () {
                    resolve();
                };
                link.onerror = function () {
                    reject();
                    console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}-【mbank-H5】${link.href}加载失败`);
                };
            });
            PromiseArr.push(promise);
        });
        // css文件加载完成后展示dom内容，避免展示乱象
        await Promise.all(PromiseArr)
            .then(() => {
                iframe.contentWindow.document.querySelector("body").innerHTML = bodyHtml;
                console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}-【mbank-H5】iframe内容已填充`);
                iframe.style.display = "block";
            })
            .catch(() => {
                // 无网且样式加载失败时直接跳网络诊断页面
                if (networkStatus == "0") {
                    jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });
                }
            });
        // 绑定镜像的点击事件,无网时点击调诊断页
        if (networkStatus == "0") {
            iframe.contentWindow.document.querySelector("body").onclick = function () {
                jsApi.openWebPageByWebContainer({ url: "./network_exception_boot_page.html" });
            };
        }
        // iframe的body禁止滑动，防止滑动至真实页面
        iframe.contentWindow.document.querySelector("body").style.touchAction = "none";
        iframe.contentWindow.document.querySelector("#layout").removeAttribute("tag-id");
        // 禁止滑动盒子波及相邻元素跟着滑动
        iframe.contentWindow.document.querySelectorAll(".templateListScroller").forEach(item => {
            item.style.touchAction = "unset";
            item.style.overflow = "auto";
            item.style.overscrollBehavior = "none";
            item.style.webkitOverflowScrolling = "touch";
        });
        // 禁止滑动盒子波及相邻元素跟着滑动
        iframe.contentWindow.document.querySelectorAll(".ehas-scroll").forEach(item => {
            item.style.touchAction = "unset";
            item.style.overflow = "auto";
            item.style.overscrollBehavior = "none";
            item.style.webkitOverflowScrolling = "touch";
        });
    },

    /**
     * 监听启动状态变更
     */
    bindReadyStartupStatus() {
        const startTime = globalUtil.getCurrentTime();
        // 有网的时候监听启动状态的变更、无网的时候直接渲染镜像
        jsApi.once("onReadyStartup", () => {
            console.info(`${dateUtils.nowDate("HH:mm:ss:SSS")}-【mbank-H5】接收到onReadyStartup事件通知，程序继续执行`);
            // 推送时间线给客户端
            callFuntion(["onReadyStartupListener", "监听启动状态变更", startTime]);
            // 未启动H5。等待启动完成继续启动。
            this.renewApp();
        });
        bindStartupEventFlag = true;
    },
    // 无网时监听网络变化
    bindNetwordStatus() {
        const startTime = globalUtil.getCurrentTime();
        jsApi.once("onNetworkStatus", data => {
            let networkStatus = data.result.networkStatus;
            console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}-【mbank-H5】监听到网络状态变更为${networkStatus}`);

            // 有网且iframe存在才去实例化Application
            if (networkStatus == "1" || networkStatus) {
                let matchStart = navigator.userAgent.match(/@startupReady\((\d+)\)/);
                const startupReady = matchStart && matchStart[1];
                console.log(
                    `${dateUtils.nowDate("HH:mm:ss.SSS")}-【mbank-H5】网络变更后记录startupReady: ${startupReady}`
                );
                // 推送时间线给客户端
                callFuntion(["onNetworkStatusListener", "监听网络状态事件", startTime]);
                // 网络恢复后，若公共包对比仍未完成，则等待公共包完成
                if (startupReady == "1") {
                    console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}-【mbank-H5】网络恢复重新初始化APP`);
                    // 实例化Application
                    this.renewApp();
                } else {
                    console.info(
                        `${dateUtils.nowDate(
                            "HH:mm:ss.SSS"
                        )}-【mbank-H5】startupReady不为1，需监听onReadyStartup事件，等待通知`
                    );
                    this.bindReadyStartupStatus();
                }
            }
        });
    }
};

if (window.LMJSSDK) {
    window.LMJSSDK.eventExceptionCallback = function (param = {}) {
        if (!bindStartupEventFlag) return;
        const startupUsedEventNames = ["onReadyStartup"];
        if (location.hash === "" && startupUsedEventNames.indexOf(param.eventName) > -1) {
            // 上送APM收集异常信息
            Log.warnWithCode("EPFH018W", "启动异常警告", param.msg);

            if (ehasProcessIsProduct) return;

            // 非生产环境弹窗提示
            jsApi.appLaunchPopDialog({
                title: "启动异常",
                desc: param.msg,
                buttonList: ["确定"]
            });
        }
    };
}
