/**
 * 当前行展示信用卡还是公务卡
 */
function showCreditCardTypeName() {
    return App.bank.flags?.account?.mCardTypeNameFlag ? "信用卡" : "公务卡";
}

/**
 * 处理getPayAcctList中的数据
 */

/* eslint-disable */
function dealList(tmpList, acctList, abledFlag) {
    for (let item of tmpList) {
        let unionpay = showUnionPayIcon(item.acctNoTyp) ? "(银联)" : "";
        item.acctNoType = item.acctNoTyp; //因acctNoTyp被覆盖，新增字段保留账户类型原始数据
        //后面处理把该字段覆盖了，但是页面需要使用，新增备份字段
        let _acctNoTyp = item.acctNoTyp;
        if (item.acctNoTyp == "01") {
            let acctLvlCd = "";
            if (item.acctLvlCd) {
                acctLvlCd = App.dict.get("f-acctAllLvlCd", item.acctLvlCd);
            }
            //本行借记卡，不显示借记卡字段，只显示账户等级名称
            item.acctNoTyp = unionpay + acctLvlCd;
        } else {
            item.acctNoTyp = unionpay + App.dict.get("f-accountTypShow", item.acctNoTyp);
        }
        let obj = {
            _acctNoTyp: _acctNoTyp,
            card: item.bkShortNm,
            cardNum: item.acctNo,
            usable: true,
            type: item.bkShortNm + item.acctNoTyp,
            logo: item.iconUrl,
            bankNo: item.cnapsBkNo,
            acctStsDtl: item.acctStsDtl, //账户状态明细
            acctLvlCd: item.acctLvlCd, //账户等级
            dfltAcctFlg: item.dfltAcctFlg,
            //2.0组件数据映射
            value: fmtSensitiveData(item.acctNo), //显示标题
            key: item.acctNo, //卡号
            tip: item.bkShortNm + item.acctNoTyp, //描述
            unionBankNo: item.cnapsBkNo, //联行号
            //新增机构号获取
            saOpenBusn: item.saOpenBusn, //开户机构号
            ...item
        };
        if (item.acctAuthFlg === "Y" && item.acctSts === "0") {
            //正常状态
            obj.usable = true; //1.0组件使用
            obj.disabled = false; //2.0组件使用
            obj.type = item.bkShortNm + item.acctNoTyp;
            obj.tip = item.bkShortNm + item.acctNoTyp;
        } else {
            if (abledFlag) {
                obj.usable = true; //1.0组件使用
                obj.disabled = false; //2.0组件使用
            } else {
                //异常状态
                obj.usable = false; //1.0组件使用
                obj.disabled = true; //2.0组件使用
            }
            obj.type = item.bkShortNm + " " + "账户状态异常";
            obj.tip = item.bkShortNm + " " + "账户状态异常";
        }
        acctList.push(obj);
    }
}
/* eslint-enable */

/**
 * 使用场景: 手机号转账、银行卡转账、预约转账、慈善捐款的付款账户列表
 * 获取转账列表 可用和不可用的卡
 * @param {*} txCd 交易码: TRN000100 银行账号转账,TRN000200 手机号转账,TRN000300 预约转账,TRN000400 慈善捐款
 * * @param {*} acctAuthFltFlg 账户权限过滤标识,可送可不送，当需要过滤不符合权限的账户时，送Y
 * @returns {*} obj
 * @param {*}abledFlag 支持有些需求账号异常也可以选择不仅仅是展示作用
 */
function getPayAcctList(txCd, acctAuthFltFlg = "N", abledFlag) {
    let result = {};
    let acctList = [];
    return App.requester
        .request("act/acct/acctlistqry.biz", {
            data: {
                txCd: txCd,
                acctAuthFltFlg: acctAuthFltFlg
            }
        })
        .then(rsp => {
            const tmpList = rsp.LIST;
            dealList(tmpList, acctList, abledFlag);
            result = {
                chooseCard: [true],
                chooseFlag: false,
                payerAccountList: acctList,
                userName: rsp.userNm,
                //用户等级
                userLvl: rsp.userLvl
            };
            return result;
        });
}

/**
 * 查询可下挂账户列表
 * @returns {*} txCd 交易码，上送该字段，则只返回未加挂正常卡列表，否则返回全部未挂卡列表
 */
function getTkDnList(txCd) {
    return App.requester
        .request("act/acct/acctlistqry4cantkgdwn.biz", {
            data: {
                txCd: txCd
            }
        })
        .then(rsp => {
            const noAccountList = rsp.LIST;
            for (let item of noAccountList) {
                // ui 1.0
                item.cardNum = getSensitiveData(item.acctNo);

                item.usable = true;
                item.logo = item.iconUrl;
                // ui2.0
                item.disabled = false;
                item.unionBankNo = item.acctNo;
                if (item.acctLvlCd == 1) {
                    item.acctLvlCd = "Ⅰ类户";
                } else if (item.acctLvlCd == 2) {
                    item.acctLvlCd = "Ⅱ类户";
                } else if (item.acctLvlCd == 3) {
                    item.acctLvlCd = "Ⅲ类户";
                }
                item.type = item.bkShortNm + item.acctLvlCd;
            }
            return noAccountList;
        });
}

/**
 * 使用正则匹配后端加密串，将加密串截取为如下样式
 * 如截取前为：!@6223*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F
 * 截取后为一个数组：
 * [0:"!@6223*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F"
 * 1:"6223*****2027"
 * 2:"4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F"]
 *
 * 使用正则匹配后端加密串，将加密串截取为如下样式
 * 如截取前为：!@622312*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F
 * 截取后为一个数组：
 * [0:"!@622312*****2027|4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F"
 * 1:"622312*****2027"
 * 2:"4F74726E6751567765424765645249346D65434849736D7937656F4D6C486F6F"]
 * */
function getSensitiveData(value, cryptograph) {
    let matches = {
        pattern: /^!@([^|]+)\|([^|]+)/
    };
    //match 方法不支持数字类型
    if (typeof value === "number") {
        value = value.toString();
    }
    //校验数据是否符合脱敏数据规则
    let vals = value ? value.match(matches.pattern) : "";
    //此处判断是否需要返回密文串，需根据后端需求进行设置
    if (cryptograph) {
        return vals !== null && vals[2] ? vals[2] : value;
    } else {
        return vals !== null && vals[1] ? vals[1] : value;
    }
}

/**
 *
 *  * 涉及字段：
 *  1.1 acctNoTyp 账户类型：
 *   01 本行借记卡
 *   02 本行公务卡
 *   03 他行卡
 *   04 活折
 *   05 定活一本通
 *   06 定期一本通
 *   07 定期存单
 *   08 零整/教育；
 *   09 内部户
 *   10 贷款
 *   11 存本取息
 *   12 整存零取
 *   CX 互联网核心账户
 *  1.2 acctLvlCd  账户级别 判断 一二三类
 *      1 I类户
 *      2 II类户
 *      3 III类户
 *  1.3 crEacctFlg 电子账户标识 0实体账户 1电子账户
 *  1.4 acctOpenChnlNo 01：柜签 非01：网签
 *  1.5 spcCrdTypCd  01:信用卡 02:公务卡 03:互联网核心独立发行的银行卡 20:儿童卡 21:社会保障金融卡 30:社会保障卡
 *  2、展示规则
 *  展示借记卡（类）、借记卡（类）、借记卡（类） 涉及字段：1.1，1.2，1.3
 *  存折、信用卡、公务卡
 *  页面展示用
 *  本行借记卡 判断借记卡  acctLvlCd判断 一二三类 借记卡（类）、借记卡（类）、借记卡（类）
 *  存折 04 05  活期结算、活期储蓄快捷功能待定
 *  信用卡公务卡利用后端spcCrdTypCd字段判断，不在前端做判断
 *
 *  3 调用处
 *  3.1、快捷操作及账户管理操作分类 唯一一份 涉及字段：1.1，1.2，1.3 1.4
 *  返回账户分类 mCardType
 *  "depositCard"    :本行储蓄卡，存折  转账汇款、买理财、明细查询
 *  "eAccountCounter":柜签II/III类户， 转账汇款、买理财、明细查询
 *  "eAccountNet"    :网签II/III类户， 充值、提现、买理财
 *  "creditCart"     :信用卡，         去还款、账单明细
 *  "otherCard"      :他行卡           无
 *
 *  3.2、挂失 账户类型选择 解挂失 卡片展示 mCardTypeName
 *  defaultCardFlag  有此值的代表可以设置成默认卡
 */
/* eslint-disable */
function cardTypeClassification(item) {
    // 增加健壮性处理
    let level = App.dict.get("f-acctLvlCd", item.acctLvlCd);
    level = !level ? "" : `${level}户`;
    switch (item.acctNoTyp) {
        case "01":
            item.defaultCardFlag = true;

            // “21社会保障金融卡”，直接显示为“社保卡”
            if (item.spcCrdTypCd == "21") {
                item.mCardTypeName = "(银联)社保卡";
                //是否展示借记卡销户入口，true为不展示
                item.cancelDebitCardShow = true;
            } else {
                item.mCardTypeName = "(银联)借记卡" + level;
            }
            if (item.crEacctFlg == "0") {
                //医保卡单独处理快捷菜单和名称
                if (item.spcCrdTypCd == "30") {
                    //是否展示借记卡销户入口，true为不展示
                    item.cancelDebitCardShow = true;
                    //医保卡是否展示查看卡号标识
                    App.bank.flags.account.showAcctNoCheckFlag && (item.showAcctNoCheck = true);
                    item.mCardTypeName = "(银联)医保卡";
                    item.mCardType = "medicalInsurance";
                } else {
                    if (item.acctLvlCd == "1") {
                        item.mCardType = "depositCardLvl";
                    } else {
                        item.mCardType = "depositCard";
                    }
                }
            } else if (item.crEacctFlg == "1") {
                //对于0-实体账户的，前端展示借记卡（I/II/III类）
                //对于1-电子账户的，前端展示电子账户（II/III类）
                //mCardTypeNameDiff == "1"   目前815行电子账户一类户  名称多法人处理
                if (item.acctLvlCd == "1" && App.bank.flags.account?.mCardTypeNameDiff == "1") {
                    item.mCardTypeName = "(银联)无介质账户" + level;
                } else {
                    item.mCardTypeName = "(银联)电子账户" + level;
                }
                // if (item.acctOpenChnlNo == "01") {
                //     item.mCardType = "eAccountCounter";
                // } else {
                // 办理渠道;
                let openChnlNo = App.bank.flags.account?.thirdAcctList
                    ? [...App.bank.flags.account?.thirdAcctList]
                    : [];
                //74   渠道目前  威海视频银行开出来的属于电子账户
                openChnlNo.push(...["03", "49", "74"]);
                if (openChnlNo.indexOf(item.acctOpenChnlNo) < 0) {
                    // 柜签电子账户  渠道参数不在数组里是柜签账户
                    if (item.acctLvlCd == "1") {
                        // 电子账户（I类）
                        item.mCardType = "depositCardLvl";
                    } else {
                        //柜签电子账户三类户设置标识
                        if (item.acctLvlCd == "3") {
                            item.counterSignFlag = true;
                        }
                        item.mCardType = "depositCard";
                    }
                } else {
                    // 非柜签电子账户
                    item.mCardType = "eAccountNet";
                }
                // }
            }
            break;
        case "02":
            // item.mCardTypeName =item.spcCrdTypCd == undefined ? "" : item.spcCrdTypCd == "01"?
            let mCardTypeNameFlag = App.bank.flags.account.mCardTypeNameFlag;
            switch (item.spcCrdTypCd) {
                case undefined:
                    item.mCardTypeName = "";
                    break;
                case "01":
                    if (mCardTypeNameFlag) {
                        item.mCardTypeName = "(银联)信用卡";
                    } else {
                        item.mCardTypeName = "(银联)公务卡";
                    }
                    App.bank.flags.account.creditCardFlag && (item.defaultCardFlag = true);
                    break;
                case "02":
                    // eslint-disable-next-line no-case-declarations
                    App.bank.flags.account.creditCardFlag && (item.defaultCardFlag = true);
                    if (mCardTypeNameFlag) {
                        item.mCardTypeName = "(银联)信用卡";
                    } else {
                        item.mCardTypeName = "(银联)公务卡";
                    }
                    break;
                default:
                    item.mCardTypeName = "";
                    break;
            }
            item.mCardType = "creditCart";
            break;
        case "03":
            item.mCardTypeName = "(银联)" + (item.bkShortNm || "他行卡");
            item.mCardType = "otherCard";
            break;
        case "04":
            item.defaultCardFlag = true;
            item.mCardTypeName = "存折" + level;
            item.mCardType = "depositCard";
            break;
        case "05":
            item.defaultCardFlag = true;
            item.mCardTypeName = "存折" + level;
            item.mCardType = "depositCard";
            break;
        case "06":
            item.defaultCardFlag = true;
            item.mCardTypeName = "定期存折";
            item.mCardType = "depositReceiptCard";
            //cardClickFlag  为true 代表卡片不可点击进入账户详情
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        case "07":
            item.defaultCardFlag = true;
            item.mCardTypeName = "定期存单";
            if (item.elcTdepFlg && item.elcTdepFlg == "01") {
                item.mCardTypeName = "电子存单";
            }
            //存单暂时无操作，仅解挂失，暂时不赋值  （老备注，没敢动）
            item.mCardType = App.bank.flags.account?.cardClickFlag ? "depositReceiptCard" : "";
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        case "08":
            item.defaultCardFlag = true;
            item.mCardTypeName = "定期存折";
            item.mCardType = "depositReceiptCard";
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        case "11":
            item.defaultCardFlag = true;
            item.mCardTypeName = "定期存折";
            item.mCardType = "depositReceiptCard";
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        case "12":
            item.defaultCardFlag = true;
            item.mCardTypeName = "定期存折";
            item.mCardType = "depositReceiptCard";
            item.cardClickFlag = App.bank.flags.account?.cardClickFlag ? true : false;
            break;
        default:
            item.mCardTypeName = "";
            item.mCardType = "";
            break;
    }
}
