/**
 *
 * @param {*} rsp 转账提交结束后 错误码处理
 */
function transferFail(rsp, objInstance) {
    const erroCode = rsp.code;
    if (erroCode) {
        const errNum = erroCode.substring(5, 7); //后端返回错误码处理
        if (erroCode.substring(0, 4) == "EA1H") {
            if ((17 <= errNum && errNum <= 21) || (27 <= errNum && errNum <= 36)) {
                //限额超限
                defaultErrorFunc(rsp, "去修改", "#SafeCenter/LimitManagement/LimitManagement", null, objInstance);
            } else {
                //只提示不跳转
                window.$G.popErrorMsg(rsp);
            }
        } else {
            //移动网关错误码处理
            window.$G.popErrorMsg(rsp);
        }
    } else {
        window.$G.popErrorMsg(rsp);
    }
}
/**
 *
 * @param {*} rsp 预提交错误码处理
 */
function starttransferFail(rsp, objInstance) {
    const startCode = rsp.code;
    if (startCode.substring(0, 4) == "EA1H") {
        let codeNum = startCode.substring(5, 7);
        if ((codeNum >= 17 && codeNum <= 21) || (codeNum >= 27 && codeNum <= 36)) {
            //快速转账需要参数objInstance
            defaultErrorFunc(rsp, "去修改", "#SafeCenter/LimitManagement/LimitManagement", null, objInstance);
        } else if (codeNum >= 47 && codeNum <= 51) {
            defaultErrorFunc(rsp, "营业网点", "#BranchService/BranchInquery/Index", null, objInstance);
        } else if (codeNum >= 37 && codeNum <= 41) {
            defaultErrorFunc(rsp, "更换认证方式", "#SafeCenter/TransferSetting/TransferSettingMenu", null, objInstance);
        } else {
            // 开头为EA1H错误码 统一提示
            window.$G.popErrorMsg(rsp);
        }
    } else if (startCode == "EA1G052B") {
        defaultErrorFunc(rsp, "营业网点", "#BranchService/BranchInquery/Index", null, objInstance);
    } else if (startCode == "EA1J171B") {
        defaultErrorFunc(
            rsp,
            "前往认证",
            `Account/MyAccount/AccountManagement/acctno/${encodeURIComponent(objInstance)}`
        );
    } else if (startCode == "EA1G054B") {
        if (App.bank.flags.common?.videoBankCode) {
            //视频银行
            defaultErrorFunc(rsp, "是", App.bank.flags.common.videoBankCode, "否", objInstance, true);
        } else {
            defaultErrorFunc(rsp, "去开通", "#SafeCenter/ConfirmationSetting/Index", "暂不开通", objInstance);
        }
    } else {
        //敏感字错误码
        window.$G.popErrorMsg(rsp);
    }
}
/**
 *银联标识展示 目前确认只有卡展示银联标识
 * acctNoTyp 账户类型：
 *   01 本行借记卡
 *   02 本行公务卡
 *   03 他行卡
 *   04 活折
 *   05 定活一本通
 *   06 定期一本通
 *   07 定期存单
 *   08 零整/教育；
 *   09 内部户
 *   10 贷款
 *   11 存本取息
 *   12 整存零取
 */
function showUnionPayIcon(acctNoTyp, otbkCrdFlg) {
    if (acctNoTyp == "01" || acctNoTyp == "02" || acctNoTyp == "03" || otbkCrdFlg == "Y") {
        return true;
    } else {
        return false;
    }
}

/**
 * 引导加挂,跳转我的账户列表,参数设置在对象内。传递卡号用下面Util.fastTakingDown方法
 * @param { Boolean } keepSelfalive 是否调用goToNextRetain方法,默认true
 * @param { Boolean } goBackFlag 加挂成功后，是否返回,默认true
 * @param { string } backRouter 返回页面路径,默认当前页面
 * @param { Object } backParam 返回参数，无参数不传
 */
function addCard(config = {}) {
    //参数设置
    let dftConfig = Object.assign(
        {
            keepSelfalive: true,
            goBackFlag: true,
            backRouter: location.hash,
            backParam: undefined
        },
        config
    );
    //设置存储，加挂成功后，使用该存储
    let financingInfo = {
        flag: dftConfig.goBackFlag, //是否返回
        funUrl: dftConfig.backRouter, //返回路径
        urlParam: dftConfig.backParam //返回时参数，无参数不传
    };
    App.storage.remove("financingObj");
    App.storage.set("financingObj", financingInfo);
    //跳转参数
    let param = { backRouter: dftConfig.backRouter };
    //进行跳转
    if (dftConfig.keepSelfalive) {
        window.$G.goToNextRetain({ router: "#Account/MyAccount/MyAcctList", param });
    } else {
        window.$G.goToNext("#Account/MyAccount/MyAcctList", param);
    }
}

/**
 * 快速加挂,需要传递卡号，参数设置在对象内，不传递卡号用上面Util.addCard方法
 * @param { String } acctNo 快速加挂的加密卡号
 * @param { Boolean } keepSelfalive 是否调用goToNextRetain方法,默认true
 * @param { Boolean } goBackFlag 是否返回,默认true
 * @param { string } backRouter 返回页面路径,默认当前页面
 */
function fastTakingDown(config = {}) {
    //参数设置
    let dftConfig = Object.assign(
        {
            acctNo: "",
            keepSelfalive: true,
            goBackFlag: true,
            backRouter: location.hash
        },
        config
    );
    //跳转参数
    let param = {
        accountPath: dftConfig.backRouter, // 路由地址
        goBackFlag: dftConfig.goBackFlag, // 返回标志 true：是 false：否
        acctNo: dftConfig.acctNo, // 加挂的银行卡号
        backRouter: dftConfig.backRouter //返回路径
    };
    //进行跳转
    if (dftConfig.keepSelfalive) {
        window.$G.goToNextRetain({ router: "#Account/MyAccount/TakingDown", param });
    } else {
        window.$G.goToNext("#Account/MyAccount/TakingDown", param);
    }
}

/**
 *  判断所选电子二类户账户状态是否支持立即充值入口
 *  payAcctInf (所选的付款账户)
 *  return ture (支持)
 *
 */
function fastRecharge(payAcctInfo) {
    const { acctStsDtls = [] } = payAcctInfo;
    if (!acctStsDtls.length) return true;
    return acctStsDtls.some(item => {
        // 圈存：51 只付不收：62 部分冻结:41 （不支持）
        return !(
            (item.type === "5" && item.code === "1") ||
            (item.type === "6" && item.code === "2") ||
            (item.type === "4" && item.code === "1")
        );
    });
}

/**
 * 针对手机号和银行卡弹框操作
 */

function isPhoneCardFrame(clipWay, currClip) {
    const title = clipWay == "phoneNo" ? "您是否要向以下手机号转账" : "您是否要向以下银行卡转账";
    const account = clipWay == "phoneNo" ? phoneUtils.format(currClip) : accountUtils.format(currClip);
    const bulletText =
        clipWay == "phoneNo"
            ? `<p class="T16 LH24 Bd" style="text-align: center;">${title}</p><p class="T16 LH24 Bd" style="text-align: center;">${account}</p>`
            : `<p class="T16 LH24 Bd" style="text-align: center;">${title}</p><p class="T16 LH24 Bd" style="text-align: center;">${account}</p>`;
    if (isNewStartup()) {
        App.jsApi
            .showTransfersAlert({
                title: title,
                message: account
            })
            .then(res => {
                if (res.result?.status == "1") {
                    _toTransfer(clipWay, currClip);
                }
            });
    } else {
        // 提示用户是否进行转账
        App.getViewInstance("content").$confirm({
            type: "t3",
            message: bulletText,
            sureText: "去转账",
            cancelText: "取消",
            closed: isSure => {
                if (isSure) {
                    _toTransfer(clipWay, currClip);
                }
            }
        });
    }
}

function _toTransfer(clipWay, currClip) {
    if (clipWay == "phoneNo") {
        // 手机号转账
        window.$G.goToNext("#Transfer/Transfer/TransferByPhoneNo", {
            payeePhone: currClip
        });
    } else {
        // 银行卡转账
        window.$G.goToNext("#Transfer/Transfer/TransferByCard", {
            rcvAcctNo: currClip
        });
    }
}

/**
 * luhn校验规则：16位银行卡号（19位通用）:
 * 1.将未带校验位的 15（或18）位卡号从右依次编号 1 到 15（18），位于奇数位号上的数字乘以 2。
 * 2.将奇位乘积的个十位全部相加，再加上所有偶数位上的数字。
 * 3.将加法和加上校验位能被 10 整除。
 * @param {*} bankno 卡号
 * @returns {*} Boolean
 */
/* eslint-disable */
function bankCardValidate(bankno) {
    // 排除全部为0的账号
    if (parseInt(bankno) == 0) {
        return false;
    }

    //取出最后一位（与luhn进行比较）
    const lastNum = +bankno.substr(bankno.length - 1, 1);
    //前15或18位
    const otherNum = bankno.substr(0, bankno.length - 1);
    //前15或18位倒序存进数组
    const newArr = [...otherNum].reverse();
    //奇数位*2的积 <9
    const arrOdd = [];
    //奇数位*2的积 >9
    const arrOdd2 = [];
    //偶数位数组
    const arrEven = [];
    for (let j = 0; j < newArr.length; j += 1) {
        if ((j + 1) % 2 === 1) {
            //奇数位
            if (parseInt(newArr[j], 10) * 2 < 9) {
                arrOdd.push(parseInt(newArr[j], 10) * 2);
            } else {
                arrOdd2.push(parseInt(newArr[j], 10) * 2);
            }
        } else {
            //偶数位
            arrEven.push(newArr[j]);
        }
    }
    //奇数位*2 >9 的分割之后的数组个位数
    const oddChild1 = [];
    //奇数位*2 >9 的分割之后的数组十位数
    const oddChild2 = [];
    for (let h = 0; h < arrOdd2.length; h += 1) {
        oddChild1.push(parseInt(arrOdd2[h], 10) % 10);
        oddChild2.push(parseInt(arrOdd2[h], 10) / 10);
    }
    //奇数位*2 < 9 的数组之和
    let sumOdd = 0;
    //偶数位数组之和
    let sumEven = 0;
    //奇数位*2 >9 的分割之后的数组个位数之和
    let sumOddChild1 = 0;
    //奇数位*2 >9 的分割之后的数组十位数之和
    let sumOddChild2 = 0;
    let sumTotal = 0;
    for (let m = 0; m < arrOdd.length; m += 1) {
        sumOdd += parseInt(arrOdd[m], 10);
    }

    for (let n = 0; n < arrEven.length; n += 1) {
        sumEven += parseInt(arrEven[n], 10);
    }

    for (let p = 0; p < oddChild1.length; p += 1) {
        sumOddChild1 += parseInt(oddChild1[p], 10);
        sumOddChild2 += parseInt(oddChild2[p], 10);
    }
    //计算总和
    sumTotal = parseInt(sumOdd, 10) + parseInt(sumEven, 10) + parseInt(sumOddChild1, 10) + parseInt(sumOddChild2, 10);
    const k = parseInt(sumTotal, 10) % 10 === 0 ? 10 : parseInt(sumTotal, 10) % 10;
    //计算luhn值
    const luhn = 10 - k;
    if (lastNum === luhn) {
        return true;
    } else {
        return false;
    }
}
/**
 * 
 *签发机关栏位值按照市、县、旗字段进行截取，取匹配签发机关地区栏位的下拉值，能匹配到就返显
有市有县(旗)时取县的名称去匹配，如济南市商河县，会取 商河 去匹配
有市无县(旗)时取市名去匹配
无市有县(旗)时取县名去匹配
无市无县(旗)时直接取整个字段去匹配
匹配的时候，默认返显匹配到最多数量的数据，如济南，会返显济南市济南市，不会返显济南市商河县
匹配数量相同的，取排序先的展示，如输入石家庄市，返显的会是最上边的石家庄市辛集市。
若匹配不正确，允许用户手动修改，修改规则维持现有逻辑不变，展示根据签发机关模糊匹配的所有值 
 */
function getCertIssueLand(certIssueLand) {
    let idOffer = certIssueLand || "";
    let shiPoint = -1,
        xianPoint = -1,
        matchStr = "";
    shiPoint = idOffer.indexOf("市");
    xianPoint = idOffer.indexOf("县");
    if (xianPoint == -1) {
        xianPoint = idOffer.indexOf("旗");
    }
    if (shiPoint > -1 && xianPoint > -1) {
        // 有市有县的
        let lastCityPoint = idOffer.lastIndexOf("市");
        if (lastCityPoint == shiPoint) {
            matchStr = idOffer.substring(Math.max(0, shiPoint + 1), xianPoint + 1);
        } else {
            //签发机关含多个市的情况下按最后一个市往后截取县名称搜索匹配
            matchStr = idOffer.substring(Math.max(0, lastCityPoint + 1), xianPoint + 1);
        }
    } else if (shiPoint > 0 && xianPoint == -1) {
        // 有市无县
        let lastCityPoint = idOffer.lastIndexOf("市");
        if (lastCityPoint == shiPoint) {
            matchStr = idOffer.substring(0, Math.max(0, shiPoint + 1));
        } else {
            //签发机关含多个市的情况下按最后一个市名称搜索匹配
            let lastSecCityPoint = idOffer.lastIndexOf("市", idOffer.lastIndexOf("市") - 1);
            matchStr = idOffer.substring(lastSecCityPoint + 1, Math.max(0, lastCityPoint + 1));
        }
    } else if (shiPoint == -1 && xianPoint > 0) {
        // 无市有县
        matchStr = idOffer.substring(0, Math.max(0, xianPoint + 1));
    } else {
        matchStr = idOffer;
    }
    return matchStr;
}
