import { View } from "@ehas/core";
import template from "./templates/index.vhtml";
import { versionSwitch } from "~/common/utils/versionSwitch";
import { isNewStartup } from "~/common/utils/check";
export let option = {
    className: "ehas-shake"
};

class Mshake extends View {
    template = template;
    props = {
        // 关闭窗口执行函数
        closed: Function
    };
    data = {
        isShowShake: App.bank.flags.common?.isShowshake, //是否有摇一摇
        menuList: [], //菜单列表
        shakeMenuVersionChange: App.bank.flags.common?.shakeMenuVersionChange, //版本切换
        close: `${ehasProcessCDNServer}/public/999/h5/mbank/shake/close.png`, //关闭图标
        VersionChange: `${ehasProcessCDNServer}/public/999/h5/mbank/shake/versionCut.png` //版本切换图标
    };
    methods = {
        //是否展示摇一摇
        async isshow() {
            this.$refs.shake && this.$refs.shake.show();
            this.getMenuList();
        },
        //隐藏摇一摇
        async hide() {
            this.$refs.shake && this.$refs.shake.hide();
            this.closed && this.closed();
        },
        async getMenuList() {
            await this.app.menu.fetchMenuList("100000058").then(res => {
                if (this.shakeMenuVersionChange?.show) {
                    let index = this.shakeMenuVersionChange?.index;
                    res.splice(index, 0, { src: this.VersionChange, text: "版本切换" });
                }
                this.menuList = res;
            });
        },
        //点击跳转
        clickjump(item) {
            this.clickHandler({ name: "clickjump", data: item }, () => {
                this.hide();
                //如果有配功能码(除了版本切换)
                if (item.id) {
                    if (item.id == "MBECM030000") {
                        window.$G.getMenuId().then(MenuId => {
                            if (MenuId == "MBECM030000") {
                                App.region.areas.subPage?.view?.goHomePage();
                            } else {
                                window.App.menu.go(item.id);
                            }
                        });
                    } else {
                        window.App.menu.go(item.id);
                    }
                } else {
                    this.versionCut();
                }
            });
        },
        //设置
        set() {
            this.clickHandler({ name: "set" }, () => {
                this.hide();
                window.App.menu.go("MBUSR020000");
            });
        },
        //关闭
        onClose() {
            this.hide();
            this.$emit("close");
        },
        //版本切换
        versionCut() {
            this.hide();
            this.toggleVersion();
        },
        /**
         * 切换版本
         */
        toggleVersion() {
            versionSwitch();
        },
        /**
         * 新客户端关闭webview弹窗
         */
        clickHandler(data, callback) {
            if (isNewStartup() && App.globalFlag.simpleFlag != "Y") {
                App.jsApi.closeWebContainer({
                    backData: { type: "ad", ...data, components: "shake" }
                });
            } else {
                callback();
            }
        }
    };
}

export default async function install(App, ViewBase) {
    let viewOption = await new Mshake()._transformParam();
    let shakeComponent = ViewBase.extend(viewOption);

    ViewBase.prototype.$shake = function(option) {
        let tempContainer = document.createElement("div");
        document.body.appendChild(tempContainer);

        let ins = new shakeComponent({
            el: tempContainer,
            propsData: option
        });

        setTimeout(() => {
            ins.isShowShake && ins.isshow();
        });

        return ins;
    };
}
