import { switchThemeClass } from "~/common/utils/theme";
import { dateUtils } from "@ehas/utils";
const M_THEME_ID = "themeSetting";
const M_ABONDON_THEMEID = "abandedThemeId";
import { getLocalUserInfo } from "~/common/utils/user"
import { isNewStartup } from "~/common/utils/check.js";
export default class Theme {
    constructor() {
        this.data = undefined;
        this.themeId = undefined;
        if (App.jsApi.isInApp()) {
            this.init();
        }
    }
    async init() {
        const { themeId, forced, themeEndTime } = await this.getThemeId();
        this.setUserThemeId(themeId, true, forced, themeEndTime);
    }
    async getThemeId() {
        let forced = await this.getForced();
        let rspForced = await this.filterCustTAgs(forced);
        if (rspForced.length) {
            // 当前强制主题时间内用户已选择其他主题,返回用户选择的主题
            let abandonedThemedId = await App.local.get(M_ABONDON_THEMEID);
            this.currentThemeId = rspForced[0].themeId;
            if (abandonedThemedId !== rspForced[0].themeId) {
                {
                    return {
                        forced: true,
                        themeId: rspForced[0].themeId,
                        themeEndTime: rspForced[0].forceEndTm
                    };
                }
            }
        }
        //  通过本地缓存获取主题ID，themeId
        this.themeId = await App.local.get(M_THEME_ID);
        return {
            forced: false,
            themeId: this.themeId
        };
    }
    async setThemeId(id) {
        let forced = await this.getForced();
        let rspForced = await this.filterCustTAgs(forced);
        if (rspForced.length) {
            if (id !== rspForced[0].themeId) {
                return;
            }
        }
        let oldTag = document.querySelector("#floor-theme-style");
        if (oldTag) {
            oldTag.parentNode.removeChild(oldTag);
        }
        App.local.set(M_THEME_ID, id);
        if (id && id !== "0") {
            // 设置主题id缓存
            await App.jsApi.downloadSpecifiedTheme({ themeId: id });
            let link = document.createElement("link");
            link.href = `./theme/theme.css?${id}`;
            link.rel = "stylesheet";
            link.className = id;
            link.id = "floor-theme-style";
            document.head.append(link);
            switchThemeClass(1);
            this.changeTheme(id)
        }
    }
    async setUserThemeId(id, isInit, isForced, themeEndTime) {
        // 通过jsapi缓存主题ID并请求客户端下载主题zip包，themeId
        // 客户端收到请求需要检查本地缓存主题包与请求的主题是否相同
        // 如果不同直接下载对应主题zip，如果相同需要与主题列表中对应主题的checkSum比较，判断是否需要更新主题zip包
        if (id && id !== "0") {
            // 设置主题id缓存
            return await App.jsApi.downloadSpecifiedTheme({ themeId: id }).then(() => {
                // 移除主题样式
                this.removeThemeStyle();
                let link = document.createElement("link");
                link.href = `./theme/theme.css?${id}`;
                link.rel = "stylesheet";
                link.className = id;
                link.id = "floor-theme-style";
                document.head.append(link);
                switchThemeClass(1);
                this.changeTheme(id)

                if (!isInit) {
                    App.local.set(M_THEME_ID, id);
                    App.local.set(M_ABONDON_THEMEID, this.currentThemeId);
                } else {
                    App.local.remove("m_theme_id");
                    App.local.set("m_theme_id", id);
                }
                if (isForced) {
                    // 为强推主题
                    return `设定成功。该主题将于${dateUtils.format(themeEndTime, "", "YYYY-MM-DD")}后登录生效`;
                } else {
                    return "设定成功";
                }
            });
        } else if (id == "0") {
            // 设置默认主题
            if (!isInit) {
                // 移除主题样式
                this.removeThemeStyle();
                App.local.set(M_THEME_ID, id);
                App.local.set(M_ABONDON_THEMEID, this.currentThemeId);
            } else {
                App.local.remove("m_theme_id");
            }
            this.changeTheme('0')
        }
    }

    changeTheme (id) {
        if (App.router.fragmentUrl.indexOf('Mine/Theme') !== -1 || App.router.fragmentUrl.indexOf('FloorHome') !== -1) {
            isNewStartup() && App.jsApi.changeTheme({themeId: id})
        }
    }
    /**
     * 移除主题样式
     */
    async removeThemeStyle() {
        let oldTag = document.querySelector("#floor-theme-style");
        if (oldTag) {
            oldTag.parentNode.removeChild(oldTag);
            switchThemeClass();
        }
    }
    // 获取主题列表
    async getData() {
        // 通过jsapi获取主题列表
        // 客户端需要判断是否需要更新主题列表，如果需要则更新并缓存
        try {
            let res = await App.jsApi.getThemeInfoByCache();
            this.data = res.result.appThemeList || [];
            return this.data;
        } catch (error) {
            this.data = [];
            return this.data;
        }
    }
    // 非强制主题
    async getUnForced() {
        const list = await this.getData();
        return list.filter(({ isForced }) => {
            return isForced === "0";
        });
    }
    // 有效时间内的强制主题
    async getForced() {
        const list = await this.getData();
        const currentTime = new Date().getTime();
        return list.filter(({ isForced, startTime, endTime }) => {
            if (isForced === "1") {
                // 判断时间
                return currentTime >= startTime && currentTime <= endTime;
            }
        });
    }
    async getCustGrpNo() {
        let rsp = await getLocalUserInfo();
        let arr = [];
        //手机号
        if (rsp && rsp.custGrpNo) {
            arr = rsp.custGrpNo.split("|");
        }
        return arr;
    }
    // 正在使用中的主题
    async setCurrent(list) {
        let { themeId } = await this.getThemeId();
        return list.map((item) => {
            if (item.themeId === themeId) {
                item.currentTheme = true;
            }
            return item;
        });
    }
    // 获取有效时间内的主题
    async getAllTheme() {
        const list = await this.getData();
        const currentTime = new Date().getTime();
        return list.filter(({ isForced, startTime, endTime }) => {
            if (isForced === "1") {
                // 判断时间
                return currentTime >= startTime && currentTime <= endTime;
            }
            return isForced === "0";
        });
    }
    // 获取有效时间内的所有主题
    async getList() {
        if (App.jsApi.isInApp()) {
            let listTheme = await this.getAllTheme(); // 获取主题数组
            let themeList = await this.setCurrent(listTheme);
            return await this.filterCustTAgs(themeList);
        } else {
            return [];
        }
    }
    // 客群过滤
    async filterCustTAgs(list) {
        let custGrpNo = await this.getCustGrpNo(); // 当前登录人的客群标签数组
        return list.filter((item) => {
            // cusTags客群标签
            let { cusTags } = item;
            if (cusTags.length) {
                if (custGrpNo.length) {
                    return cusTags.some((tag) => {
                        if (custGrpNo.indexOf(tag) !== -1) {
                            return item;
                        }
                    });
                } else {
                    return;
                }
            } else {
                return item;
            }
        });
    }
}
