import { isLogin } from "./check";
/**
 * 处理jsapi报错
 * @param {*} res 错误信息
 * @param {*} type 权限拒绝提示
 */
function dealDeviceAuth(res, type) {
    //判断错误码
    //单独处理错误码为 EPFJ005S 的时候，不需要弹出toast提示,由平台处理
    //EPFN334B:用户取消
    if (
        ![
            "EPFN335B",
            "EPFN336B",
            "EPFN337B",
            "EPFN338B",
            "EPFN339B",
            "EPFN333B",
            "EPFN330B",
            "EPFJ005S",
            "EPFN334B",
            "EPFN839T", // 用户授权信息验证失败
            "EPFN843T", //  弹框页面退出
            "EPFN844T" //  弹框页面重试
        ].includes(res.code)
    ) {
        //EPFN306B  代表客户人为拒绝开通权限，当再次用到时需要引导客户手动去开放权限
        if (res.code == "EPFN306B") {
            deviceAuthorTips(type);
        } else {
            //有错误信息时再弹toast提示语
            if (res.cvrtMsg) {
                // 非用户取消报错
                App.getViewInstance("content").$toast(res.cvrtMsg);
            }
        }
    }
}
/**
 * 用户拒绝设备权限开启时，提示信息方法
 * type-提示信息类型："1": "您拒绝了系统授权通讯录权限，将无法选取通讯录中的联系人。",//通讯录
 * "2": "您拒绝了系统授权摄像头/存储权限，将不能正常使用该功能。",//相机
 * "3": "开启定位服务，可提高您查询附近网点、进行预约的准确性。",//位置
 * "4": "您拒绝了系统麦克风权限，将不能正常使用该功能。",//麦克风
 * "5": "您拒绝了系统存储权限，将不能正常使用该功能。"//存储
 */
function deviceAuthorTips(type) {
    App.getViewInstance("content").$confirm({
        message: `${App.dict.get("f-deviceAuthorTipCd", type)}`,
        sureText: "开通权限",
        cancelText: "取消",
        closed: isSure => {
            if (isSure) {
                App.jsApi.openAppSetting();
            }
        }
    });
}

/**
 * 判断错误码类型,密码错误、密码锁定在密码弹窗内提示
 * CR的   ECR36密码错误   EF506密码锁定
 * SA的   E1234密码错误    E1234密码锁定
 * @param {*} code 错误码
 */
function errorCode(code) {
    if (code == "ECR36" || code == "E1234" || code == "EF506") {
        return true;
    } else {
        return false;
    }
}

/**
 * 处理风控异常信息
 * @param {*} riskException 异常信息对象
 * riskException:{code,orignMsg,cvrtMsg}
 */
function dealRiskException(riskException, fidoFlag = false,resultType) {
    // 人脸指纹报错上送错误信息
    if (fidoFlag && riskException?.code && riskException?.orignMsg) {
        setErrorMsg(riskException, fidoFlag);
    }
    //人脸检测失败前端结果页跳转,开放法人行:801
    if (!fidoFlag && App.bank.flags?.account?.gotoFaceResult) {
        // EPFN812T活体页面用户主动，EPFN843T弹窗页面退出，EPFN805T检测过程中锁屏或退出后台，EPFN811T引导页面用户主动取消检测，EPFN821T失败结果页面点击退出
        let errorCode = ["EPFN812T", "EPFN843T", "EPFN805T", "EPFN811T", "EPFN821T"];
        // 检测过程中非主动退出的所有情况均跳转前端超时结果页
        if (!errorCode.includes(riskException?.code)) {
            window.$G.goToNextRetain({ router: "#Account/Result/FaceCertifyResult", param: { pageFlag: "Y",resultType:resultType } });
        }
    }
    // 入参数据校验检测处理
    if (!riskException || !riskException.code || !riskException.detailCode) {
        window.$G.logWarnWithCode(
            "common/utils/error.js风控异常检测信息请求参数错误",
            "usr/login/speclgnerrinfrcd.biz请求参数错误" + JSON.stringify(riskException)
        );
        return;
    }
    setErrorMsg(riskException, fidoFlag);
}
/**
 * 请求参数接口上送错误信息
 * */

async function setErrorMsg(riskException, fidoFlag) {
    // 已登录过的用户取本地缓存，未登录过用户取会话缓存（输入手机号之后的缓存）
    const loginUserNm = (await App.local?.get("loginUserNm")) || (await App.storage.get("loginUserNmShow")) || "";
    // 入参转化
    let requestData = {
        faceIdenRsltCd: fidoFlag ? riskException.code : riskException.detailCode,
        faceIdenRsltMsg: riskException.orignMsg,
        busnTyp: fidoFlag ? 19 : "", //fido异常上送参数19
        errCdSubCd: riskException.childFaceCode, //字code码,
        loginUserNm: loginUserNm //登录用户名
    };
    // 请求处理
    App.requester
        .request("usr/login/speclgnerrinfrcd.biz", {
            data: requestData,
            useDefaultError: false,
            slient: true
        })
        .catch(res => {
            window.$G.logWarnWithCode(
                "common/utils/error.js风控异常检测信息上送请求失败",
                "风控异常检测信息上送请求usr/login/speclgnerrinfrcd.biz请求失败" + JSON.stringify(res)
            );
        });
}

