/**
 * 客户端方法异常报错信息
 * rsp：客户端错误信息  mode:认证方式，FC FG
 */
async function clientMessage(rsp, mode, callback) {
    //快捷登陆方式
    const loginWayDict = {
        FG: "指纹登录",
        GS: "手势登录",
        FC: "人脸登录"
    };
    const currentObj = await erApiMess(mode);
    if (currentObj[rsp.code]) {
        //如果对象中的值存在对象中，则弹框提示
        App.getViewInstance("content").$alert({
            btnText: "确定",
            message: currentObj[rsp.code],
            closed: () => {
                if (typeof callback == "function") {
                    //如果传过来的参数是方法，则回调该方法
                    callback();
                }
            }
        });
    } else if (typeof rsp == "object" && rsp.code) {
        App.getViewInstance("content").$alert({
            btnText: "确定",
            message: rsp.code + loginWayDict[mode] + "认证失败，请重试！"
        });
    }
    // fido异常上送接口
    if (mode == "FC" || mode == "FG") {
        dealRiskException(rsp, true);
    }
}

/**
 *弹框提示
 * @param {*} rsp:返回的结果，btnText按钮名称 url跳转路径  objInstance(快速转账需要参数objInstance) jumpMode跳转方式
 */
/* eslint-disable */
function defaultErrorFunc(rsp, btnText, url, cancels, objInstance, jumpMode) {
    let cancel = "";
    if (rsp.code.substring(0, 4) == "EA1H") {
        cancel = "我知道了";
    } else {
        if (cancels) {
            cancel = cancels;
        } else {
            cancel = "取消";
        }
    }
    App.getViewInstance("content").$confirm({
        message: `${rsp.message}`,
        sureText: btnText,
        cancelText: cancel,
        closed: isSure => {
            if (isSure) {
                //快速转账需要参数that
                if (typeof objInstance === "function") {
                    objInstance();
                }
                if (jumpMode) {
                    App.menu.go(url);
                } else {
                    window.$G.goToNextRetain({
                        router: url
                    }); //跳转修改限额页面
                }
            }
        }
    });
}
/* eslint-enable */

/**
 * 九要素校验报错异常处理
 *  @param {Object} self
 *  @param {Object} rsp 数据
 */
function nineElementsErrorFunc(rsp, cancelCallBack) {
    if (rsp.code === "EA1A013B") {
        // TODO 跳转到公共完善用户信息页面
        App.getViewInstance("content").$confirm({
            message: "您的客户信息不完整，购买前需您完善客户信息。",
            sureText: "去完善",
            cancelText: "我知道了",
            closed(isSure) {
                // 点击 去完善
                if (isSure) {
                    window.$G.goToNextRetain({
                        router: "#Mine/Setting/IdentityInfo"
                    });
                }
                // 点击 我知道了
                if (!isSure && cancelCallBack && typeof cancelCallBack === "function") {
                    cancelCallBack();
                }
            }
        });
    } else {
        window.$G.popErrorMsg(rsp);
    }
}
/**
 *
 * @param {*} rsp
 * 错误码值识别 errorCodeList内的错误码代表密码错误
 */
function errorCodeFlag(rsp) {
    const errorCodeList = [
        "ECR36",
        "EF506",
        "003851",
        "003852",
        "000642",
        "009003",
        "009140",
        "009141",
        "009297",
        "009304",
        "888551",
        "888552",
        "009098",
        "E1234",
        "EE659",
        "EPFB108B",
        "009002",
        "55"
    ];
    if (errorCodeList.includes(rsp.code)) {
        return true;
    } else {
        return false;
    }
}
/**
 *
 * @param {*} rsp
 * 人脸识别根据错误码跳转后端失败结果页
 */
function toFaceCertifyResult(rsp,type) {
    const errorCodeList = ["EA1J075B", "EA1K051B", "EA1J053B", "EA1J073B", "EA1J116B"];
    if (App.bank.flags?.account?.gotoFaceResult && errorCodeList.includes(rsp.code)) {
        window.$G.goToNextRetain({ router: "#Account/Result/FaceCertifyResult", param: { pageFlag: "N",resultType:type } });
    } else {
        window.$G.popErrorMsg(rsp);
    }
}

export {
    dealDeviceAuth,
    deviceAuthorTips,
    errorCode,
    dealRiskException,
    setErrorMsg,
    erApiMess,
    clientMessage,
    defaultErrorFunc,
    nineElementsErrorFunc,
    errorCodeFlag,
    toFaceCertifyResult
};
