// 初始化APP，创建APP实例
function renewApp() {
    console.info(`${dateUtils.nowDate("HH:mm:ss:SSS")}：【mbank-H5】执行new Application()`);
    const startTime = globalUtil.getCurrentTime();
    new Application({
        defaultLayout: "Default",
        indexPath: "#FloorHome/Home/Index",

        layouts: {
            Huinong: () => import("@layouts/HuinongLayout"),
            Default: () => import("@layouts/DefaultLayout"),
            Home: () => import("@layouts/HomeLayout"),
            // 无任何预设内容的布局
            Blank: () => import("@layouts/NormalLayout")
        },
        modules: {
            //高端客户专享
            HighLevelCustomer: () => import("./modules/HighLevelCustomer/Config"),
            FloorHome: () => import("./modules/FloorHome/Config"),
            // todel 儿童卡
            ChildCard: () => import("./modules/ChildCard/Config"),

            // 理财
            Finance: () => import("./modules/Finance/Config"),
            // 恒生理财平台
            FinanceHS: () => import("./modules/FinanceHS/Config"),
            // 齐鲁理财
            FinanceQL: () => import("./modules/FinanceQL/Config"),
            // 恒生理财转让服务
            FTS: () => import("./modules/FTS/Config"),
            // 金证理财转让服务
            FTSJZ: () => import("./modules/FTSJZ/Config"),
            // 大额存单转让服务
            CDTS: () => import("./modules/CDTS/Config"),
            //我的关注列表
            Collection: () => import("./modules/Collection/Config"),
            //我的收藏列表
            Favorities: () => import("./modules/Favorities/Config"),
            // 基金
            Fund: () => import("./modules/Fund/Config"),
            // 类余额宝
            YuEBao: () => import("./modules/YuEBao/Config"),
            // 我的存款主页
            Deposit: () => import("./modules/Deposit/Config"),
            // 借款
            Loan: () => import("./modules/Loan/Config"),
            // 财富总览
            Wealth: () => import("./modules/Wealth/Config"),
            // 财富总览新版
            WealthPO: () => import("./modules/WealthPO/Config"),
            // 登录注册
            Login: () => import("./modules/Login/Config"),
            // 转账
            Transfer: () => import("./modules/Transfer/Config"),
            // 我的账户
            Account: () => import("./modules/Account/Config"),
            // 安全中心
            SafeCenter: () => import("./modules/SafeCenter/Config"),
            // 智慧盈（投融资）
            Investment: () => import("./modules/Investment/Config"),

            // 我的个人信息展示
            Mine: () => import("./modules/Mine/Config"),
            // 配置管理
            Sign: () => import("./modules/Sign/Config"),
            //无卡取款
            NoCardWithdraw: () => import("./modules/NoCardWithdraw/Config"),
            // 账单明细
            Statement: () => import("./modules/Statement/Config"),
            //资金归集
            FundGather: () => import("./modules/FundGather/Config"),
            //设置
            Setting: () => import("./modules/Setting/Config"),
            // 网点服务
            BranchService: () => import("./modules/BranchService/Config"),
            // 亲情圈
            AffectionMoment: () => import("./modules/AffectionMoment/Config"),
            // 网点服务
            Assistant: () => import("./modules/Assistant/Config"),
            // 智能客服
            IntelligentService: () => import("./modules/IntelligentService/Config"), //
            //智能搜索
            Search: () => import("./modules/Search/Config"),
            //消息中心
            MessageCenter: () => import("./modules/MessageCenter/Config"),
            //视频专区
            Video: () => import("./modules/Video/Config"),
            // 全部菜单页面
            FloorPage: () => import("./modules/FloorPage/Config"),
            // 简版页面
            SimpleHome: () => import("./modules/SimpleHome/Config"),
            // 开放平台
            Open: () => import("./modules/Open/Config"),
            //扫码登录
            Scan: () => import("./modules/Scan/Config"),
            // SDK跳转公共模块
            SDKCommon: () => import("./modules/SDKCommon/Config"),
            // 生活模块
            Life: () => import("./modules/Life/Config"),
            //  资产证明
            ProofAssets: () => import("./modules/ProofAssets/Config"),
            // 信用卡模块
            CreditCard: () => import("./modules/CreditCard/Config"),
            //越狱白名单模块
            Allowlist: () => import("./modules/Allowlist/Config"),
            // 实物贵金属
            nobleMetal: () => import("./modules/nobleMetal/Config"),
            // 保险
            Insurance: () => import("./modules/Insurance/Config"),
            // 801直销银行
            DirectBank: () => import("./modules/DirectBank/Config"),
            //柜面无卡化认证
            AccountAuthWithScan: () => import("./modules/AccountAuthWithScan/Config"),
            // 代发专区
            DistributionArea: () => import("./modules/DistributionArea/Config"),
            //资讯
            Information: () => import("./modules/Information/Config"),
            // 惠农版
            Huinong: () => import("./modules/Huinong/Config"),
            // 社保专区
            Social: () => import("./modules/Social/Config"),
            // 账户管家
            SalaryFinance: () => import("./modules/SalaryFinance/Config"),
            // 薪酬查询
            PayQuery: () => import("./modules/PayQuery/Config"),
            // 818长量基金
            CLFund: () => import("./modules/CLFund/Config"),
            // 产品体验官
            ProductExperience: () => import("./modules/ProductExperience/Config"),
            // 乡村振兴
            RuralVitalization: () => import("./modules/RuralVitalization/Config"),
            //薪金宝专区
            Salary: () => import("./modules/Salary/Config"),
            // 新市民专区
            NewCitizenZone: () => import("./modules/NewCitizenZone/Config"),
            // 打开pdf文件
            PDF: () => import("./modules/PDF/Config"),
            // 平安三方存管
            TherePartDepository: () => import("./modules/TherePartDepository/Config"),
            // 新客户端压屏窗
            LandScape: () => import("./modules/LandScape/Config"),
            // 酷屏活动
            CoolScreen: () => import("./modules/CoolScreen/Config"),
            //数字人民币
            DigitalWallet: () => import("./modules/DigitalWallet/Config"),
            // 810特色专区
            SpecialArea: () => import("./modules/SpecialArea/Config"),
            // 积存金
            Gid: () => import("./modules/Gid/Config"),
            // 空白页面，新客户端预加载弹窗使用
            Blank: () => import("./modules/Blank/Config"),
            //代销资管
            ConsignmentAM: () => import("./modules/ConsignmentAM/Config"),
            // 生活缴费 内含校园卡
            Utilities: () => import("./modules/Utilities/Config"),
            //云闪付支付管理
            QuickPassManage:()=>import("./modules/QuickPassManage/Config"),
            // 社保卡申领
            ElecHealthInsCard: () => import("./modules/ElecHealthInsCard/Config")
        }
    });

    /** --- 时间线打点--begin--- **/
    //
    let afterStartBeginTime = "";
    App.listener.once("after_beforeStart", () => {
        console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}：【mbank-H5】执行Application beforeStart切面完成`);
        globalUtil.pushStartupTimeLine?.("App-beforeStart", "实例化App beforeStart切面总耗时", startTime);
        afterStartBeginTime = globalUtil.getCurrentTime();
    });
    App.start().then(async () => {
        globalUtil.pushStartupTimeLine?.("App-afterStart", "实例化App afterStart切面总耗时", afterStartBeginTime);
        console.info(`${dateUtils.nowDate("HH:mm:ss.SSS")}：【mbank-H5】执行Application.then()`);
        globalUtil.pushStartupTimeLine?.("newApplication", "实例化Application完成总耗时", startTime);
        import("./common/resource/common_diff.less");
        document.title = `${App.bank.flags.common.bankName}欢迎您`;
        let wai = require("~/config/wai.js");
        window.$wai = wai(App.jsApi);

        // 调整字体大小
        let customFontSize = await App?.local?.get("customFontSize");
        if (customFontSize?.value) {
            setFontSize(customFontSize?.value);
        }
        if (App.jsApi.isRootView()) {
            App.local?.remove("requst_uuid");
        }
    });
    /** --- 时间线打点--end--- **/
}
