/* eslint-disable */
export default {
    afterStart: function(App) {
        // 将beforeStart改为afterStart，因为beforeStart时公共方法还未注入
        App.globalFlag = {};
        App.globalFlag.lastStartBizsReqUrl = ""
        // 跨步场景码
        App.globalFlag.busiSceneNo = "";
        // 上一次请求路径
        App.globalFlag.lastReqUrl = "";
        // 是否正在调起登录的webview
        App.globalFlag.loginView = "";
        // 设置请求头标志
        App.globalFlag.setRequestHead = true;
        // 登录webview关闭通知信息
        App.globalFlag.closeLoginStateDate = {};
        // 首次展示
        const defaultState = App.bank.flags.safeCenter?.defaultSwitchSetting ? false : true;
        //防截/录屏路由
        App.globalFlag.noScreenCaptureUrl = [
            "Transfer/Transfer/TransferByCard", //银行卡转账
            "Transfer/Transfer/TransferByPhoneNo", //手机号转账
            "Transfer/TransferBatch/TransferBatch", //批量转账
            "Transfer/TransferByReservation/AddByTemp", //预约转账
            "Transfer/TransferByReservation/AddInfoByTemp", //预约转账
            "Transfer/TransferByReservation/RapidAddInfoByTemp", //预约转账
            "AffectionMoment/AffectionBlessing/Add", //亲情祝福
            "AffectionMoment/AffectionBlessing/Preview", //亲情祝福
            "Login/Register/CustInfoCheck", // 输入用户名
            "Login/Login/LgnByPswd", // 密码登录
            "Login/Login/DefaultLogin", // 密码登录
            "Login/Login/SpecLgnWay", // 人脸、指纹、手势登录
            "SafeCenter/LoginSetting/GesturePswdSetting", // 手势密码设置
            "Login/Login/SettingGesture" // 手势密码快捷设置
        ];

        App.globalFlag.noScreenCaptureMineUrl = [
            "FloorHome/SimplifyMine/PerDetails", // 我的
            "SimpleHome/Mine/OldPerDetails", // 老年版我的
            "Huinong/My/Index" // 惠农我的)
        ];
        if (App.bank.flags?.mine?.noScreenCapture) {
            App.globalFlag.noScreenCaptureUrl = App.globalFlag.noScreenCaptureUrl.concat(
                ...App.globalFlag.noScreenCaptureMineUrl
            );
        }
        App.local
            .get("m_version_info")
            .then(rsp => {
                if (rsp) {
                    if (rsp.simpFlg == "Y") {
                        App.globalFlag.simpleFlag = rsp.simpFlg;
                        App.globalFlag.huinong = "N";
                    } else if (rsp.huinong == "Y") {
                        App.globalFlag.huinong = rsp.huinong;
                        App.globalFlag.simpleFlag = "N";
                    }
                    App.globalFlag.autoChangeSimple = rsp.autoChangeSimple == "Y" ? "Y" : "N";
                } else {
                    App.globalFlag.simpleFlag = "N";
                    App.globalFlag.huinong = "N";
                    App.globalFlag.autoChangeSimple = "N";
                }
            })
            .catch(() => {
                App.globalFlag.simpleFlag = "N";
                App.globalFlag.huinong = "N";
                App.globalFlag.autoChangeSimple = "N";
            });
        // 账户智能读取状态 默认打开
        App.local
            .get("m_clip_card_flag")
            .then(rsp => {
                if (rsp == undefined) {
                    App.local.set("m_clip_card_flag", defaultState);
                }
            })
            .catch(() => {
                App.local.set("m_clip_card_flag", defaultState);
            });
        // 手机智能读取状态 默认打开
        App.local
            .get("m_clip_phone_flag")
            .then(rsp => {
                if (rsp == undefined) {
                    App.local.set("m_clip_phone_flag", defaultState);
                }
            })
            .catch(() => {
                App.local.set("m_clip_phone_flag", defaultState);
            });
    }
};
