/**
 * 获取服务器当前时间
 *
 *  accessSysId: ""
 *  message: "交易处理成功"
 *  oprTm: "111931"
 *  orignMsg: ""
 *  rspTp: ""
 *  sysDate: "20210722"
 *  tms: "1626923971869"
 * param:为true代表有loading圈，为false代表不展示loading圈
 */
function getServerNowDate(param = true) {
    return App.requester
        .request("cmm/sys/serverdateqry.biz", {
            slient: param
        })
        .then(res => {
            return res;
        });
}

export { getServerNowDate };
