import { isLogin } from "./check";
import { loginCusMessage } from "./user";
import memnu from "~/common/datas/menu.js";
import { ViewBase } from "@ehas/core";
/**
 * 跳转在线客服页面
 * @param {*}  pageMessage页面信息
 * @isRetain   602行跳转在线客服传值
 */
/* eslint-disable */
async function onlineUrlFuc(pageMessage, isRetain) {
    //埋点
    window.$G.getMenuId(true).then(menu => {
        App.DC.onEvent("COMMON_CUSTOMER_SERVS", {
            CMM_CUSTSERVS_PAGEID: menu?.bsnCode,
            CMM_CUSTSERVS_PAGENM: menu?.bsnName
        });
    });
    if (App.bank?.flags?.IntelligentService && App.bank.flags?.IntelligentService?.westCoast) {
        //如果是602行，则直接跳转至我的客服页面
        if (isRetain) {
            window.$G.goToNext("#IntelligentService/CustomerService/Index");
        } else {
            window.$G.goToNextRetain({
                router: "#IntelligentService/CustomerService/Index"
            });
        }
    } else {
        //如果不是村镇行，则跳转到对应的行方客服页面
        let LoginStatus = await isLogin();
        //跳转到智能客服
        let queryParam = await detailParam(pageMessage);
        let loginCusMsg = "";
        if (App.bank.flags?.IntelligentService?.cusReqInfo && LoginStatus) {
            //如果是866/817行并且是登录状态，需要将客户信息传过去
            loginCusMsg = await loginCusMessage();
            loginCusMsg = `&csInfo=${loginCusMsg}`;
        }
        //判断是否已登录
        //如果未登录，则走未登录接口；登录的情况下，走登录接口
        const requestUrl = LoginStatus ? "hlp/quest/hlpintlgsrvurlqry.biz" : "hlp/quest/hlpintlgsrvurlqrynolgn.biz";
        //跳转智能客服h5页面
        App.requester
            .request(requestUrl)
            .then(res => {
                memnu
                    .findById(`${App.bank.flags?.IntelligentService?.onlineService}`)
                    .then(rsp => {
                        const functionCode = ["MBHLP010200", "MBHLP010300", "MBHLP010400"].includes(rsp.bsnCode);
                        let onlineUrl = "";
                        if (functionCode) {
                            // 如果走三个功能码中的任何一个，上送功能码和错误码
                            onlineUrl = rsp.url + res.funcUrl + queryParam + loginCusMsg;
                        } else {
                            // 联盟在线客服的情况，不上送功能码和错误码
                            onlineUrl = rsp.url + loginCusMsg;
                        }
                        //跳转智能客服h5页面
                        if (App.bank.flags?.IntelligentService?.appletContainer) {
                            // 小程序容器打开
                            App.jsApi
                                .openApplet({
                                    url: onlineUrl,
                                    appId: res.appId,
                                    licence: res.publicKey,
                                    title: App.bank.flags?.IntelligentService?.appletContainer?.title
                                })
                                .catch(rsp => {
                                    //通过code和messge可查看调用失败的原因
                                    if (rsp.code == "EPFJ002W") {
                                        ViewBase.prototype.$alert({
                                            message: "该版本手机银行不支持此功能，请升级最新版本手机银行",
                                            btnText: "确定"
                                        });
                                    } else {
                                        ViewBase.prototype.$toast(rsp.cvrtMsg);
                                    }
                                });
                        } else {
                            App.jsApi.openWebPageByJSBContainer({ url: onlineUrl, title: "在线客服" });
                        }
                    })
                    .catch(() => {
                        Log.info("楼层功能码转化路径方法报错");
                    });
            })
            .catch(res => {
                Log.info("获取后台接口报错", res);
            });
    }
}

/**
 * 接收参数处理
 */
async function detailParam(pageMessage) {
    let queryParam = "";
    // 获取页面url
    if (pageMessage && pageMessage.pageUrl) {
        // 根据url获取功能码
        queryParam = await memnu.findByUrl(pageMessage.pageUrl).then(rsp => {
            //如果rsp有值，开始拼接参数，否则上送空
            return rsp ? "&bsnCode=" + rsp?.bsnCode + "&bsnName=" + encodeURIComponent(rsp?.bsnName) : "";
        });
    } else if (pageMessage && pageMessage.errCode) {
        // 获取错误结果页面信息
        queryParam = "&errCode=" + pageMessage.errCode + "&errMsg=" + encodeURIComponent(pageMessage.errMsg);
    }
    return queryParam;
}
/* eslint-enable */
export { onlineUrlFuc, detailParam };
