/** * 微信银行内嵌6.0业务走wap服务 * 目前仅806基金业务 */import { ViewBase } from "@ehas/core";import { getUrlParam } from "~/common/utils/https";import { initWXRouter, getRouterParam } from "~/common/js/wapWeChatRouter";class WapWeChatUtil {    /**     * 是否为微信-基金业务     * 目前806从微信银行打开基金时会判断为可进行交易业务     * (url的query参数from=WeChat)     * @return {} Boolean     */    static isWXBusiness() {
//EHAS_RESULT_BRANCH_START false 

//EHAS_RESULT_BRANCH_END 
        return false;    }    /**     * 获取登录状态     * @return {} Boolean 是否登录     */    static async isWXLogin() {        //已缓存登录状态        const isLogin = App.storage.get("WXLoginFlag");        if (isLogin !== undefined) return isLogin;        //获取url参数        const token = getUrlParam(location.href, "token");        const openAcc = getUrlParam(location.href, "openAcc");        //无token表示未加挂账户，处理为非登录        if (!token) {            App.storage.remove("WXLoginFlag");            App.storage.set("WXLoginFlag", false);            return false;        }        //获取用户信息        return App.requester            .request("usr/cust/wxusrinfoqry.biz", {                data: {                    wxPlcId: openAcc,                    token: token                }            })            .then(rsp => {                //requestForWapSetting 会自动获取此接口返回的sid                if (rsp.bindStatus == "1") {                    //登录标识                    App.storage.remove("WXLoginFlag");                    App.storage.set("WXLoginFlag", true);                    //wechatOpenid 取的是接口返回的 loginUserNm                    App.storage.remove("wechatOpenid");                    App.storage.set("wechatOpenid", rsp.loginUserNm);                    return true;                } else {                    //查询未登录状态则关闭窗口                    ViewBase.prototype.$confirm({                        message: "会话超时，请重新点菜单进入",                        sureText: "",                        cancelText: "确定",                        closed() {                            WapWeChatUtil.closeWebPage();                        }                    });                    return false;                }            });    }    /**     * 未绑卡时没有token，处理为未登录     */    static async dealWXLogin() {        if (await WapWeChatUtil.isWXLogin()) {            return true;        } else {            ViewBase.prototype.$alert("该功能需要添加下挂卡，请添加后再次尝试");            return false;        }    }    /**     * 关闭当前页面     */    static closeWebPage() {        window.close();    }    /**     * 页面跳转前routerBefore     * @param {*} routerUrl 想要跳转的路由     */    static async routerBefore(routerUrl) {        routerUrl = routerUrl.replace(/^#?/, "#");        //#Schema/Index/Index页面自动跳转到基金首页，防止意外        if (routerUrl == "#Schema/Index/Index") {            App.router.go("#Fund/Index/Index", { igSelf: true });            return false;        }        return true;    }    /**     * 路由加载后     */    static async routerAfter() {        //[data-module-fund]添加wap-wx-business属性，用于页面重写样式        setTimeout(() => {            document.body.querySelector("[data-module-fund]")?.setAttribute?.("wap-wx-business", "");        });    }    /**     * 初始化wap微信环境     */    static initWXEnvironment() {        // 重写设置标题头方法        const originSetArea = App.setArea;        App.setArea = function(name, option, ...args) {            if (name == "navBar") {                document.title = option?.data?.title || `${App.bank.flags.common?.bankName}欢迎您`;            } else {                // 原来的方法(应该不会走到这里)                originSetArea.call(App, name, option, ...args);            }        };        //初始化路由跳转配置        initWXRouter();        //屏蔽微信分享功能        setTimeout(() => {            WapWeChatUtil.onBridgeReady();        }, 0);    }    /**     * 屏蔽微信分享功能     */    static onBridgeReady() {        if (typeof WeixinJSBridge == "undefined") return;        /* eslint-disable */        WeixinJSBridge.call("hideOptionMenu");        WeixinJSBridge.call("hideToolbar");        /* eslint-enable */    }    /**     * 获取路由参数     */    static getRouterParam() {        return getRouterParam();    }}/** * 初始化微信环境 */const wapWeChatSetting = {    afterStart: function() {        if (WapWeChatUtil.isWXBusiness()) {            WapWeChatUtil.initWXEnvironment();        }    }};export { WapWeChatUtil, wapWeChatSetting };