/**
 * 删除超出失效期数据
 */
async function _deleteOutDateData() {
    let res = await getServerNowDate();
    //获取服务器时间
    if (res.sysDate) {
        await App.jsApi
            .executeUpdateSql({
                dbName: "MSG_PUSH_TABLE",
                sqls: [
                    // eslint-disable-next-line max-len
                    `delete from MSG_PUSH_TABLE where bkId='${App.bank.flags.common.bankId}' and (msgBizTyp!='01' and msgBizTyp!='02') 
                         and not (txStrtTm <= '${res.sysDate}' and '${res.sysDate}' <= txEndTm) and txEndTm!=''`
                ]
            })
            .then(({ result }) => {
                if (result?.executeResult[0]?.message == "失败") {
                }
            })
            .catch(() => { });
    }
}

/**
 * 根据消息id修改消息状态为已读
 * @param {*} taskId
 */
export async function setMessageAsRead(taskId) {
    if (taskId) {
        //如果taskid存在，则更新表中的数据
        return App.jsApi
            .executeUpdateSql({
                dbName: "MSG_PUSH_TABLE",
                sqls: [
                    `update MSG_PUSH_TABLE set pcsSts='1' where taskId='${taskId}'` +
                        ` and bkId='${App.bank.flags.common.bankId}'`
                ]
            })
            .then(({ result }) => {
                if (result?.executeResult[0]?.message == "失败") {
                }
                //当消息标记为已读后，需要处理的逻辑
                _readyMsgNumFun();
            })
            .catch(() => { });
    } else {
    }
}

/**
 * 表中数据处理成消息已读后，需要处理的逻辑
 */
export async function _readyMsgNumFun() {
    //当消息推送产生时，调用楼层写的全局监控方法，以达到能够实时刷新未读数的目的
    App.region?.triggerGlobalEvent("msgNoReadNumChange");
    if (isNewStartup()) {
        const noReadNum = (await qryMsgNoReadNum(true)) || "0";
        App.jsApi.unreadMessage({ num: noReadNum });
    }
}

/**
 * 根据 消息类型查询对应消息数据
 * @param {*} msgType 消息类型 01-通知，02-动账，03-银行，04-网点，05-生活，06-理财，07-信用卡，08-活动，10-基金
 * @param {*} netNo 网点编号 仅网点传参
 * @param {*} cstNo 客户号 仅登录传参
 */
export async function qryMsgDataByType(msgType, netNo, cstNo) {
    // 处理sql语句
    let sqlStr = "";
    if (cstNo) {
        // 查询通知01、动账02-需登录
        sqlStr =
            `select * from MSG_PUSH_TABLE where msgBizTyp='${msgType}' ` +
            `and ebkCustNo='${cstNo}' and bkId='${App.bank.flags.common.bankId}' order by sndTms desc`;
    } else if (netNo) {
        // 查询网点公告04-根据网点查询
        sqlStr =
            `select * from MSG_PUSH_TABLE where msgBizTyp='${msgType}' ` +
            `and isuInstNo='${netNo}' and bkId='${App.bank.flags.common.bankId}' order by sndTms desc`;
    } else {
        // 查询资讯、公告
        sqlStr =
            `select * from MSG_PUSH_TABLE where msgBizTyp='${msgType}' ` +
            `and bkId='${App.bank.flags.common.bankId}' order by sndTms desc`;
    }
    return App.jsApi
        .executeQuerySql({
            dbName: "MSG_PUSH_TABLE",
            sql: sqlStr
        })
        .then(res => {
            if (res.result.executeResult && res.result.executeResult.length > 0) {
                return res.result.executeResult;
            } else {
                return [];
            }
        })
        .catch(({ code, orignMsg, cvrtMsg }) => {
            window.$G.logWarnWithCode(
                "common/js/message.js查询数据入库 qryMsgDataByType 捕捉catch MSG_PUSH_TABLE INSERT error",
                "code:" + code + "orignMsg:" + orignMsg + "cvrtMsg:" + cvrtMsg,
                true
            );
            return [];
        });
}

/**
 * 查询所有未读数
 * @param {*} cstNo 客户号（登录状态传值）
 *  @param {*} slientFlag 主页是否显示loading层
 */
export async function qryMsgNoReadNum(slientFlag) {
    try {
        // 判断是否切换用户登录,清除缓存，实时请求未读数
        let refreshNoreadNum = (await App.local.get("refreshNoreadNum")) || false;
        const sessionmsg = (await App.storage.get("msgNoReadingNum")) || {};
        if ((sessionmsg?.num || sessionmsg?.num == 0) && !refreshNoreadNum) {
            return `${sessionmsg.num}`;
        } else {
            if (refreshNoreadNum) {
                App.local.remove("refreshNoreadNum");
                App.local.set("refreshNoreadNum", false);
            }
            //等sql表建立成功之后，再继续执行下面的逻辑，以防出现查表查不到的情况
            await App.jsApi
                .executeUpdateSql({
                    dbName: "MSG_PUSH_TABLE",
                    //bkId-成员行号,txStrtTm消息生效时间,txEndTm消息失效时间,tagUrl标签URL,iconUrl图标URL
                    //writerNm作者姓名,isuInstNo  公告咨询消息网点号，stckFlg  置顶标志(YN)， strusFlg  启用标志(YN)   crt_tms 创建时间戳
                    //upd_tms 更新时间戳 taskId-任务id,ebkCustNo-客户号,msgBizTyp-消息类型,
                    // mesgTitle-标题,msgCntnt-内容,sndTms-发布时间,pcsSts-未读状态(0-未读),busData- 透传数据
                    sqls: [
                        // eslint-disable-next-line max-len
                        "CREATE TABLE IF NOT EXISTS MSG_PUSH_TABLE(bkId chart(3),txStrtTm chart(20),txEndTm chart(20)," +
                            // eslint-disable-next-line max-len
                            "iconUrl chart(400),writerNm chart(60),isuInstNo chart(9),stckFlg chart(1),strusFlg chart(1), " +
                            // eslint-disable-next-line max-len
                            "taskId chart(16),ebkCustNo chart(22),tagUrl chart(400),msgBizTyp chart(4),mesgTitle chart(128)," +
                            "msgCntnt chart(8000),sndTms chart(20),pcsSts chart(1),busData chart(4000)," +
                            "crt_tms TimeStamp not null default (datetime('now','localtime')),upd_tms TimeStamp " +
                            // eslint-disable-next-line max-len
                            "default (datetime('now','localtime')),constraint PK_MSG_PUSH_TABLE primary key (bkId,taskId))"
                    ]
                })
                .then(({ result }) => {
                    if (result?.executeResult[0]?.message == "失败") {
                    }
                })
                .catch(() => { });
            //取缓存信息——加载消息须上送  手机号 机构号
            const UserInfo = (await getLocalUserInfo()) || {};
            const cstNo = UserInfo.cstNo || "";
            const loginUserNm = UserInfo.mobileNo || "";
            const branchId = UserInfo.branchNo || "";

            // 查询资讯/公告未读数（无需登录）
            let totalNum = await App.jsApi
                .executeQuerySql({
                    dbName: "MSG_PUSH_TABLE",
                    sql:
                        `select taskId from MSG_PUSH_TABLE where pcsSts='0' and ` +
                        ` (msgBizTyp!='01' and msgBizTyp!='02')` +
                        ` and bkId='${App.bank.flags.common.bankId}'`
                })
                .catch(() => { });

            //未读消息总数
            let NoReadNum = totalNum?.result?.executeResult?.length || 0;
            //登录-查询通知、动账
            if (cstNo) {
                let loginTotalNum = await App.jsApi
                    .executeQuerySql({
                        dbName: "MSG_PUSH_TABLE",
                        sql:
                            `select taskId from MSG_PUSH_TABLE where ebkCustNo='${cstNo}' and ` +
                            ` pcsSts='0' and (msgBizTyp='01' or msgBizTyp='02')` +
                            ` and bkId='${App.bank.flags.common.bankId}'`
                    })
                    .catch(() => { });
                //资讯/公告+通知/动账
                if (loginTotalNum?.result?.executeResult?.length) {
                    NoReadNum = Number(NoReadNum) + Number(loginTotalNum?.result?.executeResult?.length);
                }
            }
            // 获取更新数据时间
            const updateTime = (await App.local.get("m_msg_update_time")) || {};

            //查询未加载的数据（我的页主页用）
            return App.requester
                .request("msg/msg/msghmpg.biz", {
                    data: {
                        loginUserNm: loginUserNm,
                        branchId: branchId,
                        txStrtTm: updateTime.delTime || "",
                        inqFlg: "1"
                    },
                    slient: slientFlag || false
                })
                .then(res => {
                    res.inqNum = Number(res.inqNum);
                    //已加载消息+未加载消息
                    NoReadNum = NoReadNum + res.inqNum;
                    //获取时间
                    App.local.remove("m_msg_update_time");
                    App.local.set("m_msg_update_time", { delTime: res.tmpSysTm });
                    if (typeof NoReadNum === "number" && !isNaN(NoReadNum)) {
                        App.storage.remove("msgNoReadingNum");
                        //将未读数存入到缓存中
                        App.storage.set("msgNoReadingNum", { num: NoReadNum });
                        return `${NoReadNum}`;
                    }
                    return "";
                });
        }
    } catch (e) {
        return "";
    }
}
