import { View } from "@ehas/core";
import template from "./index.vhtml";
import { versionSwitchWithoutDialog } from "~/common/utils/versionSwitch";

export let option = {
    className: "e-simple-guide",
    default: {
        type: "1",
        isShowDelete: true,
        iconPosition: "t1",
        pctDlgFrq: "01",
        adType: "0"
    }
};

class MSimpleGuide extends View {
    template = template;
    inheritAttrs = true;
    props = {

        picUrl: String //图片地址
    };
    data = {
        simpleGuideConfig: {},
        // “去体验”按钮
        goSimpleUrl: `${ehasProcessCDNServer}/public/999/h5/mbank/Common/components/m-simple-guide/go-simple.png`,
        // “不用了”按钮
        noSimpleUrl: `${ehasProcessCDNServer}/public/999/h5/mbank/Common/components/m-simple-guide/no-simple.png`
    };
    methods = {
        // 取消
        cancel() {
            App.globalFlag.autoChangeSimple = "Y";
            App.local.set("m_version_info", {autoChangeSimple: "Y" });
            this.hide();
        },
        // 切换简爱版
        versionSwitch() {
            this.hide();
            versionSwitchWithoutDialog();

        },
        /**
         * 判断是否去弹弹屏广告
         */
        // eslint-disable-next-line complexity
        async isToPup() {
            this.show();
        },
        show() {
            this.$refs.modal && this.$refs.modal.show();
        },
        hide() {
            this.$refs.modal && this.$refs.modal.hide();
            this.closed && this.closed();
        },
        onClose() {
            this.hide();
            this.$emit("close");
        }
        
    };
    computed = {
        iconType() {
            return this.type === "1" ? "cancel" : "search-delete";
        }
    };
}

export default async function install(App, ViewBase) {
    let viewOption = await new MSimpleGuide()._transformParam({});
    let SimpleGuideComponent = ViewBase.extend(viewOption);

    ViewBase.prototype.$simpleGuide = function(option) {
        let tempContainer = document.createElement("div");

        document.body.appendChild(tempContainer);

        let ins = new SimpleGuideComponent({
            el: tempContainer,
            propsData: option
        });

        setTimeout(() => {
            ins.isToPup();
        });

        return ins;
    };
}
