/* eslint-disable max-lines-per-function */
import { getJson } from "@ehas/request-ajax";
import menu from "./menu";
import { transferUrl } from "~/common//js/filters";

export default {
    data: undefined,
    fileName: undefined,
    // 获取全部协议列表数据
    async getData(needUpdate) {
        if (this.data && !needUpdate) return this.data;

        try {
            const bankId = App.bank.flags.common.bankId;
            await App.requester
                .request("cmm/agr/agrandpresqry.biz", {
                    data: {
                        busnTyp: "02" //协议
                    }
                })
                .then(rsp => {
                    let fileUrl = rsp.ompCdnFileRcdDtoEntity.fileUrl;
                    let lastIndexOf = fileUrl.lastIndexOf("/");
                    this.fileName = fileUrl.substr(lastIndexOf + 1);
                });
            const rsp = await getJson(`${ehasProcessCDNServer}/public/${bankId}/omp/appagr/${this.fileName}`);
            this.data = rsp.data || [];
            return this.data;
        } catch (error) {
            //记录错误信息
            window.$G.popErrorMsg(error);
            return [];
        }
    },

    // 获取当前菜单的协议列表数据
    async get() {
        const data = await this.getData();
        const fragmentUrl = App.router.fragmentUrl;
        const currentMenu = await menu.findByUrl(fragmentUrl);
        const acctLvl = App.storage.get("acctLvl");
        const eAccountAgreeFlag = App.bank.flags.account.eAccountAgreeFlag; //目前只有802行配置
        const signType = App.storage.get("signType");
        const eAccountAgreeOpenDiff = App.bank.flags.account.eAccountAgreeOpenDiff; //协议打开方式多法人配置
        if (currentMenu) {
            // eslint-disable-next-line max-statements
            return data.reduce((list, agreement) => {
                let newAgree = {};
                if (agreement.pgCd == "MBACT030007" && eAccountAgreeFlag) {
                    //电子账户开户二三类协议分开展示单独处理
                    if (agreement.subPgCd == acctLvl) {
                        if (
                            (acctLvl == "02" || acctLvl == "03") &&
                            agreement.sn == "01" &&
                            eAccountAgreeOpenDiff == "1"
                        ) {
                            let div = document.createElement("div");
                            div.innerHTML = transferUrl(agreement.gdCntnt);
                            let agreeUrl = div.children[0].getAttribute("href");
                            newAgree = null;
                            App.storage.remove("eAccountAgreeObj");
                            App.storage.set(
                                "eAccountAgreeObj",
                                {
                                    agreeType: acctLvl,
                                    agreeUrl: agreeUrl
                                },
                                true
                            );
                        } else {
                            newAgree =
                                agreement.pgCd === currentMenu.bsnCode
                                    ? { id: agreement.gdId, content: transferUrl(agreement.gdCntnt) }
                                    : null;
                        }
                    } else {
                        newAgree = null;
                    }
                    // 817快捷支付签约协议配置
                } else if (agreement.pgCd == "MBUSR010200") {
                    if (agreement.subPgCd == signType || agreement.subPgCd == "MBUSR010200") {
                        newAgree =
                            agreement.pgCd === currentMenu.bsnCode
                                ? { id: agreement.gdId, content: transferUrl(agreement.gdCntnt) }
                                : null;
                    } else {
                        newAgree = null;
                    }
                } else if (agreement.pgCd == "MBTSR011700") {
                    if (agreement.subPgCd == signType || agreement.subPgCd == "MBTSR011700") {
                        newAgree =
                            agreement.pgCd === currentMenu.bsnCode
                                ? { id: agreement.gdId, content: transferUrl(agreement.gdCntnt) }
                                : null;
                    } else {
                        newAgree = null;
                    }
                } else {
                    newAgree =
                        agreement.pgCd === currentMenu.bsnCode
                            ? { id: agreement.gdId, content: transferUrl(agreement.gdCntnt) }
                            : null;
                }
                return newAgree ? [...list, newAgree] : list;
            }, []);
        }
    },

    // 协议内文档链接的点击事件
    aClick(href, title) {
        const unshift = title.split("《");
        let lastOne = "";
        //如果存在书名号，则切割另外一个对应的书名号
        if (unshift.length > 1) {
            //将剩余的书名号切除掉
            lastOne = unshift[1].split("》");
        }
        const lastTitle = unshift.length > 1 ? lastOne[0] : unshift[0];
        App.jsApi.previewPdf({
            url: href,
            title: lastTitle
        });
    }
};
