import { LogByConsole } from "@ehas/log-console";export default {    beforeStart: function (App) {        class SelfLogByConsole extends LogByConsole {            customLogHandler(errorCode, errorMsg) {                const alertOptions = {                    title: "温馨提示",                    btnText: "确定",                    message: `${errorMsg}`,                    subInfo: `${errorCode}`                };
//EHAS_RESULT_BRANCH_START true 
                if (!errorCode.startsWith("EPFH035T")) {                    App.getViewInstance("content")?.$alert(alertOptions);                }
//EHAS_RESULT_BRANCH_END 
                if (errorCode.startsWith("EPFH035T")) {                    App.getViewInstance("content")?.$alert(alertOptions);                }            }        }        App.IContainer.inject(new SelfLogByConsole());    }};