import { getJson } from "@ehas/request-ajax";
import menu from "./menu";

export default {
    data: undefined,
    fileName: undefined,
    // 获取全部温馨提示数据
    async getData(needUpdate) {
        if (this.data && !needUpdate) return this.data;
        try {
            const bankId = App.bank.flags.common.bankId;
            await App.requester
                .request("cmm/agr/agrandpresqry.biz", {
                    data: {
                        busnTyp: "01" //温馨提示
                    }
                })
                .then((rsp) => {
                    let fileUrl = rsp.ompCdnFileRcdDtoEntity.fileUrl;
                    let lastIndexOf = fileUrl.lastIndexOf("/");
                    this.fileName = fileUrl.substr(lastIndexOf + 1);
                });
            const rsp = await getJson(`${ehasProcessCDNServer}/public/${bankId}/omp/apptips/${this.fileName}`);
            this.data = rsp.data;
            return this.data || [];
        } catch (error) {
            return [];
        }
    },

    // 获取当前菜单的温馨提示
    async get(_subPgCd) {
        const data = await this.getData();
        const { moduleName, menuName, pageName, params } = App.router.fragment;
        const formatUrl = `${moduleName}/${menuName}/${pageName}`;

        // 根据当前hash路由查询菜单信息, 如果未查到则使用带参数的url进行查询
        let currentMenu = await menu.findByUrl(formatUrl);
        if (!currentMenu) currentMenu = await menu.findByUrl(`${formatUrl}/${params}`);

        let tipsData = null;
        if (currentMenu) {
            const { bsnCode } = currentMenu;
            tipsData = data.find((agreement) => {
                // subPgCd: 为00 一对一关系, 非00 是一对多的关系
                const { subPgCd, pgCd } = agreement;
                return (subPgCd === "00" || subPgCd === _subPgCd) && pgCd === bsnCode;
            });
        }
        return {
            title: tipsData?.nm,
            content: tipsData?.cntnt
        };
    },

    // 温馨提示中的点击事件
    aClick(href) {
        menu.go(href);
    }
};
