import { View } from "@ehas/core";
import template from "./templates/index.vhtml";
class AlarmWord extends View {
    template = template;
    inheritAttrs = true;
    props = {
        custNm: String,
        pdNm: String,
        toDetail: Function
    };
    data = function () {
        return {
            bgImg: `${ehasProcessCDNServer}/public/999/h5/mbank/Common/alarm_word_bg.png`,
            iconClose: `${ehasProcessCDNServer}/public/999/h5/mbank/Common/icon_close.png`,
            show: true
        };
    };
    onCreated() {}
    methods = {
        handleClickDetail() {
            this.handleClickClose();
            this.toDetail();
        },
        handleClickClose() {
            this.show = false;
        }
    };
}

export default async function install(App, ViewBase) {
    let viewOption = await new AlarmWord()._transformParam({});
    let AlarmWordComponent = ViewBase.extend(viewOption);

    ViewBase.prototype.$alarmWord = function (option = {}) {
        let tempContainer = document.createElement("div");

        document.body.appendChild(tempContainer);

        let ins = new AlarmWordComponent({
            el: tempContainer,
            propsData: option
        });

        return ins;
    };
}
