import { transferUrl } from "~/common/js/filters.js";
// 请求PDF接口
export function getPdfqry(agrTyp) {
    App.requester.request("hlp/fincast/ebkatclqry.biz", {
        data: {
            agrTyp: agrTyp
        }
    }).then(res => {
        // 组装可访问路径
        if (res?.skpPath) {
            res.skpPath = transferUrl(res.skpPath);
            openFile(res.skpPath, res?.ttl);
        } else {
            App.getViewInstance("content").$confirm({
                message: "敬请期待",
                sureText: "确定",
                cancelText: ""
            });
        }
    });
}
export function openFile(fileUrl, name) {
    App.jsApi
        .previewPdf({
            url: fileUrl,
            title: name
        })
        .then(() => {
            return false
        });
}