/* eslint-disable */
import menu from "../common/datas/menu";
import { transferUrl } from "../common/js/filters";
import { pictureJump } from "~/common/utils/ad";
import { globalUtil } from "~/common/js/globalUtil.js";
import { isNewStartup } from "~/common/utils/check.js";
import { isLogin } from "~/common/utils/check.js";

// 关闭浮窗的页面列表
let noneMenuList = [];
let timer = null;
let el = document.querySelector("#floatBox");
let FloatObject = {
    startY: "",
    getHeight() {
        return el.getBoundingClientRect()?.height;
    },
    getTop() {
        return el.getBoundingClientRect()?.top;
    },
    getBottom() {
        return el.getBoundingClientRect()?.bottom;
    },
    createFloatBox(App, item) {
        el.style.display = "block";
        // 浮窗图
        // 日签分享需要通过以下方法选择浮窗广告dom进行dom操作，以下方法修改需要同步修改日签分享
        let img = el.querySelector(".ad-img");

        // 关闭
        let icon = el.querySelector(".clear-icon");

        img.src = transferUrl(item.pctUrl);
        img.setAttribute("data-item", JSON.stringify(item));
        img.setAttribute("wai-text", item.advgDsc);
        el.removeEventListener("touchmove", FloatObject.elMoveHandler);
        img.removeEventListener("click", imgClick);
        img?.addEventListener("click", imgClick);

        // 关闭按钮事件
        icon?.addEventListener("click", async () => {
            // 将关闭浮窗的页面bsnCode添加到noneMenuList里
            let menuInfo = await menu.findByUrl(App.router.fragmentUrl);
            noneMenuList = noneMenuList.concat(menuInfo.bsnCode);
            el.style.display = "none";
            //解除监听touch事件
            el.removeEventListener("touchstart", FloatObject.touchstartadd);
            el.removeEventListener("touchend", FloatObject.touchEndEventadd);
            el.removeEventListener("touchmove", FloatObject.elMoveHandler);
            window.removeEventListener("touchmove", FloatObject.floatBoxRight, false);
            window.removeEventListener("touchend", FloatObject.touchEndEvent, false);
        });
        el?.addEventListener("touchstart", FloatObject.touchstartadd, false);
        window?.addEventListener("touchstart", FloatObject.touchstartEvent, false);
        //添加监听touch事件
        // 滑动中
        el?.addEventListener("touchmove", FloatObject.elMoveHandler, false);
        window?.addEventListener("touchmove", FloatObject.floatBoxRight, false);

        // 滑动结束
        el?.addEventListener("touchend", FloatObject.touchEndEventadd, false);
        window?.addEventListener("touchend", FloatObject.touchEndEvent, false);
    },
    touchstartEvent(e) {
        FloatObject.startY = e.targetTouches[0]?.clientY;
    },
    touchstartadd() {
        FloatObject.disableRouter() && App.jsApi.disablePulldownRefresh();
    },
    touchEndEventadd() {
        FloatObject.disableRouter() && App.jsApi.enablePulldownRefresh();
    },
    touchEndEvent(e) {
        FloatObject.showFloatBox();
        FloatObject.antiCoincidence(e);
    },
    disableRouter() {
        return (
            globalUtil.refreshByNativePulldown?.() &&
            globalUtil.routersNeedNativePulldown.includes(App.router.fragmentUrl)
        );
    },
    /**
     * 防止活动浮窗与求助浮窗重合
     */
    antiCoincidence(e) {
        const clientY = e.changedTouches[0]?.clientY;
        // 获取意见反馈的位置信息
        const flotHelp = document.getElementById("move_id");
        const { top, bottom } = flotHelp?.getBoundingClientRect() || {};
        const floatBoxHeight = FloatObject.getHeight();
        const floatBoxTop = FloatObject.getTop();
        const floatBoxBottom = FloatObject.getBottom();
        const floatBoxCenter = floatBoxTop + floatBoxHeight / 2;
        // 判断活动浮窗的上边、中心点、底边是否在求助浮窗内
        const topIsInFloatHelp = floatBoxTop >= top && floatBoxTop <= bottom;
        const bottomIsInFloadHelp = floatBoxBottom >= top && floatBoxBottom <= bottom;
        const centerIsInFloadHelp = floatBoxCenter >= top && floatBoxCenter <= bottom;
        if (topIsInFloatHelp || bottomIsInFloadHelp || centerIsInFloadHelp) {
            // 根据拖动方向，将活动浮窗移动到求助浮窗上方或者下方
            let newTop;
            if (FloatObject.startY - clientY > 0) {
                // 上滑
                newTop = top - 20;
            } else {
                // 下滑
                newTop = bottom + floatBoxHeight + 20;
            }
            el.setAttribute("style", `display:block;bottom: calc(100vh - ${newTop}px);`);
        }
    },

    // 3秒后收起
    setTimeoutHide() {
        timer && clearTimeout(timer);
        timer = setTimeout(() => {
            FloatObject.floatBoxRight();
        }, 3000);
    },
    // el对象
    elMoveHandler(e) {
        FloatObject.floatBoxRight(e);
        e.preventDefault();
    },
    // window对象不阻止默认事件
    floatBoxRight(e) {
        if (!FloatObject.adsorptionFollowing(e)) {
            // 浮窗图
            el.className = "ad-img-trasnition--right";
        }
    },
    /**
     * 拖动活动弹窗吸附跟随
     */
    adsorptionFollowing(e) {
        let target = e.target;
        let isWrapper = target.getAttribute("id") == "floatBox";
        let isAdImg =
            target.className && typeof target.className.indexOf === "function"
                ? target.className.indexOf("ad-img") != -1
                : false;
        let isCloseIcon =
            target.className && typeof target.className.indexOf === "function"
                ? target.className.indexOf("clear-icon") != -1
                : false;
        // 只有滑动目标是活动弹窗、图片、关闭图标时才被拖动
        if (isWrapper || isAdImg || isCloseIcon) {
            const { clientY } = e.targetTouches[0];
            const height = FloatObject.getHeight();
            // 活动浮窗的中心点跟随手势滑动
            const top = clientY + height / 2;
            // 设置滑动浮窗底边距
            el.setAttribute("style", `display: block;bottom: calc(100vh - ${top}px);`);
            return true;
        }
        return false;
    },

    showFloatBox() {
        // 浮窗图
        el.className = "ad-img-trasnition--left";
    },
    // 隐藏 浮窗图
    floatBoxNone() {
        el.style.display = "none";
    }
};
function imgClick() {
    const item = JSON.parse(this.getAttribute("data-item"));
    //浮窗运营位埋点
    if (item.opclId) {
        App.DC.onEvent(item.opclId, {
            OPCL_ADVG_NO: item.advgNo || "",
            OPCL_ADVG_TTL: item.advgTtl || ""
        });
    }
    pictureJump(item);
}

export default {
    el,
    afterStart: async function(App) {
        let list = [];

        if (App.jsApi.isInApp()) {
            let rsp = {};
            try {
                // 开始时间
                const startTime = window.$G.getCurrentTime();
                rsp = await App.jsApi.getAdInfoByCache();
                // 推送时间线给客户端
                window.$G.pushStartupTimeLine("afterStart-getAdInfoByCache", "浮窗广告拉取运营位", startTime);
            } catch (e) {
                rsp = {};
                window.$G.logErrorWithCode("src/config/floatBoxSetting.js推送时间线给客户端异常", JSON.stringify(e));
            }
            if (rsp.code && rsp.code == "0000") {
                if (rsp?.result) {
                    for (let value in rsp.result) {
                        if (value.indexOf("OPCL03") > -1) {
                            list = list.concat(rsp.result[value]);
                        }
                    }
                }
            } else {
                return;
            }
        } else {
            const startTime = window.$G.getCurrentTime();
            await App.requester
                .request(`cmm/opcl/opclmanage.biz`, {
                    data: { custNo: "" },
                    resolution: true,
                    slient: true
                })
                .then(rsp => {
                    if (rsp?.hashMap) {
                        for (let value in rsp.hashMap) {
                            if (value.indexOf("OPCL03") > -1) {
                                list = list.concat(rsp.hashMap[value]);
                            }
                        }
                    }
                });
            // 推送时间线给客户端
            window.$G.pushStartupTimeLine(
                "afterStart-getAdInfoByCache",
                "浮窗广告拉取运营位：opclmanage.biz",
                startTime
            );
        }

        App.router.on("before", (e, routerInfo) => {
            let startTime = window.$G.getCurrentTime();
            let newUrl = routerInfo.newFragmentUrl;
            // 非新首页浮窗广告
            if (!isNewStartup()) {
                menu.findByUrl(newUrl).then(menuInfo => {
                    if (menuInfo && list.length > 0) {
                        // 是否被关闭
                        let isnone = noneMenuList.some(item => {
                            return menuInfo.bsnCode === item;
                        });
                        if (isnone) {
                            FloatObject.floatBoxNone();
                            return;
                        }

                        // 是否在设置列表中
                        let FloatBoxFlag = list.some(item => {
                            return menuInfo.bsnCode === item?.pageId;
                        });

                        // 页面是否配置浮动广告
                        if (FloatBoxFlag) {
                            for (let i = 0; i < list.length; i++) {
                                if (menuInfo.bsnCode === list[i]?.pageId) {
                                    FloatObject.createFloatBox(App, list[i]);
                                    break;
                                }
                            }
                        } else {
                            // 没有配置则将浮动广告隐藏
                            FloatObject.floatBoxNone();
                        }
                    }
                });
            }

            // 浮窗广告before切面的耗时
            window.$G.pushStartupTimeLine("routerBefore_floatBoxSetting", "routerBefore: 浮窗广告配置", startTime);
        });
        // 809出现底部弹窗时，浮窗上移
        App.router.on("after", async () => {
            await isLogin().then(async res => {
                if (res && App.router.fragmentUrl === "FloorHome/Home/Index") {
                    let lgncstinfo = (await App.storage.get("login_check_lgncstinfo")) || [];
                    lgncstinfo?.LIST?.forEach(item => {
                        if (item.rmndTyp == "05") {
                            if (document.getElementsByClassName("elegant-footertab-snackbar").length) {
                                document.getElementById("floatBox").style.bottom = "305px";
                            }
                        }
                    });
                }
            });
            // 新首页浮窗广告
            if(isNewStartup()) {
                menu.findByUrl(App.router.fragmentUrl).then(menuInfo => {
                    if (menuInfo && list.length > 0) {
                        // 是否被关闭
                        let isnone = noneMenuList.some(item => {
                            return menuInfo.bsnCode === item;
                        });
                        if (isnone) {
                            FloatObject.floatBoxNone();
                            return;
                        }
    
                        // 是否在设置列表中
                        let FloatBoxFlag = list.some(item => {
                            return menuInfo.bsnCode === item?.pageId;
                        });
    
                        // 页面是否配置浮动广告
                        if (FloatBoxFlag) {
                            for (let i = 0; i < list.length; i++) {
                                if (menuInfo.bsnCode === list[i]?.pageId) {
                                    App.jsApi.showFloatWindow({
                                        url: list[i]?.pctUrl,
                                        params: JSON.stringify(list[i])
                                    });
                                    break;
                                }
                            }
                        } else {
                            // 没有配置则将浮动广告隐藏
                            FloatObject.floatBoxNone();
                        }
                    }
                });
            }
        });
    }
};
