import { div } from "~/common/js/calculation";
import { toNumVersion } from "./format";
import { WapWeChatUtil } from "~/common/js/wap";
/* eslint-disable */
function checkSensitive(val) {
    let matches = {
        pattern: /^!@([^|]+)\|([^|]+)/
    };
    //校验数据是否符合脱敏数据规则
    return matches.pattern.test(val);
}

/**
 * 判断是否是本行
 * @param {*} unionBankNo 联行号
 * @returns {*} true:是本行,false:不是本行
 */
function isCurrBank(unionBankNo) {
    let currBankNo = App.dict.get("bankInfo", "unionBankNo");
    if (currBankNo === unionBankNo) {
        return true;
    }
    return false;
}

/**
 * 判断用户是否登录
 * @returns {*} Boolean:true-登录,false-未登录
 */
async function isLogin() {
    //wap微信业务
    if (WapWeChatUtil.isWXBusiness()) {
        return WapWeChatUtil.isWXLogin();
    }
    const isInApp = await App.jsApi.isInApp();
    if (isInApp) {
        try {
            const loginObj = await App.jsApi.isLogin();
            const loginFlag = loginObj.result.isLogin;
            if (loginFlag) {
                return true;
            }
            return false;
        } catch (error) {
            return false;
        }
    } else {
        return App.storage.get("m-login-flag");
    }
}

/**
 * 判断是否是首页频道页和转账首页
 * 如果传路径就取传值，否则默认原来的首页和转账首页
 */
function isNotBulletFrame(urlList) {
    const requestUrl = App.router.fragmentUrl;
    const routerUrlList = urlList || ["FloorHome/Home/Index", "Transfer/Transfer/Index"];
    //如果获取的URL是首页或者转账首页的路径则返回true，否则返回false
    return routerUrlList.includes(requestUrl) ? true : false;
}

function checkPhoneNo(str) {
    return /^1[3-9]{1}[0-9]{9}$/.test(str);
}

/**
 * 忘记密码找回入口开放标志
 * 开户渠道为03 和49，面签标志为强弱面签的账户允许
 * 账户等级不为 1，一类户不展示忘记密码
 */
function isShowforgetPassword(acctInfo) {
    if (acctInfo) {
        if (
            acctInfo.acctLvlCd != "1" &&
            (acctInfo.acctOpenChnlNo == "03" || acctInfo.acctOpenChnlNo == "49") &&
            acctInfo.strSwkSgnfcFlg != "A"
        ) {
            return true;
        } else {
            return false;
        }
    } else {
        return false;
    }
}

/**
 * 判断功能是否开通
 * @param {*} txCd 功能交易码
 */
function queryMenuState(txCd) {
    return App.requester
        .request("cmm/param/bizparamqry.biz", {
            data: {
                txCd: txCd
            },
            useDefaultError: false
        })
        .then(rsp => {
            // effFlg 生效标识 为"Y"则功能开通，为"N"或者接口报错则功能关闭
            if (rsp.effFlg === "Y") {
                return true;
            }
            return false;
        })
        .catch(() => {
            return false;
        });
}

/**
 *
 * @param {} acctInfo
 * 借记卡1类户、面签2、3类户、存折、定活一本通
 * 04 活折   05 定活一本通
 */
function acctNoUnlockFlag(acctInfo) {
    if (acctInfo) {
        for (let item of acctInfo.acctStsDtls) {
            //账户锁定状态
            if (item.type == "2" && item.code == "3") {
                if (acctInfo.acctNoTyp == "04" || acctInfo.acctNoTyp == "05") {
                    //存折  活期一本通
                    return true;
                } else if (acctInfo.acctNoTyp == "01" && acctInfo.acctLvlCd == "1") {
                    //本行借记卡并且是一类户
                    return true;
                } else if (acctInfo.acctNoTyp == "01" && acctInfo.acctLvlCd != "1" && acctInfo.strSwkSgnfcFlg == "A") {
                    //面签2、3类户
                    return true;
                } else {
                    return false;
                }
            }
        }
    } else {
        return false;
    }
}

/**
 *
 * @param {} acctInfo
 * 借记卡1类户、面签2、3类户
 *
 */
function acctNoPswdModFlag(acctInfo) {
    if (acctInfo) {
        if (acctInfo.acctNoTyp == "01" && acctInfo.acctLvlCd == "1") {
            //本行借记卡并且是一类户
            return true;
        } else if (acctInfo.acctNoTyp == "01" && acctInfo.acctLvlCd != "1" && acctInfo.strSwkSgnfcFlg == "A") {
            //面签2、3类户
            return true;
        } else {
            //801行特殊处理，存折也放开密码修改功能
            let acctNoPswdFlag = App.bank.flags.account?.acctNoPswdFlag;
            if (acctNoPswdFlag && (acctInfo.acctNoTyp == "04" || acctInfo.acctNoTyp == "05")) {
                return true;
            } else {
                return false;
            }
        }
    } else {
        return false;
    }
}

/**
 * 判断是否是object格式
 * @param {*} obj 数据
 * @returns {*} obj
 */
function isObject(obj) {
    const type = typeof obj;
    return type === "function" || (type === "object" && !!obj);
}

/**
 * 判断当前菜单状态是否处于非正常状态, 0:禁用 3:暂停
 * */

function isMenuDisabled(menuInfo) {
    const viewInstance = App.getViewInstance("content");
    const message = "该功能暂不可用";
    const btnText = "确定";

    if (menuInfo.stt === "0") {
        viewInstance.$alert({ message, btnText });
        return true;
    }
    if (menuInfo.stt === "3") {
        viewInstance.$alert({
            message: menuInfo.tip || message,
            btnText
        });
        return true;
    }
}
/**
 * 禁用视频银行弹窗提示
 */
function remoteBankMenuDisabled(bsnCode) {
    let clientVersion = App.jsApi.getOsVer();
    const isAndroid = App.jsApi.isInAndroid();
    const osVerUnSupport = toNumVersion(clientVersion) < toNumVersion("7.0.0");
    if (isAndroid && osVerUnSupport && App.bank.flags?.common?.remoteBankPopupHint && bsnCode === "MBCMM120000") {
        isMenuDisabled({
            stt: "3",
            tip: "系统正在进行升级，请您通过登录“烟台银行远程银行”微信小程序办理业务，感谢支持！"
        });
        return true;
    } else if (
        App.bank.flags?.common?.remoteBankMenuDisabled &&
        App.jsApi.isInIOS() &&
        bsnCode === "MBCMM040000" &&
        App.jsApi.getAppVerion() === "6.0.0"
    ) {
        // 810我的页签视频银行控制ios6.0.0点击报敬请期待，其他版本能正常使用
        isMenuDisabled({ stt: "3", tip: "敬请期待" });
        return true;
    }
}
/**
 *
 * @param {*} 是否是折叠屏
 *
 */
function isMaxWidthScreen() {
    let scale = div(document.body.clientWidth, document.body.clientHeight);
    if (scale > 0.6) {
        return true;
    } else {
        return false;
    }
}
/**
 * 是否是新客户端
 * @return { launcherType } String 1 新启动流程，0 或者没有为老流程
 */
function isNewStartup() {
    const UA = window.navigator.userAgent.toLowerCase();
    const launcherTypeReg = /@launcherType\(1\)/gi;
    return launcherTypeReg.test(UA);
}

/**
 * 是否为新客户端预加载打开的webview
 */
function preloadStyleIsNormal() {
    const UA = window.navigator.userAgent.toLowerCase();
    const launcherTypeReg = /@preloadStyle\(1\)/gi;
    return launcherTypeReg.test(UA);
}

function preloadStyleIsDialog() {
    const UA = window.navigator.userAgent.toLowerCase();
    const launcherTypeReg = /@preloadStyle\(2\)/gi;
    return launcherTypeReg.test(UA);
}

/**
 * 是否为新客户端中首页所在webview
 */
 function isHomeView() {
    const UA = window.navigator.userAgent.toLowerCase();
    const viewTypeReg = /@isHomeView\(YES\)/gi;
    return viewTypeReg.test(UA);
}  
export {
    checkSensitive,
    isCurrBank,
    isLogin,
    checkPhoneNo,
    isNotBulletFrame,
    isShowforgetPassword,
    queryMenuState,
    acctNoUnlockFlag,
    acctNoPswdModFlag,
    isObject,
    isMenuDisabled,
    remoteBankMenuDisabled,
    isMaxWidthScreen,
    isNewStartup,
    preloadStyleIsNormal,
    preloadStyleIsDialog,
    isHomeView
};
