import depSession from "../modules/Deposit/session.storage";
import cdtsSession from "../modules/CDTS/session.storage";
import ftsSession from "../modules/FTS/session.storage";
import fudSession from "../modules/Fund/session.storage";
import welSession from "../modules/Wealth/session.storage";
import welpoSession from "../modules/WealthPO/session.storage";
import childSession from "../modules/ChildCard/session.storage";
import financeSession from "../modules/Finance/session.storage";
import financeHSSession from "../modules/FinanceHS/session.storage";
import financeQLSession from "../modules/FinanceQL/session.storage";
import floorSession from "../modules/FloorHome/session.storage";
import statement from "../modules/Statement/session.storage";
