import commonLocal from "./local.storage";
import simLocal from "../modules/SimpleHome/local.storage";
import minLocal from "../modules/Mine/local.storage";
import floorLocal from "../modules/FloorHome/local.storage";
import vidLocal from "../modules/Video/local.storage";
import staLocal from "../modules/Statement/local.storage";
import braLocal from "../modules/BranchService/local.storage";
import intLocal from "../modules/IntelligentService/local.storage";
import safeLocal from "../modules/SafeCenter/local.storage";
import msgLocal from "../modules/MessageCenter/local.storage";
import lgnLocal from "../modules/Login/local.storage";
import escLocal from "../modules/Search/local.storage";
import fundLocal from "../modules/Fund/local.storage";
import inveLocal from "../modules/Investment/local.storage";
import depositLocal from "../modules/Deposit/local.storage";
import nobleMetalLocal from "../modules/nobleMetal/local.storage";
import productExperienceSession from "../modules/ProductExperience/session.storage";
import therePartDepSession from "../modules/TherePartDepository/session.storage";
import insuranceLocal from "../modules/Insurance/local.storage";
import inpSession from "../modules/IntelligentService/session.storage";
import GidSession from "../modules/Gid/session.storage";
import digitalWalletSession from "../modules/DigitalWallet/session.storage";
import InsureSession from "../modules/Insurance/session.storage";
import consignmentamSession from "../modules/ConsignmentAM/session.storage";
import utilitiesSession from "../modules/Utilities/session.storage";
import astLocal from "../modules/Assistant/local.storage";
import elecHealthInsCardSession from "../modules/ElecHealthInsCard/session.storage";
import crdSession from "../modules/CreditCard/session.storage";
export default {
    // eslint-disable-next-line max-lines-per-function
    beforeStart: function(App) {
        App.config.sessionStorageName = `${App.bank.flags.common.bankId}_mb_sccba_session`;
        App.config.localStorageName = `${App.bank.flags.common.bankId}_mb_sccba_local`;

        //Session
        let sessionStorage = new SessionStorage();

        App.IContainer.inject(sessionStorage);

        //local
        let localStorage = new LocalStorage();
        let nativeStorage = new NativeStorage();
        if (App.jsApi.isInApp()) {
            App.IContainer.inject(nativeStorage);
        } else {
            App.IContainer.inject(localStorage);
        }
    },
    afterStart(App) {
        new StorageCheck(App, [
            videoSession,
            videoLocal,
            actSession,
            afeSession,
            astSession,
            brsSession,
            lgnSession,
            minSession,
            ctfSession,
            escSession,
            trnSession,
            loaSession,
            cdtsSession,
            depSession,
            ftsSession,
            fudSession,
            welSession,
            welpoSession,
            childSession,
            financeSession,
            financeHSSession,
            financeQLSession,
            floorSession,
            statement,
            msgSession,
            fundGatherSession,
            commonSession,
            commonLocal,
            simLocal,
            minLocal,
            floorLocal,
            vidLocal,
            staLocal,
            braLocal,
            intLocal,
            safeLocal,
            msgLocal,
            yebSession,
            lgnLocal,
            escLocal,
            fundLocal,
            signLocal,
            inveLocal,
            scanSession,
            depositLocal,
            nobleMetalLocal,
            nobleMetalSession,
            salaryFinanceSession,
            distributionAreaSession,
            isShowAllInfo,
            noCardWithdraw,
            accountAuthWithScan,
            productExperienceSession,
            therePartDepSession,
            insuranceLocal,
            inpSession,
            GidSession,
            digitalWalletSession,
            InsureSession,
            consignmentamSession,
            utilitiesSession,
            elecHealthInsCardSession,
            astLocal,
            crdSession
        ]);
    }
};
