async function getRandomData(ctrl) {
    const module = ctrl.$attrs.pswdModule;
    const _randomData = async () => {
        const randomNumObj = await App.requester.request(`${module}/pswd/pswdrandomqry.biz`, {
            useDefaultError: false,
            resolution: true,
            slient: true
        });
        return randomNumObj.randomNum;
    };
    try {
        const random = await _randomData();
        return random;
    } catch (error) {
        window.$G.logErrorWithCode("src/config/uiSetting3.js随机数请求失败，重新请求", JSON.stringify(error), {
            message: "第一次请求随机数失败，第二次请求",
            stack: `${module}/pswd/pswdrandomqry.biz`
        });
        try {
            const random = await _randomData();
            return random;
        } catch (error) {
            window.$G.logErrorWithCode("src/config/uiSetting3.js随机数请求失败，重新请求", JSON.stringify(error), {
                message: "第二次请求随机数失败，停止发送请求",
                stack: `${module}/pswd/pswdrandomqry.biz`
            });
            return false;
        }
    }
}
