import { getSdkVersion, getAppVerion } from "@ehas/app-jsapi";
//当前zip包信息
function getZipInfo() {    
    return {
        // zip包名称
        zipName: App.router.zipName ? App.router.zipName.split(".")[0] : "",
        // zip包版本号
        moduleZipVersion: App.router.zipVersion
    }
}
// 获取菜单信息
async function getMenuInfo() {
    // 获取菜单编码
    return await window.$G.getMenuId(true)
        .then((menuInfo) => {
            return {
                //菜单码
                menuId: menuInfo ? menuInfo.bsnCode : "",
                //菜单名称
                menuName: menuInfo ? menuInfo.bsnName : ""
            };                
        })
        .catch((err) => {
            window.$G.jsApiErrorMsg(err);
        });    
}
// 获取客户端信息
async function getClientInfo() {
    return await App.jsApi.getNativeInfo().catch((err) => {
        window.$G.jsApiErrorMsg(err);
    });
}
// 获取历史记录信息 
const collectorHistory = []
export function addHistoryInfo(routerInfo) {    
    // 只上送最近10个 多了出队
    if (collectorHistory.length > 10) {
        collectorHistory.shift()
    }
    collectorHistory.push(routerInfo)
}

export default async function getCollectorInfo() {

    const menuInfo = await getMenuInfo() || {};
    const clientInfo = await getClientInfo() || {};
    const zipInfo = getZipInfo() || {};

    return  {
        bkId: App.bank.flags.common.bankId,
        timeStamp: Date.now(),        
        appVer: getAppVerion(),
        sdkVer: getSdkVersion(),
        hash: window.location.href,
        history: collectorHistory,
        clientInfo: clientInfo.result,
        menuInfo,
        zipInfo
    }
}