import { dateUtils } from "@ehas/utils"; //导入日期处理类
import { fastTakingDown, addCard } from "~/common/utils/account";
import { isLogin as checkIsLogin } from "~/common/utils/check";
//import { fmtSensitiveData } from "~/common/js/filters";
import { ViewBase } from "@ehas/core";
import { transferUrl, fmtDate } from "~/common/js/filters.js"; //协议路径处理工具
import "@components/m-pdf/Index.js";
import { nineElementsErrorFunc } from "~/common/utils/error";
import { goToLogin, dealLogin } from "~/common/utils/login.js";
import { getUrlParams, getDomainName } from "~/common/utils/https.js";
import { getloginUserNm, checkCustomerAuthInfo, checkCustomerAuth } from "~/common/utils/user";
class FinanceUtil {
    /**
     * 理财购买前校验，可购买返回true，不可购买返回false
     * @param {*} param0 产品数据，包括pdCd,chnlPdNo,sourceNo
     * @param {*} cancel 校验不通过，点击取消时的回调函数
     */
    static async checkFinCustInfo({ pdCd, chnlPdNo, sourceNo } = {}, cancel = null, fromFts = false) {
        // 判断是否登录
        const isLogin = await checkIsLogin();
        if (!isLogin) {
            //登录后继续执行
            await goToLogin("", {}, { type: 1, selfRefreshFlag: true });
            //跳转登录再返回时判断是否登录，登录则继续校验
            const isFinish = await checkIsLogin();
            if (!isFinish) {
                return false;
            }
        }
        // 校验用户权限等级（是否加挂本行借记卡或存折）
        const authFlg = await checkCustomerAuth(2, cancel);
        if (!authFlg) {
            return false;
        }

        //查询产品详情，防止剩余额度校验相关数据不全或数据有问题
        let { finDetailEntity } = await FinanceUtil.queryDetail({ pdCd, chnlPdNo, sourceNo });

        //如果不是从理财转让进入，则校验剩余的额度
        if (!fromFts) {
            // 校验剩余额度
            const remLmtFlg = await checkRemLmt(finDetailEntity);
            if (!remLmtFlg) {
                return false;
            }
        }

        // 校验签约风评信息
        return await querySign({ pdCd: pdCd, chnlPdNo: chnlPdNo, sourceNo: sourceNo }, querySignResolve);

        //不可购买时展示confirm弹窗
        function showPopup({
            title = "",
            message = "",
            sureText = "",
            cancelText = "取消",
            url = "", //确认时，跳转的路径
            param = "", //确认时，跳转的路径，传递的参数
            cb = null //确认时,回调函数
        }) {
            App.getViewInstance("content").$confirm({
                title: title,
                message: message,
                cancelText: cancelText,
                sureText: sureText,
                closed: isSure => {
                    if (isSure) {
                        if (cb && typeof cb === "function") {
                            cb();
                        }
                        if (url) {
                            window.$G.goToNextRetain({ router: url, param: param });
                        }
                    } else {
                        cancel && cancel();
                    }
                }
            });
        }

        // 校验剩余额度
        async function checkRemLmt(finDetailEntity) {
            //pdCd产品代码、strtBuyAmt起购金额、incmAmt递增金额、remLmt剩余额度
            const { pdCd, strtBuyAmt, incmAmt, remLmt } = finDetailEntity;
            //剩余额度大于等于起购金额,或者不控制额度时
            if (Number(remLmt) >= Number(strtBuyAmt) || !remLmt) return true;
            //查询是否为首次购买
            const firstBuyFlg = await FinanceUtil.queryFirstBuy(pdCd);
            if (firstBuyFlg) {
                //首次购买且剩余额度小于起购金额
                showPopup({
                    message: `剩余额度不足起购金额，暂不能购买`,
                    cancelText: "我知道了"
                });
                return false;
            } else if (Number(remLmt) < Number(incmAmt)) {
                //非首次购买且剩余额度小于递增金额
                showPopup({
                    message: `剩余额度不足，暂不能购买`,
                    cancelText: "我知道了"
                });
                return false;
            } else {
                //非首次购买且剩余额度大于递增金额
                return true;
            }
        }
        // 查询签约风评信息
        function querySign({ pdCd, sourceNo } = {}, cb) {
            return App.requester
                .request("fin/finsign/fincustsigncheck.biz", {
                    submitKey: true,
                    data: {
                        pdCd: pdCd,
                        sourceNo: sourceNo
                    },
                    useDefaultError: false
                })
                .then(rsp => {
                    if (cb && typeof cb === "function") {
                        return cb(rsp);
                    }
                })
                .catch(rsp => {
                    if (rsp.code === "EPFP218T") {
                        //针对证件过期单独处理异常
                        showPopup({
                            title: "",
                            message: "您在我行预留的证件已过有效期，为保证您的账户资金安全，请及时更新证件信息",
                            sureText: "立即更新",
                            url: "#Mine/Setting/IdentityInfo",
                            param: { backRouter: location.hash }
                        });
                    } else {
                        // TODO 跳转到公共完善用户信息页面
                        nineElementsErrorFunc(rsp, cancel);
                    }
                });
        }
        // 校验签约风评信息
        async function querySignResolve(rsp) {
            /** custRskLvl：客户风险等级
             * pdRskLvl：产品风险等级
             * pdRskLvlNm：产品风险等级名称
             * custSgnFlg：理财签约标志
             * highRiskFlag 0 否 1 是
             * riskMatch：风险匹配标志 D：过期 E：是未风评 Y：匹配 N：不匹配 */
            const { custSgnFlg, riskMatch, pdRskLvl, pdRskLvlNm, highRiskFlag } = rsp;
            // 未签约
            if (custSgnFlg === "N" || custSgnFlg === "") {
                showPopup({
                    message: "您尚未进行理财签约，请至柜面进行理财签约后才可进行理财购买。",
                    cancelText: "我知道了",
                    sureText: "网点预约",
                    url: "#BranchService/BranchInquery/Index",
                    param: { backRouter: location.hash }
                });
                return false;
            }
            // 判断是否是四级（含）以上的理财产品,highRiskFlag判断是否开通电子购买渠道,0未开通
            if (pdRskLvl && Number(pdRskLvl) >= 4 && highRiskFlag === "0") {
                showPopup({
                    message: "因该产品风险评级为四级以上理财产品，您需在营业网点进行柜面书面约定。",
                    cancelText: "我知道了",
                    sureText: "网点预约",
                    url: "#BranchService/BranchInquery/Index",
                    param: { backRouter: location.hash }
                });
                return false;
            }
            // 已签约,未风评
            if (custSgnFlg === "Y" && riskMatch === "E") {
                showPopup({
                    title: "尚未风险测评，暂不可购买",
                    message: "为了您的资金安全，首次风评需要在网点面签确认，确认后才可进行理财购买。",
                    cancelText: "我知道了",
                    sureText: "网点预约",
                    url: "#BranchService/BranchInquery/Index",
                    param: { backRouter: location.hash }
                });
                return false;
            }

            // 风评过期
            if (custSgnFlg === "Y" && riskMatch === "D") {
                showPopup({
                    message: "尊敬的客户，您的风险承受能力评估已经过期，按监管要求，您需要在购买前重新完成风险评估",
                    cancelText: "我知道了",
                    sureText: "重新评估",
                    url: `#${App.bank.flags.finance.module}/RiskAssessment/Questions`,
                    param: {
                        backRouter: location.hash
                    }
                });
                return false;
            }

            // 风险等级不匹配
            if (custSgnFlg === "Y" && riskMatch === "N") {
                //多法人参数控制，是否引导重新风评
                if (App.bank.flags.finance.reRiskQuestions) {
                    showPopup({
                        message: `当前购买产品为${pdRskLvlNm}产品，与您的风险等级不匹配，请根据您的实际情况重新进行风评。`,
                        cancelText: "我知道了",
                        sureText: "风险测评",
                        url: `#${App.bank.flags.finance.module}/RiskAssessment/Questions`,
                        param: {
                            backRouter: location.hash
                        }
                    });
                } else {
                    showPopup({
                        message: `当前购买产品为${pdRskLvlNm}产品，与您的风险等级不匹配，请谨慎投资。`,
                        cancelText: "我知道了"
                    });
                }
                return false;
            }
            return true;
        }
    }

    // 查询是否首次购买
    static async queryFirstBuy(pdCd) {
        let module = App.bank.flags.finance.module,
            url = "",
            strtDt = "";
        if (module == "Finance") {
            //金证理财平台，交易记录接口,交易时间跨度送一年
            url = "fin/transrecord/jztransrecordlistqry.biz";
            strtDt = dateUtils.subtractDate(dateUtils.nowDate("YYYYMMDD"), "1", "year", "YYYYMMDD", "YYYYMMDD");
        } else {
            //恒生、齐鲁理财平台，交易记录接口，交易时间跨度送三年
            url = "fin/transrecord/hstransrecordlistqry.biz";
            strtDt = dateUtils.subtractDate(dateUtils.nowDate("YYYYMMDD"), "3", "year", "YYYYMMDD", "YYYYMMDD");
        }
        return App.requester
            .request(url, {
                data: {
                    pdCd: pdCd,
                    txTyp: "",
                    strtDt: strtDt,
                    endDt: dateUtils.nowDate("YYYYMMDD"),
                    turnPageBeginPos: 1,
                    turnPageShowNum: 10,
                    custIdTyp: "1"
                }
            })
            .then(rsp => {
                //恒生、齐鲁理财平台 :hsTransRecordEntityList
                //金证理财平台:transRecordList
                let TransRecordList = rsp.hsTransRecordEntityList || rsp.transRecordList;
                //首次购买
                return TransRecordList.length == 0;
            });
    }
    /**
     * 查询该卡是否首次购买该产品,首次购买返回true
     * @param {*} pdCd 产品代码
     * @param {*} txAcctNo 付款账户
     */
    static async queryCardFirstBuy(pdCd, txAcctNo, sourceNo) {
        return App.requester
            .request("fin/buy/finfirstbuyflgqry.biz", {
                data: { pdCd, txAcctNo, sourceNo }
            })
            .then(rsp => {
                return rsp.dfltFlg == "1";
            });
    }
    //查询理财详情
    static async queryDetail({ pdCd, chnlPdNo, sourceNo } = {}) {
        let loginUserNm = (await getloginUserNm()) || "";
        return App.requester
            .request("fin/findetail/findetailqry.biz", {
                data: {
                    pdCd: pdCd,
                    chnlPdNo: chnlPdNo,
                    loginUserNm: loginUserNm,
                    sourceNo: sourceNo
                }
            })
            .then(rsp => {
                return rsp;
            });
    }

    /**
     * 查询产品四书列表
     * @param {*} param0 产品数据，含sourceNo, chnlPdNo
     */
    static queryBookList({ sourceNo, chnlPdNo, inqFlg, type } = {}) {
        return App.requester
            .request("fin/findetail/finibkatchinflistqry.biz", {
                data: { sourceNo, chnlPdNo, inqFlg, type }
            })
            .then(rsp => {
                let bookList = [];
                let TransRules = {};
                let Instructions = {};
                for (let item of rsp.ibkAtchInfEntityList) {
                    if (["01", "02", "03", "04", "05", "07", "30"].includes(item.pdAtchTyp)) {
                        if (item.pdAtchTyp == "02") {
                            Instructions = item;
                        }
                        //'01','02','03','04','05'(四书合一)
                        bookList.push(item);
                    } else if (item.pdAtchTyp == "06") {
                        //'06'交易规则
                        TransRules = item;
                    }
                }
                return Object.assign(rsp, {
                    ibkAtchInfEntityList: bookList,
                    TransRules,
                    Instructions
                });
            });
    }

    /**
     * 产看协议
     * @param {*} param0 url协议路径、title标题
     */
    static previewAgreement({ url, title, hasConfirmed, useJsApi } = {}) {
        url = transferUrl(url);
        //html文件需要单独处理
        if (/(.htm|.html)$/.test(url)) {
            window.$G.goToNextRetain({
                router: `#${App.bank.flags?.finance?.module || "Finance"}/Buying/HtmAgreement`,
                param: {
                    url,
                    title
                }
            });
        } else {
            //是否使用jsapi（详情页）,无法录屏
            if (useJsApi) {
                return App.jsApi.previewPdf({
                    url,
                    title
                });
            } else {
                //使用$previewPdf,可以录屏
                return App.getViewInstance("content").$previewPdf({
                    url, // pdf的地址
                    title, // 导航栏标题
                    hasConfirmed
                });
            }
        }
    }

    /**
     * 理财查询下挂卡列表
     * @param {*} dealNoCard 无付款账户时，是否引导加挂，默认false
     */
    static finGetPayAcctList(dealNoCard = false) {
        return App.requester
            .request("fin/acctlist/acctlistqry.biz", {
                data: {
                    txCd: "FIN000101",
                    acctAuthFltFlg: "Y",
                    acctStsFltFlg: "N"
                }
            })
            .then(rsp => {
                rsp.LIST = rsp.acctListQryDtoEntitys;
                if (rsp.LIST.length == 0 && dealNoCard) {
                    App.getViewInstance("content").$confirm({
                        title: "温馨提示",
                        message: "该功能需要添加本行借记卡Ⅰ类或Ⅱ类账户后使用",
                        sureText: "立即添加",
                        closed: isSure => {
                            if (isSure) {
                                addCard();
                            }
                        }
                    });
                }
                return rsp;
            });
    }

    /**
     * 校验账户是否下挂,已加挂返回true，未加挂返回false
     * @param {*} acctNo 校验的卡号
     * @param {*} cancel 点击取消时的回调函数
     */
    static checkTakingDown(acctNo, cancel = null) {
        return FinanceUtil.finGetPayAcctList().then(rsp => {
            let acctIndex = rsp.LIST.findIndex(item => {
                return item.acctNo == acctNo;
            });

            if (acctIndex == -1) {
                //未加挂
                App.getViewInstance("content").$confirm({
                    message: "理财购买账户未添加，请添加账户后处理",
                    sureText: "去添加",
                    cancelText: "返回",
                    closed: isSure => {
                        if (isSure) {
                            //引导跳转快速加挂
                            fastTakingDown({ acctNo });
                        } else {
                            cancel && cancel();
                        }
                    }
                });
                return false;
            } else {
                return true;
            }
        });
    }

    /**
     * 查询是否开通理财转让服务,开通true,未开通false
     */
    static async checkOpenFTS() {
        //已缓存则取缓存的值
        var isOpenFTS = App.storage.get("isOpenFTS");
        if (isOpenFTS !== undefined) {
            return isOpenFTS;
        } else {
            let loginUserNm = (await getloginUserNm()) || "";
            //法人行参数,是否开通理财转让标识
            if (!App.bank.flags.finance.ifOpenFinanceTransfer) return false;
            //后端开关,理财转让功能是否可用
            return App.requester
                .request("wlh/common/switchquery.biz", {
                    data: {
                        txCd: "FIN000702",
                        loginUserNm
                    }
                })
                .then(rsp => {
                    let openFlg = rsp.svSwch == "Y";

                    //缓存接口返回的值，防止多次重复调用
                    App.storage.remove("isOpenFTS");
                    App.storage.set("isOpenFTS", openFlg);
                    return openFlg;
                });
        }
    }
    /**
     * 判断某理财产品模板编号是否可转让
     * @param {*} pdTplNo 产品模板编号
     */
    static checkFtsPdTplNo(pdTplNo) {
        //目前金证、恒生支持转让的模板一致，若不一致则可使用法人行参数判断
        return ["1301", "1303", "1306"].includes(pdTplNo);
    }

    /**
     * 是否根据份额明细转让，目前仅金证1306
     * @param {*} pdTplNo 产品模板编号
     */
    static checkTrnsVoldetail(pdTplNo) {
        return ["1306"].includes(pdTplNo);
    }

    /**
     * 依次校验 登录、加挂卡、理财签约
     * @param {String} message 未签约时引导提示语
     * @param {Function} cancel 跳转登录后但不登录时回调，未挂卡/未签约时弹窗-点击取消时回调
     */
    static async checkCustSgn({
        cancel = null,
        message = "您尚未进行理财签约，请先至柜面完成理财签约。",
        inqFlg = "0"
    } = {}) {
        // 校验登录
        if (!(await dealLogin())) {
            cancel && cancel();
            return false;
        }

        // 校验用户权限等级（是否加挂本行借记卡或存折）
        if (!(await checkCustomerAuthInfo({ cancel }))) return false;

        // 校验是否进行理财签约;
        let rsp = await App.requester.request("fin/finsign/custrskresultqry.biz", {
            data: {
                inqFlg
            }
        });
        if (rsp.custSgnFlg === "N" || rsp.custSgnFlg === "") {
            //引导签约时，弹窗
            App.getViewInstance("content").$confirm({
                title: "温馨提示",
                message,
                sureText: "网点预约",
                cancelText: "我知道了",
                closed(isSure) {
                    if (isSure) {
                        window.$G.goToNext("#BranchService/BranchInquery/Index", {
                            backRouter: location.hash
                        });
                    } else {
                        cancel && cancel();
                    }
                }
            });

            return false;
        }

        //校验通过
        return true;
    }
    /**
     * 理财跳转产品详情页面包括行方H5页面，跳转逻辑
     * @param {*}产品返回信息
     */
    static async goProductDetail(item) {
        if (item.jumpTyp == "7") {
            let appId = getUrlParams(item.pgNo, "appId");
            let isHiddenNavigationBar = getUrlParams(item.pgNo, "isHiddenNavigationBar");
            return await window.App.menu.go(
                { url: item.pgNo, jumpType: item.jumpTyp },
                { appId: appId, isHiddenNavigationBar: isHiddenNavigationBar }
            );
        } else if (item.jumpTyp == "6" || item.jumpTyp == "5") {
            return await window.App.menu.go({ url: item.pgNo, jumpType: item.jumpTyp, ...item }, "");
        } else if (item.jumpTyp == "4") {
            return await window.App.menu.go(
                { url: App.bank.flags.finance.finance5Name, jumpType: item.jumpTyp },
                item.pgNo
            );
        } else {
            return await window.App.menu.go(item.pgNo, item);
        }
    }
    /**
     * @method
     * @desc 理财回溯录屏开始
     * @param {Number} type 录屏类型 1:购买 2:风评
     * @param {Function} cb 开始录屏回调函数
     * @returns {}
     */
    static async startTrace(type, cb) {
        //App.bank.flags.finance.OpenfinanceTrace 判断是否开通视频回溯
        if (App.bank.flags.finance.OpenfinanceTrace) {
            let accAddress = `${getDomainName("financeTrace")}/ebank/uitrack/newtouch-trace-sdk`;
            let message = "";
            let sureText = "";
            let title = "温馨提示";
            if (type == 1) {
                message =
                    "您即将进入理财购买流程。购买时所有操作都将被记录，请仔细阅读产品相关协议、条款。退出理财交易界面即结束录制。理财非存款、产品有风险、投资须谨慎。";
                sureText = "开始购买";
            } else if (type == 2) {
                message =
                    "您即将进入理财风险测评流程。测评时所有操作都将被记录，请仔细阅读测评题目并作答。退出风评交易界面即结束录制。";
                sureText = "开始测评";
                if (App.bank.flags.finance?.riskAssessmentTips) {
                    title = "<p style='font-weight:bold;'>银行提示声明：</p>";
                    message =
                        "<p style='text-indent:2em; font-weight:bold;'>您即将进入理财风险测评流程。测评时所有操作都将被记录，请仔细阅读测评题目并作答。退出风评交易界面即结束录制。</p><p style='text-indent:2em;'>本评估问卷仅对年满18周岁客户开放，问卷包含10个问题，每个问题请选择唯一选项，不可多选。每次评估结果的有效期为一年，如您本人或家庭在未来一年内发生可能影响您风险承受能力的情况，您需及时告知银行并尽快重新评估。</p><p style='text-indent:2em;'>本评估问卷旨在根据您的财务状况、投资经验、投资风格、投资目的和风险承受能力等要素信息，帮助您了解自己的投资风险承受能力，进而帮助您选择合适的投资产品，并不代表银行对您投资结果的任何承诺和保证。</p><p style='text-indent:2em;'>本评估问卷的结果完全基于您提供的有效信息，因此您提供的所有信息应当真实、准确、完整。如因虚假、无效或不完整的信息，导致您的测评结果出现错误或偏差，所产生的不利后果完全由您承担。</p><p style='text-indent:2em; font-weight:bold;'>“理财非存款、产品有风险、投资须谨慎”！购买产品前，请认真阅读相关产品说明书、协议书、风险揭示书、权益须知等文件，充分了解投资可能面临的市场风险、汇率风险、信用风险以及可能的本金全部损失甚至超出本金的损失等各类风险，独立判断，自主决策。</p>";
                }
            } else if (type == 3) {
                message =
                    "您即将进入理财撤单流程。撤单时所有操作都将被记录，请仔细阅读产品相关协议、条款。退出撤单交易界面即结束录制。";
                sureText = "开始撤单";
            } else if (type == 4) {
                message = "您即将进入理财赎回流程。赎回时所有操作都将被记录。退出赎回交易界面即结束录制。";
                sureText = "开始赎回";
            }
            //弹窗
            ViewBase.prototype.$confirm({
                title: title,
                message: message,
                sureText: sureText,
                cancelText: "我再想想",
                type: "t4",
                closed(isSure) {
                    if (isSure) {
                        let $nrrweb = require("~/config/newtouch-trace-jssdk.js");
                        $nrrweb.setLoggerLevel(2);
                        $nrrweb
                            .init(accAddress, App.bank.flags.finance.finTraceSysNum, "SDBO123NBSD3S800sbB=")
                            .then(() => {
                                $nrrweb.startRecord();
                            });
                        FinanceUtil.traceDataUpload();
                        if (cb && typeof cb === "function") {
                            cb();
                        }
                    } else if (!isSure) {
                        if (!cb) {
                            App.requester.app.router.back();
                        }
                    }
                }
            });
        } else {
            if (cb && typeof cb === "function") {
                cb();
            }
        }
    }
    /**
     * @method
     * @desc 理财回溯录屏数据上送和结束录屏
     * @param {String} pdNm 产品名称 非必送
     * @param {String} pdCd 产品代码 非必送
     * @param {String} txsrlNo 交易流水号 非必送
     * @param {String} txAmt 交易金额 非必送
     * @param {String} type 交易类型 非必送
     * @param {Boolean} ifEnd 是否停止录制 非必送，默认不停止
     * @returns {}
     */
    static async traceDataUpload({ pdNm, pdCd, txsrlNo, txAmt, type } = {}, ifEnd = false) {
        //App.bank.flags.finance.OpenfinanceTrace 判断是否开通视频回溯
        if (App.bank.flags.finance.OpenfinanceTrace) {
            //获取用户信息，用于录屏上送
            return App.requester
                .request("usr/info/usrinfqry.biz", {
                    slient: true
                })
                .then(rsp => {
                    let userNm = rsp.userNm;
                    let certNo = rsp.certNo;
                    let certTyp = App.dict.get("f-certTyp", rsp.certTyp);
                    //替换
                    let $nrrweb = require("~/config/newtouch-trace-jssdk.js");
                    $nrrweb.addBusinessData({
                        organ_no: App.bank.flags.common.bankId, //行号
                        customer_name: userNm, //客户名
                        customer_cert_no: certNo, //客户证件号
                        customer_cert_type: certTyp, //客户证件类型
                        product_name: pdNm || "", //产品名称
                        product_no: pdCd || "", //产品代码
                        business_flow_no: txsrlNo || "", //交易流水号
                        business_price: txAmt || "", //交易金额
                        business_type: type || "" //交易类型
                    });
                    if (ifEnd) {
                        $nrrweb.stopRecord();
                        $nrrweb.addBusinessData({
                            organ_no: App.bank.flags.common.bankId, //行号
                            customer_name: userNm, //客户名
                            customer_cert_no: certNo, //客户证件号
                            customer_cert_type: certTyp, //客户证件类型
                            product_name: pdNm || "", //产品名称
                            product_no: pdCd || "", //产品代码
                            business_flow_no: txsrlNo || "", //交易流水号
                            business_price: txAmt || "", //交易金额
                            business_type: type || "" //交易类型
                        });
                    }
                });
        }
    }

    /**
     * @method
     * @desc 时间轴格式化日期
     * @param {String} date YYYYMMDD
     * @return {String} MM-DD || YYYY-MM-DD
     */

    static formatDate(date) {
        let yearNow = Number(dateUtils.nowDate("YYYY"));
        let yearDate = Number(dateUtils.format(date, "YYYYMMDD", "YYYY"));
        if (yearNow !== yearDate) {
            return dateUtils.format(date, "YYYYMMDD", "YYYY-MM-DD");
        }
        return dateUtils.format(date, "YYYYMMDD", "MM-DD");
    }

    /**
     * @method
     * @desc 获取到期提醒列表
     * @pgNo 接口上送参数
     * "" 财富频道页，
     * "1" 财私专区，
     * "2" 理财主页，
     * "3" 代销资管主页(私募专区)
     * @return {Array} 到期提醒列表
     */
    static getDueremindlist(pgNo = "1") {
        return App.requester
            .request("wlh/msg/dueremindlistqry.biz", {
                data: {
                    pgNo
                }
            })
            .then(rsp => {
                rsp.dueRemindEntityList = rsp.dueRemindEntityList.map(item => {
                    if (item.cdrEventNm.includes("到期")) {
                        item.text = `您的${item.pdNm}产品将于${fmtDate(item.endDt)}日到期。`;
                    } else if (item.cdrEventNm.includes("开放期")) {
                        item.text = `您的${item.pdNm}产品将于${fmtDate(item.endDt)}日进入开放期。`;
                    } else {
                        item.text = `${item.pdNm}`;
                    }
                    return item;
                });
                return rsp;
            });
    }
    /**
     * @method
     * @desc 格式化金额
     * @param val 需要格式化的金额
     * @return {String} 格式化后的金额 *万元
     */
    static formatMoneyBeyondTH(val) {
        const mType = typeof val;
        if (mType !== "string" && mType !== "number") return "";

        const mNum = Number(val);
        if (!mNum && mNum !== 0) return "";

        const mStr = `${val}`;
        const mStrWithSymbol = `${mStr}元`;
        if (mStr.length < 5) return mStrWithSymbol;

        const mPos = mStr.length - 4;
        const mStart = mStr.substring(0, mPos);
        const mEnd = mStr.substring(mPos);
        return mEnd === "0000" ? `${mStart}万元` : mStrWithSymbol;
    }

    /**
     * 判断产品状态是否可购买（73版本getBuyBtnFlg还校验txSts、selloutFlg）
     * @param {*} pdSts 产品状态
     */
    static checkBuyPdSts(pdSts) {
        /**
         * 0-开放期
         * 1-募集期
         * 2-发行成功
         * 3-发行失败
         * 4-停止交易
         * 5-停止申购
         * 6-停止赎回
         * 7-权益登记
         * 8-红利发放
         * 9-产品封闭
         * a-产品终止
         * b-预约认购期
         * 只有1 0 6能购买-haopp
         */
        return ["0", "1", "6"].includes(pdSts);
    }

    /**
     * 获取转让产品本周期剩余天数
     * @param {*} item 理财转让产品列表返回参数
     */
    static getTrnsResidualDays(item) {
        /**
         * custCrnRemDays 客户周期产品 - 剩余天数 非客户周期产品时为'0'
         * custThsCyclDays 客户周期产品 - 周期天数 非客户周期产品时为'0'
         * pdRemDays 其他产品 - 剩余天数
         */
        const { custCrnRemDays, custThsCyclDays, pdRemDays } = item;
        // 客户周期产品
        if (Number(custCrnRemDays) && Number(custThsCyclDays)) {
            return custCrnRemDays;
        }

        // 非客户周期产品
        return pdRemDays;
    }
}

export { FinanceUtil };
